import { McDbCustomEntity, McGePoint3d, IMcDbDwgFiler, McGePoint3dArray, MxCADWorldDraw, McDb, MxCpp, McDbPolyline, McDbLine, McDbText, MxCADUiPrPoint, MxCADUiPrDist, McDbEntity, McDbCircle, McGeVector3d } from "mxcad";
import { MxFun } from "mxdraw";
import { t } from '../../i18n';
import { alginType } from "../../SymbolAnnotation"

export enum indexStyle {
  //指向索引
  Directed,
  // 剖切索引
  Cutting
}

// 索引符号
export class McDbTestIndexSymbol extends McDbCustomEntity {
  // 定义McDbTestIndexSymbol内部的点对象 
  // 索引节点位置
  private _indexNodePos: McGePoint3d = new McGePoint3d();
  // 转折点位置
  private _turninPointPos: McGePoint3d = new McGePoint3d();
  // 文字索引号位置
  private _indexNumPos: McGePoint3d = new McGePoint3d();
  // 剖切线端点位置
  private lineStart: McGePoint3d = new McGePoint3d();
  private lineEnd: McGePoint3d = new McGePoint3d();
  // 文字位置
  private textPosArr: McGePoint3d[] = [];
  // 上下标文字
  private _textUp: string = "";
  private _textDown: string = "";
  // 索引号半径
  private _indexRadius: number = 0;
  // 索引图号
  private _indexDrawNumber: string = "";
  // 索引编号
  private _indexNumber: string = "";
  // 固定角度
  private _fixedAngle: number = 0;
  // 索引范围半径
  private _indexAreaRadius: number = 0;
  /**
   * 文字对齐方式
   */
  private _textAlignment: number = alginType.start;
  // 索引样式
  private _indexStyle: number = indexStyle.Directed;
  // 文字高度
  private height: number = 0;
  /** 包围盒最大点 包围盒最小点 */
  private minPt: McGePoint3d = new McGePoint3d();
  private maxPt: McGePoint3d = new McGePoint3d();
  // 构造函数
  constructor(imp?: any) {
    super(imp);
  }
  // 创建函数
  public create(imp: any) {
    return new McDbTestIndexSymbol(imp)
  }
  // 获取类名
  public getTypeName(): string {
    return "McDbTestIndexSymbol";
  }
  //设置或获取文本字高
  public set textHeight(val: number) {
    this.height = val;
  }
  public get textHeight(): number {
    return this.height;
  }
  //设置或获取索引节点位置
  public set indexNodePos(pt: McGePoint3d) {
    this._indexNodePos = pt.clone();
  }
  public get indexNodePos(): McGePoint3d {
    return this._indexNodePos;
  }
  //设置或获取转折点位置
  public set turninPointPos(pt: McGePoint3d) {
    if (this._fixedAngle && this._indexStyle === indexStyle.Directed) {
      const point = this.getFixedAnglePt(this._indexNodePos, pt);
      this._turninPointPos = point.clone();
    } else {
      this._turninPointPos = pt.clone();
    }
    if (this._indexStyle === indexStyle.Cutting) {
      this.getCutLinePos()
    }
  }
  public get turninPointPos(): McGePoint3d {
    return this._turninPointPos;
  }
  //设置或获取文字索引号位置
  public set indexNumPos(pt: McGePoint3d) {
    this._indexNumPos = new McGePoint3d(pt.x, this._turninPointPos.y);
  }
  public get indexNumPos(): McGePoint3d {
    return this._indexNumPos;
  }
  //设置或获取索引图标半径
  public set indexRadius(val: number) {
    this._indexRadius = val;
  }
  public get indexRadius(): number {
    return this._indexRadius;
  }
  //设置或获取文字对齐方式
  public set textAlignment(val: number) {
    this._textAlignment = val;
  }
  public get textAlignment(): number {
    return this._textAlignment;
  }
  //设置或获取上下标文字内容
  public set textUp(val: string) {
    this._textUp = val;
  }
  public set textDown(val: string) {
    this._textDown = val;
  }
  public get textUp(): string {
    return this._textUp;
  }
  public get textextDowntUp(): string {
    return this._textDown;
  }
  //设置或获取索引图号
  public set indexDrawNumber(val: string) {
    this._indexDrawNumber = val;
  }
  public get indexDrawNumber(): string {
    return this._indexDrawNumber;
  }
  // 设置或获取索引编号
  public set indexNumber(val: string) {
    this._indexNumber = val;
  }
  public get indexNumber(): string {
    return this._indexNumber;
  }
  // 设置或获取固定角度
  public set fixedAngle(val: number) {
    this._fixedAngle = val;
  }
  public get fixedAngle(): number {
    return this._fixedAngle;
  }
  // 设置或获取索引范围半径
  public set indexAreaRadius(val: number) {
    this._indexAreaRadius = val;
  }
  public get indexAreaRadius(): number {
    return this._indexAreaRadius;
  }
  // 设置或获取索引样式
  public set indexStyle(val: number) {
    this._indexStyle = val;
  }
  public get indexStyle(): number {
    return this._indexStyle;
  }
  // 读取自定义实体数据
  public dwgInFields(filter: IMcDbDwgFiler): boolean {
    this.textPosArr = filter.readPoints("textPosArr").val;
    this._indexNumPos = filter.readPoint("indexNumPos").val;
    this._turninPointPos = filter.readPoint("turninPointPos").val;
    this._indexNodePos = filter.readPoint("indexNodePos").val;
    this.lineEnd = filter.readPoint("lineEnd").val;
    this.lineStart = filter.readPoint("lineStart").val;
    this.height = filter.readDouble("height").val;
    this._indexRadius = filter.readDouble("indexRadius").val;
    this._fixedAngle = filter.readDouble("fixedAngle").val;
    this._indexAreaRadius = filter.readDouble("indexAreaRadius").val;
    this._textAlignment = filter.readLong("textAlignment").val;
    this._indexStyle = filter.readLong("indexStyle").val;
    this._textUp = filter.readString("textUp").val;
    this._textDown = filter.readString("textDown").val;
    this._indexDrawNumber = filter.readString("indexDrawNumber").val;
    this._indexNumber = filter.readString("indexNumber").val;
    this.minPt = filter.readPoint("minPt").val;
    this.maxPt = filter.readPoint("maxPt").val;
    return true;
  }
  // 写入自定义实体数据
  public dwgOutFields(filter: IMcDbDwgFiler): boolean {
    filter.writePoints("textPosArr", this.textPosArr);
    filter.writePoint("indexNumPos", this._indexNumPos);
    filter.writePoint("turninPointPos", this._turninPointPos);
    filter.writePoint("indexNodePos", this._indexNodePos);
    filter.writePoint("lineStart", this.lineStart);
    filter.writePoint("lineEnd", this.lineEnd);
    filter.writeLong("textAlignment", this._textAlignment);
    filter.writeLong("indexStyle", this._indexStyle);
    filter.writeString("textDown", this._textDown);
    filter.writeString("textUp", this._textUp);
    filter.writeDouble("height", this.height);
    filter.writeDouble("indexAreaRadius", this._indexAreaRadius);
    filter.writeDouble("fixedAngle", this._fixedAngle);
    filter.writeDouble("indexRadius", this._indexRadius);
    filter.writeString("indexDrawNumber", this._indexDrawNumber);
    filter.writeString("indexNumber", this._indexNumber);
    filter.writePoint("minPt", this.minPt);
    filter.writePoint("maxPt", this.maxPt);
    return true;
  }

  // 移动自定义对象的夹点
  public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
    this.assertWrite();
    if (iIndex === 0) {
      this._indexNodePos.x += dXOffset;
      this._indexNodePos.y += dYOffset;
      this._indexNodePos.z += dZOffset;
      this.getCutLinePos()
    } else if (iIndex === 1) {
      this._turninPointPos.x += dXOffset;
      this._turninPointPos.y += dYOffset;
      this._turninPointPos.z += dZOffset;
      this._indexNumPos.x += dXOffset;
      this._indexNumPos.y += dYOffset;
      this._indexNumPos.z += dZOffset;
      this.getCutLinePos()
    } else if (iIndex === 2) {
      this._indexNumPos.x += dXOffset;
    } else if (iIndex === 3) {
      const l = new McDbLine(this._indexNodePos, this._turninPointPos);
      const pt = l.getClosestPointTo(this.lineStart, false).val;
      const v = this.lineStart.sub(pt).mult(2);
      this.lineStart.subvec(v);
      this.lineEnd.subvec(v);
    } else if (iIndex === 4) {
      const point = this.lineEnd.clone();
      point.x += dXOffset;
      point.y += dYOffset;
      point.z += dZOffset;
      const l = new McDbLine(this.lineStart, this.lineEnd);
      const pt = l.getClosestPointTo(point, true).val;
      const v = this._turninPointPos.sub(this._indexNodePos).normalize();
      const vec = v.mult(this._indexRadius);
      const endingPt = this.lineStart.clone().addvec(vec);
      if ((this.lineEnd.x > this.lineStart.x && pt.x > endingPt.x) ||
        (this.lineEnd.x < this.lineStart.x && pt.x < endingPt.x)) {
        this.lineEnd = pt;
      } else {
        this.lineEnd = endingPt;
      }
    }
  };
  // 获取自定义对象的夹点
  public getGripPoints(): McGePoint3dArray {
    let ret = new McGePoint3dArray();
    ret.append(this._indexNodePos)
    ret.append(this._turninPointPos)
    ret.append(this._indexNumPos)
    if (this._indexStyle === indexStyle.Cutting) {
      ret.append(this.lineStart);
      ret.append(this.lineEnd);
    }
    return ret;
  };
  // 绘制实体 
  public worldDraw(draw: MxCADWorldDraw): void {
    const allEntityArr: McDbEntity[] = [];
    let firstPt = this._indexNodePos.clone();
    const v = this._turninPointPos.sub(this._indexNodePos).normalize()
    // 绘制索引范围
    if (this._indexAreaRadius && this.indexStyle === indexStyle.Directed) {
      const circle = new McDbCircle(this._indexNodePos.x, this._indexNodePos.y, this._indexNodePos.z, this.indexAreaRadius);
      draw.drawEntity(circle);
      allEntityArr.push(circle)
      const dist = this._indexNodePos.distanceTo(this._turninPointPos);
      if (dist > this._indexAreaRadius) {
        const vec = v.mult(this._indexAreaRadius);
        firstPt.addvec(vec);
      }
    }
    // 绘制引线
    const pl = new McDbPolyline();
    pl.addVertexAt(firstPt);
    pl.addVertexAt(this._turninPointPos);
    pl.addVertexAt(this._indexNumPos);
    draw.drawEntity(pl);
    allEntityArr.push(pl)
    // 绘制文字
    const textArr = this.drawText();
    if (textArr) {
      textArr.forEach((entity: McDbText) => {
        if ((this._textAlignment === alginType.start && this._turninPointPos.x > this._indexNumPos.x) ||
          this._textAlignment === alginType.end && this._turninPointPos.x < this._indexNumPos.x) {
          entity.horizontalMode = McDb.TextHorzMode.kTextRight
        }
        draw.drawEntity(entity)
        allEntityArr.push(entity)
      })
    }
    // 绘制图标
    this.drawIndexIcon().forEach(entity => {
      draw.drawEntity(entity)
      allEntityArr.push(entity)
    })
    // 绘制剖切线
    if (this._indexStyle === indexStyle.Cutting) {
      const _pl = new McDbPolyline();
      _pl.addVertexAt(this.lineEnd);
      _pl.addVertexAt(this.lineStart);
      _pl.constantWidth = this.height / 4;
      draw.drawEntity(_pl);
      allEntityArr.push(_pl)
    }

    this.getBox(allEntityArr);
  }
  private getBox(entityArr: McDbEntity[]) {
    const mxcad = MxCpp.getCurrentMxCAD();
    let _minPt, _maxPt = null;
    entityArr.forEach(entity => {
      const id = mxcad.drawEntity(entity);
      const ent = id.getMcDbEntity();
      if (!ent) return;
      const { minPt, maxPt, ret } = ent.getBoundingBox();
      if (!_minPt) _minPt = minPt.clone();
      if (!_maxPt) _maxPt = maxPt.clone();
      if (minPt.x < _minPt.x) _minPt.x = minPt.x;
      if (minPt.y < _minPt.y) _minPt.y = minPt.y;
      if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
      if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
      id.erase();
    });
    if (_minPt && _maxPt) {
      this.maxPt = _maxPt;
      this.minPt = _minPt
    }
  }
  // 绘制索引图标
  private drawIndexIcon(): McDbEntity[] {
    let param = 1;
    if (this._turninPointPos.x > this._indexNumPos.x) param = -1;
    const center = new McGePoint3d(this._indexNumPos.x + this._indexRadius * param, this._indexNumPos.y);
    const circle = new McDbCircle();
    circle.radius = this._indexRadius;
    circle.center = center;
    const line = new McDbLine(this._indexNumPos, new McGePoint3d(this._indexNumPos.x + 2 * this._indexRadius * param, this._indexNumPos.y));

    const text1 = new McDbText();
    text1.height = this.height * (5 / 4);
    text1.textString = this._indexNumber.toString();
    text1.horizontalMode = McDb.TextHorzMode.kTextCenter;
    text1.position = text1.alignmentPoint = new McGePoint3d(center.x, center.y + this.height / 4);
    const id1 = MxCpp.getCurrentMxCAD().drawEntity(text1);
    const { minPt, maxPt } = id1.getMcDbEntity().getBoundingBox();
    id1.erase();
    text1.widthFactor = this._indexRadius * (2 / 3) / (maxPt.x - minPt.x);

    const text2 = new McDbText();
    text2.height = this.height * (5 / 4);
    text2.textString = this._indexDrawNumber;
    text2.horizontalMode = McDb.TextHorzMode.kTextCenter;
    text2.position = text2.alignmentPoint = new McGePoint3d(center.x, center.y - this.height * (5 / 4));
    const id = MxCpp.getCurrentMxCAD().drawEntity(text2);
    const { minPt: _minPt, maxPt: _maxPt } = id.getMcDbEntity().getBoundingBox();
    id.erase();
    text2.widthFactor = this._indexRadius * (2 / 3) / (_maxPt.x - _minPt.x);
    return [circle, line, text1, text2]
  }
  // 绘制文字
  private drawText(): McDbEntity[] | null {
    this.setTextPos();
    if (this.textPosArr.length > 0) {
      const text = new McDbText();
      text.height = this.height;
      text.horizontalMode = McDb.TextHorzMode.kTextLeft
      const textUp = text.clone() as McDbText;
      textUp.textString = this._textUp;
      textUp.position = textUp.alignmentPoint = this.textPosArr[0];
      const textDown = text.clone() as McDbText;
      textDown.textString = this._textDown;
      textDown.position = textDown.alignmentPoint = this.textPosArr[1];
      return [textUp, textDown];
    } else {
      return null
    }
  }

  // 设置文字位置
  private setTextPos() {
    if (!this.height) this.height = MxFun.viewCoordLong2Cad(15);
    if (!this._indexRadius) this._indexRadius = this.height * 2;
    let param = 1;
    if (this._turninPointPos.x > this._indexNumPos.x) param = -1
    if (this._textAlignment === alginType.start) {
      const textUpPt = new McGePoint3d(this._turninPointPos.x + (this.height / 2) * param, this._turninPointPos.y + this.height / 2);
      const textDownPt = new McGePoint3d(this._turninPointPos.x + (this.height / 2) * param, this._turninPointPos.y - this.height * (3 / 2));
      this.textPosArr = [textUpPt, textDownPt];
    } else if (this._textAlignment === alginType.middle) {
      const x = this._turninPointPos.x - (this._turninPointPos.x - this._indexNumPos.x) / 2;
      const textUpPt = new McGePoint3d(x, this._turninPointPos.y + this.height / 2);
      const textDownPt = new McGePoint3d(x, this._turninPointPos.y - this.height * (3 / 2));
      this.textPosArr = [textUpPt, textDownPt];
    } else if (this._textAlignment === alginType.end) {
      const textUpPt = new McGePoint3d(this._indexNumPos.x, this._indexNumPos.y + this.height / 2);
      const textDownPt = new McGePoint3d(this._indexNumPos.x, this._indexNumPos.y - this.height * (3 / 2));
      this.textPosArr = [textUpPt, textDownPt];
    }
  };
  // 计算固定角度后的取点
  private getFixedAnglePt(fixedPt: McGePoint3d, pt: McGePoint3d): McGePoint3d {
    const vec = pt.sub(fixedPt);
    const vecAngle = vec.angleTo2(McGeVector3d.kXAxis, McGeVector3d.kNegateZAxis);
    const num = 360 / this._fixedAngle;
    let angleSub = null;
    let angle = 0;
    for (let i = 0; i < num; i++) {
      const a = Math.abs(this._fixedAngle * i - vecAngle * 180 / Math.PI);
      if (!angleSub) {
        angleSub = a;
        angle = this._fixedAngle * i;
      } else if (angleSub > a) {
        angleSub = a;
        angle = this._fixedAngle * i;
      }
    }
    const _pt = fixedPt.clone().addvec(McGeVector3d.kXAxis.normalize().mult(100).rotateBy(angle * Math.PI / 180))
    const _line = new McDbLine(fixedPt, _pt);
    const newPoint = _line.getClosestPointTo(pt, true).val;
    return newPoint;
  }
  // 计算剖切线位置
  private getCutLinePos(): void {
    const v = this._turninPointPos.sub(this._indexNodePos).normalize();
    let length = this.lineEnd.distanceTo(this.lineStart);
    if (!length) {
      length = this._indexRadius ? this._indexRadius : MxFun.viewCoordLong2Cad(15) * 2
    }
    const vec = v.mult(length);
    const point = this._indexNodePos.clone().addvec(vec);
    let dist = this.height ? this.height / 2 : MxFun.viewCoordLong2Cad(15) / 2;
    const movePt = this._indexNodePos.clone().subvec(v.perpVector().mult(dist));
    const line = new McDbLine(this._indexNodePos, point);
    line.move(this._indexNodePos, movePt);
    this.lineStart = line.startPoint;
    this.lineEnd = line.endPoint;
  }

  // 获取包围盒
  public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
    return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
  }
};

export async function drawIndexSymbol(styleType: number, textContent: string[], textAlignment: number, indexDrawNumber: string, indexNumber: string, angle: number, addIndexArea: boolean) {
  const pLabeling = new McDbTestIndexSymbol();
  pLabeling.textUp = textContent[0];
  pLabeling.textDown = textContent[1];
  pLabeling.textAlignment = textAlignment;
  pLabeling.indexDrawNumber = indexDrawNumber;
  pLabeling.indexNumber = indexNumber;
  pLabeling.fixedAngle = angle;
  pLabeling.indexStyle = styleType;

  const getPoint1 = new MxCADUiPrPoint();
  getPoint1.setMessage(`\n${t('请指定索引节点的位置')}:`);
  const point1 = await getPoint1.go();
  if (!point1) return;
  pLabeling.indexNodePos = point1;

  if (addIndexArea && styleType === indexStyle.Directed) {
    const getRadius = new MxCADUiPrDist();
    getRadius.setMessage('请指定节点范围');
    getRadius.setBasePt(point1);
    getRadius.setUserDraw((pt, pw) => {
      const radius = pt.distanceTo(point1);
      const circle = new McDbCircle(point1.x, point1.y, point1.z, radius);
      pw.drawMcDbEntity(circle);
    });
    const radius = await getRadius.go();
    if (!radius) return;
    pLabeling.indexAreaRadius = radius;
  }

  const getPoint2 = new MxCADUiPrPoint();
  getPoint2.setMessage(`\n${t('请指定转折点的位置')}:`);
  getPoint2.setUserDraw((pt, pw) => {
    const _clone = pLabeling.clone() as McDbTestIndexSymbol;
    _clone.turninPointPos = pt;
    _clone.indexNumPos = new McGePoint3d(pt.x + MxFun.viewCoordLong2Cad(30), pt.y);
    pw.drawMcDbEntity(_clone)
  })
  const point2 = await getPoint2.go();
  if (!point2) return;
  pLabeling.turninPointPos = point2;
  const getPoint3 = new MxCADUiPrPoint();
  getPoint3.setMessage(`\n${t('请指定文字索引号位置')}:`);
  getPoint3.setUserDraw((pt, pw) => {
    const _clone = pLabeling.clone() as McDbTestIndexSymbol;
    _clone.indexNumPos = pt;
    pw.drawMcDbEntity(_clone);
  });
  const point3 = await getPoint3.go();
  if (!point3) return;
  pLabeling.indexNumPos = point3;
  const mxcad = MxCpp.getCurrentMxCAD();
  mxcad.drawEntity(pLabeling);
}