setTimeout(async () => {
    MxPluginContext.getUiConfig().then((uiConfig) => {
        const data = JSON.parse(JSON.stringify(originalData));
        data.tab = t(data.tab[0]);
        data.list.forEach((item) => {
            item.tab = t(item.tab);
        });
        const dataClone = JSON.parse(JSON.stringify(data));
        i18nScope.on("change", () => {
            dataClone.tab = t(originalData.tab[0]);
            dataClone.list.forEach((item, index) => {
                item.tab = t(originalData.list[index].tab);
            });
            data.tab = dataClone.tab
            data.list.forEach((item, index) => {
                item.tab = dataClone.list[index].tab
            });
        })
        const _data = {
            isResponsive: true,
            "tab": t("等分"),
            "cmd": "Mx_Extool_Bisection",
            "icon": "dengfen"
        } as typeof uiConfig.mMenuBarData[number]
        i18nScope.on("change", () => {
            _data.tab = t("等分")
        })
        uiConfig.mMenuBarData.forEach(item => {
            if (item.tab === "工具(A)") {
                item.list.push(data as any, _data as any)
            }
        })
    })
}, 500);

