import { McDbTestArrowCitation } from './ArrowCitation/hooks'
import { McDbTestExportAnnotation } from './ExtractionAnnotation/hooks'
import { McDbTestPracticeLabeling } from './PracticeLabeling/hooks'
import { McDbTestIndexSymbol } from './IndexSymbol/hooks'
import { McDbTestIndexImgName } from './IndexImgName/hooks'
import { McDbTestSliceSymbol } from './SliceSymbol/hooks'
import { McDbTestElevationMark} from './ElevationMarking/hooks'
import { McDbTestMapTitleMark} from './MapTitleMarking/hooks'
import { McDbTestFractureLine, addFractureLine } from './FractureLine/hooks'
import { McDbTestAxisOfSymmetry, drawAxisOfSymmetry } from './AxisOfSymmetry/hooks'
import { McDbTestCompass, drawCompass } from './compass'

import "../HomeDecoration/index"

export enum arrowType {
  // 箭头
  arrow,
  // 半箭头
  halfArrow,
  // 点
  point,
  // 十字
  cross,
  // 无
  none
}
export enum alginType {
  // 始端
  start,
  // 中间
  middle,
  // 末端
  end
}

// 注册箭头引注类
setTimeout(()=>{
  new McDbTestArrowCitation().rxInit();
},0)
// 注册引出标注
new McDbTestExportAnnotation().rxInit();
// 注册做法标注
new McDbTestPracticeLabeling().rxInit();
// 注册索引符号标注
new McDbTestIndexSymbol().rxInit();
// 注册折断线类
new McDbTestFractureLine().rxInit();
// 剖切符号类
new McDbTestSliceSymbol().rxInit();
// 标高标注类
new McDbTestElevationMark().rxInit();
// 注册索引图名类
new McDbTestIndexImgName().rxInit();
// 注册图名标注
new McDbTestMapTitleMark().rxInit();
MxPluginContext.addCommand("JZDX", ()=>addFractureLine());
// 注册对称轴类
new McDbTestAxisOfSymmetry().rxInit();
MxPluginContext.addCommand("HDCZ", ()=>drawAxisOfSymmetry());
// 注册指北针类
new McDbTestCompass().rxInit();
MxPluginContext.addCommand("HZBZ", ()=>drawCompass());