<template>
  <div>
    <v-container>
      <!-- 顶部栏 -->
      <v-row align="center" class="mb-2" justify="space-between">
        <v-col cols="auto">
          <v-btn color="primary" @click="addCategory"
            >⊕{{ t("新建分类") }}</v-btn
          >
          <span style="margin-left: 10px"
            >{{ t("当前分类") }}：{{ currentCategoryName }}</span
          >
        </v-col>
        <v-col cols="auto">
          <v-btn @click="showAll" class="mr-2">{{ t("全部显示") }}</v-btn>
          <v-btn @click="hideAll">{{ t("全部隐藏") }}</v-btn>
        </v-col>
      </v-row>
      <!-- 搜索栏 -->
      <v-row align="center" class="mb-2" justify="space-between">
        <v-col cols="auto">
          <span class="text-caption">{{ t("查找分类名称") }}：</span>
        </v-col>
        <v-col cols="auto">
          <v-text-field
            style="width: 345px"
            v-model="search"
            :placeholder="t('请输入要查找的分类名称')"
          />
        </v-col>
      </v-row>

      <!-- 分类表格 -->
      <div class="table-container">
        <table style="width: 100%">
          <thead>
            <tr>
              <th
                v-for="header in headers"
                :key="header.text"
                :style="{ width: header.width }"
              >
                {{ header.text }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="cat in filteredCategories"
              :key="cat.id"
              :class="{ 'current-row': cat.id === currentCategoryId }"
            >
              <!-- 名称：双击可编辑 -->
              <td>
                <v-text-field
                  v-model="cat.name"
                  @blur="editingId = null"
                  @keyup.enter="editingId = null"
                />
              </td>
              <!-- 颜色：双击弹色板 -->
              <td>
                <v-select :items="colorList" v-model="cat.color">
                  <template #selection>
                    <span
                      :style="{ backgroundColor: cat.color }"
                      class="color-box"
                    ></span>
                  </template>
                  <template v-slot:item="{ item: items, props }">
                    <v-list-item
                      @click="
                        () => {
                          items.color = items;
                          props.onClick();
                          checkIsShowTops(cat);
                        }
                      "
                    >
                      <span
                        :style="{ backgroundColor: items.value }"
                        class="color-box"
                      ></span>
                    </v-list-item>
                  </template>
                </v-select>
              </td>
              <!-- 显示：点击切换 -->
              <td
                class="text-orange"
                v-ripple.prevent
                @click="changeVisible(cat)"
              >
                <v-icon :icon="cat.visible ? 'yanjing1' : 'yanjing'"></v-icon>
              </td>
              <!-- 当前：点击设置当前 -->
              <td @click="setCurrent(cat)">
                <v-icon
                  v-if="cat.current"
                  icon="class:iconfont gou"
                  size="x-small"
                  color="#16FD21"
                ></v-icon>
              </td>
              <!-- 操作：删除 -->
              <td>
                <span @click="removeCategory(cat)" class="delete-btn">{{
                  t("删除")
                }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </v-container>
    <!-- 底部提示 -->
    <div class="mt-2 text-caption grey--text">
      {{ t("点击表格项可以修改分类的名称和颜色，也可以设置当前分类") }}
    </div>
    <v-dialog v-model="alertDialog" max-width="200">
      <v-card :text="alertText" title="提示">
        <template v-slot:actions>
          <v-spacer></v-spacer>
          <v-btn @click="alertDialog = false"> {{ t("确定") }} </v-btn>
        </template>
      </v-card>
    </v-dialog>
    <v-dialog v-model="tipDialog" max-width="200">
      <v-card
        :text="t('是否需要将该分类下所有标注的颜色一起修改成分类的新颜色？')"
        :title="t('提示')"
      >
        <template v-slot:actions>
          <v-spacer></v-spacer>
          <v-btn @click="changeAllEntityColor(cat)">
            {{ t("一起修改") }}
          </v-btn>
          <v-btn @click="tipDialog = false"> {{ t("不需要") }} </v-btn>
        </template>
      </v-card>
    </v-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, reactive, onBeforeUnmount } from "vue";
import {
  measurementCategory,
  MxMeasureEntity,
  hexToRgb,
  currentAnnotationColor,
  getMeasurementCategory,
  colorList
} from "../../../MeasurementAnnotation/hooks";
import {
  VDataTable,
  VBtn,
  VIcon,
  VTextField,
  VCol,
  VRow,
  VContainer,
  VMain,
  VApp,
  VSelect,
  VColorPicker,
  VCheckbox,
  VDialog,
  VCard,
  VList,
  VListItem,
  VListItemTitle,
  VListItemSubtitle,
  VListItemAction,
  VAlert,
} from "vuetify/components";
import { dialogWidth } from "../dialog";
import {
  McCmColor,
  McDbDictionary,
  McDbXrecord,
  MxCADResbuf,
  MxCADSelectionSet,
  MxCpp,
} from "mxcad";
import { t } from "../../../i18n";

interface Category {
  id: number;
  name: string;
  color: string;
  visible: boolean;
  current: boolean;
}
onMounted(() => {
  dialogWidth.value = 500;
  getMxMeasureEntities();
});
const MxMeasureEntities: MxMeasureEntity[] = [];
// 获取测量标注
async function getMxMeasureEntities() {
  const ss = new MxCADSelectionSet();
  const filter = new MxCADResbuf();
  filter.AddString("CUSTOMENTITY", 5020);
  await ss.allSelect(filter);
  if (ss.count() === 0) return;
  ss.forEach((id) => {
    const entity = id.getMcDbEntity();
    if (entity instanceof MxMeasureEntity) {
      MxMeasureEntities.push(entity);
    }
  });
}
const search = ref("");
const tipDialog = ref(false);
const alertText = ref("");
const alertDialog = ref(false);

const currentCategoryId = ref<number | null>(null);

const headers = [
  { text: t("名称"), width: "40%" },
  { text: t("颜色"), width: "20%" },
  { text: t("显示"), width: "15%" },
  { text: t("当前"), width: "15%" },
  { text: "", width: "10%" },
];
const filteredCategories = computed(() =>
  measurementCategory.value.filter((cat) => cat.name.includes(search.value))
);

const currentCategoryName = computed(() => {
  const cat = measurementCategory.value.find((c) => c.current);
  return cat ? cat.name : "未分类";
});

function setCurrent(item: Category) {
  measurementCategory.value.forEach((c) => (c.current = false));
  item.current = true;
  item.visible = true;
  currentAnnotationColor.value = item.color;
}

function addCategory() {
  const id = Date.now();
  measurementCategory.value.push({
    id,
    name: `分类${measurementCategory.value.length + 1}`,
    color: "#D35400",
    visible: true,
    current: false,
  });
}

function removeCategory(item: Category) {
  if (item.current) {
    alertText.value = t(
      "这个分类是当前分类，不能被删除，您可以将其它分类设置为当前分类后，再重新操作!"
    );
    alertDialog.value = true;
    return;
  }
  const arr = MxMeasureEntities.filter(
    (entity) => entity.categoryName === item.name
  );
  if (arr.length > 0) {
    alertText.value = t(
      "该分类下有标注，不能被删除，您可以将标注移动到其它分类后，再重新操作!"
    );
    alertDialog.value = true;
    return;
  }
  measurementCategory.value = measurementCategory.value.filter(
    (c) => c.id !== item.id
  );
}

function toggleVisible(item: Category) {
  item.visible = !item.visible;
}

function showAll() {
  measurementCategory.value.forEach((c) => (c.visible = true));
  MxMeasureEntities.forEach((entity) => {
    entity.visible = true;
  });
  MxCpp.getCurrentMxCAD().updateDisplay();
}

function hideAll() {
  measurementCategory.value.forEach((c) => {
    if (!c.current) c.visible = false;
  });
  MxMeasureEntities.forEach((entity) => {
    if (entity.categoryName != currentCategoryName.value)
      entity.visible = false;
  });
  MxCpp.getCurrentMxCAD().updateDisplay();
  alertText.value = `${currentCategoryName.value}${t(
    "是当前分类，无法设置隐藏，其它分类已隐藏"
  )}`;
  alertDialog.value = true;
}

const currentCategory = ref<Category | null>(null);
// 判断是否需要显示修改颜色的tips
async function checkIsShowTops(cat: Category) {
  if (MxMeasureEntities.length === 0) return;
  const arr = MxMeasureEntities.filter(
    (entity) => entity.categoryName === cat.name
  );
  if (arr.length > 0) {
    tipDialog.value = true;
    currentCategory.value = cat;
  }
}
// 一起修改颜色
function changeAllEntityColor() {
  MxMeasureEntities.forEach((entity) => {
    if (entity.categoryName === currentCategory.value?.name) {
      entity.trueColor = new McCmColor(
        ...hexToRgb(currentCategory.value?.color)
      );
    }
  });
  MxCpp.getCurrentMxCAD().updateDisplay();
  tipDialog.value = false;
}
// 切换显示
function changeVisible(cat: Category) {
  if (cat.current) {
    alertText.value = t(
      "这个分类是当前分类，不能被隐藏，您可以将其它分类设置为当前分类后，再重新操作!"
    );
    alertDialog.value = true;
    return;
  }
  cat.visible = !cat.visible;
  MxMeasureEntities.forEach((entity) => {
    if (entity.categoryName === cat.name) {
      entity.visible = cat.visible;
    }
  });
  MxCpp.getCurrentMxCAD().updateDisplay();
}
// 在关闭弹框前，将分类信息存入全局字典中
onBeforeUnmount(() => {
  // 得到当前cad对象
  let mxcad = MxCpp.getCurrentMxCAD();
  // 得到全局字典
  let dict = mxcad.getDatabase().getNamedObjectsDictionary();
  // 得到用户自定义字典
  let sName = "MyCategoryList";
  let idDict = dict.getAt(sName);
  if (idDict.isNull()) {
    // 如果没有，就添加一个字典。
    let newDict = new McDbDictionary();
    idDict = dict.addObject(sName, newDict);
  }
  let myDict = idDict.getMcDbDictionary();
  if (myDict) {
    if (myDict.getAt("MyListRecord")) myDict.remove("MyListRecord");
    // 创建扩展记录
    let xrec = new McDbXrecord();
    // 创建数据链表
    let data = new MxCADResbuf();
    // 设置值
    const jsonData = JSON.stringify(measurementCategory.value);
    data.AddString(jsonData);
    // 设置扩展记录中的数据
    xrec.setData(data);
    // 字典写入扩展数据
    myDict.addObject("MyListRecord", xrec);
  }
});
</script>

<style scoped lang="scss">
th,
td {
  text-align: center;
  vertical-align: middle;
  line-height: 30px;
}
td {
  padding: 0px 5px;
}
.color-box {
  width: 80px;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 2px;
  display: inline-block;
  vertical-align: middle;
}
/* 设置文字下划线，hover高亮 */
.delete-btn {
  text-decoration: underline;
  cursor: pointer;
  &:hover {
    color: #89cbfa;
  }
}

.table-container {
  height: 300px;
  overflow-y: auto;
}
thead th {
  position: sticky;
  background-color: rgb(var(--v-theme-dialog-card));
  top: 0;
  z-index: 1;
}

tbody tr {
  &:hover {
    background-color: rgb(204, 204, 204, 0.3);
  }
}
</style>