<template>
    <div class="content">
        <div style="display:flex;align-items: center;margin-bottom: 10px;">
            <span>{{ t('标注样式：') }}</span>
            <div class="radio" v-for="item in styleArr" :key="item.id">
                <input type="radio" :id="item.name" v-model="dataForm.markStyle" :value="item.id" />
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div class="inputBox">
            <span>{{ `${t('图名文字')}：` }}</span>
            <input type="text" v-model="dataForm.textName" @focus="() => { setCommandFocus(false) }"
                @blur="() => { setCommandFocus(true); }" />
        </div>
        <div style="display:flex;align-items: center;margin-top: 10px;" class="custom-checkbox">
            <input type="checkbox" id="scale" value="scale" v-model="dataForm.scale" style="margin-bottom: 10px;">
            <div class="inputBox">
                <span>{{ t('显示比例：') }}</span>
                <input type="text" v-model="dataForm.scaleValue" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" style="width: 80px;"/>
            </div>
            <div style="display: flex;align-items: center;margin-left: 10px;margin-bottom: 10px;">
                <input type="checkbox" id="continuous" value="continuous" v-model="dataForm.isContinuous" >
                <span>{{ t('连续标注') }}</span>
            </div>
        </div>
        <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
            <button class="btn_normal" @click="sureDraw" style="margin: 5px 0px 5px;color: #fff!important;">{{
                t('确定') }}</button>
        </div>
    </div>
</template>

<script setup lang="ts">
import { reactive } from 'vue';
import { t } from '../../i18n';
import { drawTitleMark, markStyle } from './hooks'
import { isOpen } from "../../index";

const dataForm: any = reactive({
    markStyle: markStyle.NationalMarking,
    textName: "图名标注",
    scale: false,
    isContinuous: false,
    scaleValue: '1:100'
});

const { store } = MxPluginContext;
const { setCommandFocus } = store.useFocus();

const styleArr = reactive([
    { name: t('国标标注'), id: markStyle.NationalMarking },
    { name: t('传统标注'), id: markStyle.TraditionalMarking }
]);
// 确定绘制箭头引注
const sureDraw = async () => {
    isOpen.value = false;
    drawTitleMark(dataForm.textName, dataForm.scale, dataForm.scaleValue, dataForm.markStyle)
}
</script>

<style lang="scss" scoped>
@import '../../assets/dialogContens.scss';
</style>