import { McDbCustomEntity, McGePoint3d, IMcDbDwgFiler, McGePoint3dArray, MxCADWorldDraw, McDb, MxCpp, McDbLine, McGeVector3d, MxCADUiPrPoint, McDbPolyline, MxCADSelectionSet, MxCADUiPrDist, McCmColor, McObjectId, MxCADResbuf, MxCADUiPrKeyWord, MxCADUtility, DxfCode, McDbPoint, McDbHatch, McGeMatrix3d, McDbDatabase, McDbEntity } from "mxcad";
import { t } from "../../i18n";
import { McDbTestSingleDoor } from "../DoorsAndwindows/singleDoor";
import { McDbTestStandardWindow } from "../DoorsAndwindows/standardWindow";


//  计算多段线相关数据
const getPolyline = (startPoint: McGePoint3d, endPoint: McGePoint3d, width: number, turnPoints?: McGePoint3d[]): any => {
    const _startPoint = startPoint.clone();
    const _endPoint = endPoint.clone();
    if (startPoint.x > endPoint.x) {
        startPoint = _endPoint;
        endPoint = _startPoint;
    }
    const pl = new McDbPolyline();
    const v = endPoint.sub(startPoint).normalize().perpVector();
    let pt1 = startPoint.clone().addvec(v.clone().mult(width / 2));
    let pt2 = startPoint.clone().addvec(v.clone().negate().mult(width / 2));
    let pt3 = endPoint.clone().addvec(v.clone().negate().mult(width / 2));
    let pt4 = endPoint.clone().addvec(v.clone().mult(width / 2));
    const pointArr = {
        pt1Arr: [],
        pt2Arr: [],
        pt3Arr: [],
        pt4Arr: []
    }
    if (turnPoints && turnPoints.length > 0) {
        turnPoints.forEach(pt => {
            const dist1 = pt.distanceTo(startPoint);
            const dist2 = pt.distanceTo(endPoint);
            if (dist1 < dist2) {
                // 开始端
                const _dist1 = pt.distanceTo(pt1);
                const _dist2 = pt.distanceTo(pt2);
                if (_dist1 < _dist2) {
                    // pt1 = pt;
                    pointArr.pt1Arr.push(pt)
                } else {
                    // pt2 = pt;
                    pointArr.pt2Arr.push(pt)
                }
            } else {
                // 结束端
                const _dist1 = pt.distanceTo(pt3);
                const _dist2 = pt.distanceTo(pt4);
                if (_dist1 < _dist2) {
                    // pt3 = pt;
                    pointArr.pt3Arr.push(pt)
                } else {
                    // pt4 = pt;
                    pointArr.pt4Arr.push(pt);
                }
            };
        });
        for (let i = 0; i < 4; i++) {
            if (pointArr[`pt${i + 1}Arr`].length !== 0) {
                let dist = null;
                let point = new McGePoint3d();
                switch (i + 1) {
                    case 1:
                        point = pt4;
                        break;
                    case 2:
                        point = pt3;
                        break;
                    case 3:
                        point = pt2;
                        break;
                    case 4:
                        point = pt1;
                        break;
                }
                pointArr[`pt${i + 1}Arr`].forEach(pt => {
                    const _dist = pt.distanceTo(point);
                    if (!dist || _dist < dist) {
                        dist = _dist;
                        switch (i + 1) {
                            case 1:
                                pt1 = pt;
                                break;
                            case 2:
                                pt2 = pt;
                                break;
                            case 3:
                                pt3 = pt;
                                break;
                            case 4:
                                pt4 = pt;
                                break;
                        }
                    }
                })
            }
        }
    } else {
        pl.addVertexAt(pt1);
        pl.addVertexAt(pt2);
        pl.addVertexAt(pt3);
        pl.addVertexAt(pt4);
    }
    pl.isClosed = true;
    const line = new McDbLine(startPoint, endPoint);
    return { pl, line, pt1, pt2, pt3, pt4 };
}

// 墙体类
export class McDbTestWall extends McDbCustomEntity {
    // 定义McDbTestWall内部的点对象 
    // 墙体开始点
    private startPoint: McGePoint3d = new McGePoint3d();
    // 墙体夹点移动前结束点位置
    private _oldEndPoint: McGePoint3d = new McGePoint3d();
    // 墙体夹点移动前开始点位置
    private _oldStartPoint: McGePoint3d = new McGePoint3d();
    // 墙体结束点
    private endPoint: McGePoint3d = new McGePoint3d();
    // 墙体宽
    private _wallWidth: number = 30;
    // 墙体断点
    private _breakPoints: McGePoint3d[] = [];
    // 墙体拐点
    private _turnPoints: McGePoint3d[] = [];
    // 墙体相交点
    private _insterPoints: McGePoint3d[] = [];
    // 每一个断点组的长度记录
    private breakPtsCount: number[] = [];
    // 门窗句柄集合
    private _doorHandles: string[] = [];
    // 墙体中心点
    private centerPoint: McGePoint3d = new McGePoint3d();
    // 墙体夹点移动前中心点
    private oldCenterPoint: McGePoint3d = new McGePoint3d();
    // 门窗断点
    private doorBreakPoints: McGePoint3d[] = [];
    // 门窗断点组长度记录
    private doorBreakPtsCount: number[] = [];
    // 门窗相交点
    private doorInsterPoints: McGePoint3d[] = [];
    // 精度
    private dol: number = 0.00001;
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();
    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new McDbTestWall(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "McDbTestWall";
    }
    //设置或获取墙体宽
    public set wallWidth(val: number) {
        this._wallWidth = val;
    }
    public get wallWidth(): number {
        return this._wallWidth;
    }
    //设置或获取墙体断点
    public set breakPoints(val: McGePoint3d[]) {
        this._breakPoints = val;
    }
    public get breakPoints(): McGePoint3d[] {
        return this._breakPoints;
    }
    //设置或获取墙体拐点
    public set turnPoints(val: McGePoint3d[]) {
        this._turnPoints = val;
    }
    public get turnPoints(): McGePoint3d[] {
        return this._turnPoints;
    }
    //设置或获取墙体相交点
    public set insterPoints(val: McGePoint3d[]) {
        this._insterPoints = val;
    }
    public get insterPoints(): McGePoint3d[] {
        return this._insterPoints;
    }
    //设置或获取墙体单次相交断点数
    public set breakPtsCounts(val: number[]) {
        this.breakPtsCount = val;
    }
    public get breakPtsCounts(): number[] {
        return this.breakPtsCount;
    }
    // 获取墙体移动前开始点
    public get oldStartPoint(): McGePoint3d {
        return this._oldStartPoint;
    }
    // 获取墙体移动前结束点
    public get oldEndPoint(): McGePoint3d {
        return this._oldEndPoint;
    }
    // 获取墙体门窗句柄集合
    public get doorHandles(): string[] {
        return this._doorHandles;
    }
    // 设置墙体门窗句柄集合
    public set doorHandles(val: string[]) {
        this._doorHandles = val;
        this.countDoorData();
    }
    // 获取墙体旧中心
    public get wallOldCenterPoint(): McGePoint3d {
        return this.oldCenterPoint;
    }
    // 获取墙体中心
    public get wallCenterPoint(): McGePoint3d {
        return this.centerPoint;
    }
    // 读取自定义实体数据
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.startPoint = filter.readPoint("startPoint").val;
        this.endPoint = filter.readPoint("endPoint").val;
        this.centerPoint = filter.readPoint("centerPoint").val;
        this._oldEndPoint = filter.readPoint("oldEndPoint").val;
        this._oldStartPoint = filter.readPoint("oldStartPoint").val;
        this.oldCenterPoint = filter.readPoint("oldCenterPoint").val;
        this._breakPoints = filter.readPoints("breakPoint").val;
        this._insterPoints = filter.readPoints("insterPoints").val;
        this._turnPoints = filter.readPoints("turnPoint").val;
        this._wallWidth = filter.readDouble("wallWidth").val;
        this.doorBreakPoints = filter.readPoints("doorBreakPoints").val;
        this.doorInsterPoints = filter.readPoints("doorInsterPoints").val;
        this.dol = filter.readDouble("dol").val;
        const _breakPtsCount = filter.readString("breakPtsCount").val;
        if (_breakPtsCount) {
            this.breakPtsCount = _breakPtsCount.split(',').map(Number);
        }
        const _doorBreakPtsCount = filter.readString("doorBreakPtsCount").val;
        if (_doorBreakPtsCount) {
            this.doorBreakPtsCount = _doorBreakPtsCount.split(',').map(Number);
        }
        const _doorHandles = filter.readString("doorHandles").val;
        if (_doorHandles) {
            this._doorHandles = _doorHandles.split(',');
        }
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;

        return true;
    }
    // 写入自定义实体数据
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        if (this.startPoint.distanceTo(this.endPoint) > 0.00001) {
            this.centerPoint = this.startPoint.clone().addvec(this.endPoint.sub(this.startPoint).mult(1 / 2));
        }
        filter.writePoint("endPoint", this.endPoint);
        filter.writePoint("startPoint", this.startPoint);
        filter.writePoint("centerPoint", this.centerPoint);
        filter.writePoint("oldStartPoint", this._oldStartPoint);
        filter.writePoint("oldEndPoint", this._oldEndPoint);
        filter.writePoint("oldCenterPoint", this.oldCenterPoint);
        filter.writePoints("breakPoint", this._breakPoints);
        filter.writePoints("insterPoints", this._insterPoints);
        filter.writePoints("turnPoint", this._turnPoints);
        filter.writeDouble("wallWidth", this._wallWidth);
        filter.writePoints("doorBreakPoints", this.doorBreakPoints);
        filter.writePoints("doorInsterPoints", this.doorInsterPoints);
        filter.writeDouble("dol", this.dol);
        if (this.breakPtsCount.length > 0) {
            filter.writeString("breakPtsCount", this.breakPtsCount.join(','));
        }
        if (this.doorBreakPtsCount.length > 0) {
            filter.writeString("doorBreakPtsCount", this.doorBreakPtsCount.join(','));
        }
        if (this._doorHandles.length > 0) {
            filter.writeString("doorHandles", this.doorHandles.join(','));
        }
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);

        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        const line = new McDbLine(this.startPoint, this.endPoint);
        this.oldCenterPoint = this.centerPoint.clone();
        if (iIndex === 0) {
            this._oldStartPoint = this.startPoint.clone();
            this.startPoint.x += dXOffset;
            this.startPoint.y += dYOffset;
            this.startPoint.z += dZOffset;
            if (this._doorHandles.length > 0) {
                const point = line.getClosestPointTo(this.startPoint, true).val;
                this.startPoint = point.clone();
            }
        } else if (iIndex === 1) {
            this._oldEndPoint = this.endPoint.clone();
            this.endPoint.x += dXOffset;
            this.endPoint.y += dYOffset;
            this.endPoint.z += dZOffset;
            if (this._doorHandles.length > 0) {
                const point = line.getClosestPointTo(this.endPoint, true).val;
                this.endPoint = point.clone();
            }
        } else if (iIndex === 2) {
            this.endPoint.x += dXOffset;
            this.endPoint.y += dYOffset;
            this.endPoint.z += dZOffset;
            this.startPoint.x += dXOffset;
            this.startPoint.y += dYOffset;
            this.startPoint.z += dZOffset;
        };
        this.centerPoint = this.startPoint.clone().addvec(this.endPoint.sub(this.startPoint).mult(1 / 2));
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray()
        ret.append(this.startPoint);
        ret.append(this.endPoint);
        ret.append(this.centerPoint);
        return ret;
    };
    // 绘制实体 
    public worldDraw(draw: MxCADWorldDraw): void {
        const allEntityArr: McDbEntity[] = [];

        const breakPoints = [...this._breakPoints];
        const breakPtsCount = [...this.breakPtsCount];
        const insterPoints = [...this._insterPoints];
        if (this._doorHandles.length > 0) {
            breakPoints.push(...this.doorBreakPoints);
            breakPtsCount.push(...this.doorBreakPtsCount);
            insterPoints.push(...this.doorInsterPoints);
            if (this.doorBreakPoints.length > 0) {
                const ptsArr1: McGePoint3d[] = [];
                const ptsArr2: McGePoint3d[] = [];
                this.doorBreakPoints.forEach((pt, index) => {
                    if (index % 2 === 0) {
                        ptsArr1.push(pt)
                        if (ptsArr1.length === 2) {
                            const line = new McDbLine(ptsArr1[0], ptsArr1[1]);
                            draw.drawEntity(line);
                            allEntityArr.push(line)
                            ptsArr1.length = 0;
                        }
                    } else {
                        ptsArr2.push(pt);
                        if (ptsArr2.length === 2) {
                            const line = new McDbLine(ptsArr2[0], ptsArr2[1]);
                            draw.drawEntity(line);
                            allEntityArr.push(line)
                            ptsArr2.length = 0;
                        }
                    }
                });
            }
        }
        const { pl, pt1, pt2, pt3, pt4 } = getPolyline(this.startPoint.clone(), this.endPoint.clone(), this._wallWidth, this._turnPoints);
        if (this.turnPoints.length > 0 || breakPoints.length > 0) {
            // 有拐点,有断点
            const lineArr: McDbLine[] = [];
            const line1 = new McDbLine(pt1, pt4);
            const line2 = new McDbLine(pt2, pt3);
            lineArr.push(line2, line1)
            const dist1 = pt1.distanceTo(pt2);
            const dist2 = pt4.distanceTo(pt3);
            if (dist1 - this._wallWidth < 0.00001) {
                const line3 = new McDbLine(pt2, pt1);
                if (this._turnPoints.length > 0) {
                    let r = this._turnPoints.filter(pt => pt2.distanceTo(pt) < this.dol || pt1.distanceTo(pt) < this.dol);
                    if (r.length === 0) lineArr.push(line3)
                } else {
                    lineArr.push(line3);
                }
            }
            if (dist2 - this._wallWidth < 0.00001) {
                const line4 = new McDbLine(pt3, pt4);
                if (this._turnPoints.length > 0) {
                    let r = this._turnPoints.filter(pt => pt3.distanceTo(pt) < this.dol || pt4.distanceTo(pt) < this.dol);
                    if (r.length === 0) lineArr.push(line4)
                } else {
                    lineArr.push(line4)
                }
            };
            if (breakPoints.length > 0) {
                /**
                 * 有断点
                 * 找出每一个断点所在的直线
                 * 以每一组断点去切割直线，再绘制切割好的直线
                 */
                let count = 0;
                const breakPlArr: McDbPolyline[] = []; // 存储每一组断点多段线
                const breakPts = [];
                breakPtsCount.forEach((item) => {
                    count += item;
                    const pl = new McDbPolyline()
                    if (count > 0) {
                        // 一组断点
                        const breakPoint = breakPoints.filter((pt, i) => i < count && i >= count - item);
                        breakPoint.forEach(pt => {
                            pl.addVertexAt(pt);
                        })
                        breakPts.push(breakPoint)
                    };
                    breakPlArr.push(pl);
                });
                lineArr.forEach((l) => {
                    // 直线上的断点集合
                    const plArr: McDbPolyline[] = []; // 直线上的断点连线
                    const r: McGePoint3d[] = []; // 直线上的断点集合
                    const _r: McGePoint3d[] = []; // 直线上的单断点集合
                    breakPts.forEach((item) => {
                        const points = item.filter(pt => {
                            const point = l.getClosestPointTo(pt, false).val;
                            const dist = point.distanceTo(pt);
                            return dist < this.dol
                        });
                        if (points.length > 0) {
                            r.push(...points)
                            if (points.length % 2 === 0) {
                                points.forEach((pt, index) => {
                                    if (index % 2 == 0) {
                                        const pl = new McDbPolyline();
                                        pl.addVertexAt(pt);
                                        pl.addVertexAt(points[index + 1]);
                                        plArr.push(pl);
                                    }
                                });
                            } else {
                                _r.push(...points)
                            }
                        }
                    })
                    if (r.length > 0) {
                        // 直线上含有断点
                        l.splitCurves(r).forEach((obj) => {
                            const _line = obj.clone() as McDbLine;
                            const midPt = _line.startPoint.clone().addvec(_line.endPoint.sub(_line.startPoint).mult(1 / 2))
                            const res1 = r.filter(pt => _line.startPoint.distanceTo(pt) < this.dol);
                            const res2 = r.filter(pt => _line.endPoint.distanceTo(pt) < this.dol);
                            // 起始点只有一个是断点
                            if (res1.length === 0 || res2.length === 0) {
                                if (_r.length === 0) {
                                    // 无单断点直接绘制
                                    draw.drawEntity(_line)
                                    allEntityArr.push(_line)
                                } else {
                                    // 筛选出在切断后直线的单断点
                                    const singlePts = _r.filter(pt => _line.getDistAtPoint(pt).ret);//切割后线段上的单断点
                                    if (singlePts.length === 0) {
                                        // 若单断点不在这条直线上，则直接绘制
                                        draw.drawEntity(_line)
                                        allEntityArr.push(_line)
                                    } else {
                                        // 若单断点在这条直线上，则判断是否与对应的交点同向
                                        singlePts.forEach(pt => {
                                            if (breakPlArr.length > 0) {
                                                const _plNum = breakPlArr.filter((pl, index) => {
                                                    const num = pl.numVerts();
                                                    if (num === 2) {
                                                        return this.countLineToPl(pl, index, pt, midPt, insterPoints);
                                                    } else {
                                                        let flag = false;
                                                        for (let i = 0; i < num; i++) {
                                                            if (i % 2 == 0) {
                                                                const _pl = new McDbPolyline();
                                                                _pl.addVertexAt(pl.getPointAt(i).val);
                                                                _pl.addVertexAt(pl.getPointAt(i + 1).val);
                                                                const r = this.countLineToPl(_pl, index, pt, midPt, insterPoints)
                                                                if (r) flag = true;
                                                            }
                                                        };
                                                        return flag
                                                    }
                                                    // else if(num === 0){
                                                    //     return false
                                                    // }

                                                });
                                                if (_plNum.length === singlePts.length) {
                                                    draw.drawEntity(_line)
                                                    allEntityArr.push(_line)
                                                }
                                            }
                                        })
                                    }

                                }
                            }
                            // 起始点都是断点
                            if (res1.length !== 0 && res2.length !== 0) {
                                const _r1 = plArr.filter((pl) => {
                                    const pt = pl.getClosestPointTo(midPt, false).val;
                                    return pt.distanceTo(midPt) < this.dol
                                });
                                if (_r1.length === 0) {
                                    draw.drawEntity(_line)
                                    allEntityArr.push(_line)
                                }
                            }
                        })
                    } else {
                        // 线段上无断点
                        const length = l.getLength().val;
                        if (Math.abs(length - this._wallWidth) < this.dol) {
                            const midPt = l.getPointAtDist(l.getLength().val / 2).val;
                            const distArr = insterPoints.map(pt => pt.distanceTo(midPt));
                            const index = distArr.findIndex(element => element === Math.min(...distArr));
                            const pl = breakPlArr[index];
                            const insterPt = insterPoints[index];
                            const p = l.getClosestPointTo(insterPt, true).val;
                            if (p.distanceTo(insterPt) > this.dol) {
                                const closePt = pl.getClosestPointTo(insterPt, true).val;
                                const insterWallWidth = closePt.distanceTo(insterPt);
                                const insterPtToLine = l.getClosestPointTo(insterPt, true).val.distanceTo(insterPt);
                                if (insterPtToLine > insterWallWidth) {
                                    draw.drawEntity(l)
                                    allEntityArr.push(l)
                                } else {
                                    const midVec = midPt.sub(insterPt);
                                    const num = pl.numVerts();
                                    if (num === 2) {
                                        const vec = pl.getPointAt(0).val.sub(pl.getPointAt(1).val);
                                        const angle = Number((midVec.angleTo1(vec) * (180 / Math.PI)).toFixed(4));
                                        if (angle === 0 || angle === 180) {
                                            draw.drawEntity(l)
                                            allEntityArr.push(l)
                                        }
                                    } else {
                                        let count = 0;
                                        for (let i = 0; i < num; i++) {
                                            if (i % 2 == 0) {
                                                const _pl = new McDbPolyline();
                                                _pl.addVertexAt(pl.getPointAt(i).val);
                                                _pl.addVertexAt(pl.getPointAt(i + 1).val);
                                                _pl.trueColor = new McCmColor(255, 0, 0);
                                                const vec = _pl.getPointAt(0).val.sub(_pl.getPointAt(1).val);
                                                const angle = Number((midVec.angleTo1(vec) * (180 / Math.PI)).toFixed(4));
                                                if (angle === 0 || angle === 180) {
                                                    count += 1;
                                                }
                                            }
                                        }
                                        if (count === num / 2) {
                                            draw.drawEntity(l);
                                            allEntityArr.push(l)
                                        }
                                    }
                                }
                            }

                        } else {
                            draw.drawEntity(l);
                            allEntityArr.push(l)
                        }
                    }
                });
            } else {
                // 无断点，全是拐点
                lineArr.forEach((l, index) => {
                    if (index < 2) {
                        draw.drawEntity(l);
                        allEntityArr.push(l)
                    } else {
                        const length = l.getLength().val;
                        const r = this._turnPoints.filter(pt => pt.distanceTo(l.startPoint) < this.dol || pt.distanceTo(l.endPoint) < this.dol)
                        if (Math.abs(length - this._wallWidth) < this.dol && r.length === 0) {
                            draw.drawEntity(l);
                            allEntityArr.push(l)
                        }
                    }
                })
            }
        }
        if (!breakPoints.length && !this.turnPoints.length) {
            draw.drawEntity(pl);
            allEntityArr.push(pl)
        }
        const line = new McDbLine(this.startPoint, this.endPoint)
        draw.drawOsnapEntity(line);

        this.getBox(allEntityArr);
    }
    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        entityArr.forEach(entity => {
            const { minPt, maxPt, ret } = entity.getBoundingBox();
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }
    private countLineToPl(pl: McDbPolyline, index: number, pt: McGePoint3d, midPt: McGePoint3d, insterPoints: McGePoint3d[]): Boolean {
        const dist1 = pl.getPointAt(0).val.distanceTo(pt);
        const dist2 = pl.getPointAt(1).val.distanceTo(pt);
        const insterPt = insterPoints[index];
        if (dist1 < this.dol || dist2 < this.dol) {
            const _closePt = pl.getClosestPointTo(insterPt, true).val;
            const v = insterPt.sub(_closePt);
            const _v = midPt.sub(_closePt);
            let angle1 = v.angleTo2(McGeVector3d.kXAxis, McGeVector3d.kNegateZAxis);
            let angle2 = _v.angleTo2(McGeVector3d.kXAxis, McGeVector3d.kNegateZAxis);
            const angle = Math.abs(angle1 - angle2);
            if (Number((angle * (180 / Math.PI)).toFixed(4)) >= 90 && Number((angle * (180 / Math.PI)).toFixed(4)) <= 270) {
                return true
            } else {
                return false
            }
        } else {
            return false
        }
    }
    // 计算门窗在墙体上的数据
    public countDoorData(): void {
        // 清空门窗数据
        this.doorBreakPoints.length = this.doorBreakPtsCount.length = this.doorInsterPoints.length = 0;
        // 根据与墙体关联的门窗实体，添加墙体上的断点和线段
        if (this._doorHandles.length > 0) {
            const dataBase: McDbDatabase = MxCpp.getCurrentMxCAD().getDatabase();
            this._doorHandles.forEach(handle => {
                const objectId = dataBase.handleToIdIndex(handle);
                const ent = objectId.getMcDbEntity() as McDbCustomEntity;
                /**
                 * 根据墙体上的单开门实体长度，添加墙体上的断点和线段
                 */
                const door = ent.clone() as McDbTestSingleDoor;
                const v = this.endPoint.sub(this.startPoint).normalize();
                const pt1 = door.getPosition().clone().addvec(v.clone().mult(door.width / 2));
                const pt2 = door.getPosition().clone().addvec(v.clone().negate().mult(door.width / 2));
                // 判断单开门是否在墙体中，如果不在墙体中则删除该单开门句柄
                const line = new McDbLine(this.startPoint, this.endPoint);
                const _pt1 = line.getClosestPointTo(pt1, false).val;
                const _pt2 = line.getClosestPointTo(pt2, false).val;
                if (_pt1.distanceTo(pt1) < this.dol && _pt2.distanceTo(pt2) < this.dol) {
                    // 若单开门中包含了墙体断点，删除该单开门句柄
                    const { pl: doorPl } = getPolyline(pt1, pt2, this._wallWidth);
                    const findBreakPts = this._turnPoints.filter(pt => {
                        const res = doorPl.getClosestPointTo(pt);
                        if (res.ret) {
                            return res.val.distanceTo(pt) < this.dol
                        } else {
                            return false
                        }
                    });
                    if (findBreakPts.length === 0) {
                        const _v = v.clone().perpVector();
                        const breakPt1 = pt1.clone().addvec(_v.clone().mult(this._wallWidth / 2));
                        const _breakPt1 = pt2.clone().addvec(_v.clone().mult(this._wallWidth / 2));
                        const breakPt2 = pt1.clone().addvec(_v.clone().negate().mult(this._wallWidth / 2));
                        const _breakPt2 = pt2.clone().addvec(_v.clone().negate().mult(this._wallWidth / 2));
                        this.doorBreakPoints.push(breakPt1, _breakPt1, breakPt2, _breakPt2);
                        this.doorBreakPtsCount.push(4);
                        this.doorInsterPoints.push(door.getPosition());
                    } else {
                        this._doorHandles.splice(this._doorHandles.indexOf(door.getHandle()), 1);
                        door.wallHandle = "";
                    }
                } else {
                    this._doorHandles.splice(this._doorHandles.indexOf(door.getHandle()), 1);
                    door.wallHandle = "";
                }
            })
        }
    }
    // 设置墙体起点
    public setStartPoint(pt: McGePoint3d) {
        this.startPoint = pt.clone()
        this._oldStartPoint = pt.clone()
    }
    // 获取墙体起点
    public getStartPoint(): McGePoint3d {
        return this.startPoint;
    }
    // 设置墙体结束点
    public setEndPoint(pt: McGePoint3d) {
        this.endPoint = pt.clone()
        this._oldEndPoint = pt.clone()
    }
    // 获取墙体结束点
    public getEndPoint(): McGePoint3d {
        return this.endPoint.clone();
    }
    // 增加墙体断点
    public addBreakPoints(pts: McGePoint3d[]) {
        this.breakPoints.push(...pts);
    }
    // 增加墙体交点
    public addInsterPoints(pts: McGePoint3d[]) {
        this._insterPoints.push(...pts);
    }
    // 增加墙体单次相交的断点数
    public addBreakPtsCount(val: number[]) {
        if (val.length > 0) {
            this.breakPtsCount.push(...val);
        }
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
};

export class MxdrawWalls {
    private startPoint: McGePoint3d = new McGePoint3d();
    private endPoint: McGePoint3d = new McGePoint3d();
    private wallWidth: number = 10;
    private pointArr: McGePoint3d[] = [];
    private turnPointArr: McGePoint3d[] = [];
    private insterPointArr: McGePoint3d[] = [];
    private breakPtsCount: number[] = [];
    private doorHandles: string[] = [];
    private dol = 0.00001;

    // 获取相交墙体
    public getIntersectingWalls(startPoint: McGePoint3d, endPoint: McGePoint3d, wallWidth: number): McObjectId[] {
        this.startPoint = startPoint.clone();
        this.endPoint = endPoint.clone();
        this.wallWidth = wallWidth;
        const intersectingWalls: McObjectId[] = [];
        const { pt1, pt3, line } = getPolyline(startPoint, endPoint, wallWidth);
        const length = line.getLength().val;
        // 设置过滤器，过滤出自定义实体
        let filter = new MxCADResbuf();
        filter.AddString("CUSTOMENTITY", 5020);
        const ss = new MxCADSelectionSet();
        ss.crossingSelect(pt1.x - length, pt1.y - length, pt3.x + length, pt3.y + length, filter);
        // 与其他墙体相交
        if (ss.count() !== 0) {
            ss.forEach(id => {
                const ent = id.getMcDbEntity();
                if ((ent as McDbCustomEntity).getTypeName() === "McDbTestWall") {
                    intersectingWalls.push(id);
                }
            });
        }
        return intersectingWalls;
    }
    // 初始绘制墙体
    public drawWall(startPoint: McGePoint3d, endPoint: McGePoint3d, wallWidth: number): McObjectId {
        const wallObjIds = this.getIntersectingWalls(startPoint, endPoint, wallWidth);
        this.dealingWithWalls(wallObjIds);
        const wallId = this.draw();
        // 重绘墙体的时候更新关联信息
        if (this.doorHandles.length > 0) {
            const dataBase: McDbDatabase = MxCpp.getCurrentMxCAD().getDatabase();
            this.doorHandles.forEach(handle => {
                const objectId = dataBase.handleToIdIndex(handle);
                const entity = objectId.getMcDbEntity() as McDbTestSingleDoor | McDbTestStandardWindow;
                entity.wallHandle = wallId.getMcDbEntity()?.getHandle();
            })
        }
        MxCpp.getCurrentMxCAD().updateDisplay();
        return wallId
    }
    private isAngleBetween(angleRad: number, startRad: number, endRad: number) {
        let minRad, maxRad
        if (startRad - endRad > 0) {
            minRad = endRad;
            maxRad = startRad;
        } else {
            minRad = startRad;
            maxRad = endRad;
        }
        if (maxRad - minRad > Math.PI) {
            return angleRad <= minRad || angleRad >= maxRad;
        } else {
            return angleRad >= minRad && angleRad <= maxRad;
        }
    }
    // 处理相交墙体得到断点与拐点
    private dealingWithWalls(wallObjIds: McObjectId[]) {
        const { pl, pt1, pt2, pt3, pt4, line } = getPolyline(this.startPoint, this.endPoint, this.wallWidth);
        if (wallObjIds.length != 0) {
            // 与新墙体有交点的墙体集合
            const wallArr = [];
            // 与其他墙体相交
            wallObjIds.forEach(id => {
                const entity = id.getMcDbEntity();
                if ((entity as McDbCustomEntity).getTypeName() === "McDbTestWall") {
                    const wall = entity.clone() as McDbTestWall
                    const { pl: _pl, pt1: _pt1, pt2: _pt2, pt3: _pt3, pt4: _pt4, line: _line } = getPolyline(wall.getStartPoint(), wall.getEndPoint(), wall.wallWidth);
                    const _pointArr: McGePoint3d[] = [];
                    _pl.IntersectWith(pl, McDb.Intersect.kOnBothOperands).forEach(item => _pointArr.push(item));
                    if (_pointArr.length > 0) {
                        wallArr.push({ id, wall, pl: _pl, pt1: _pt1, pt2: _pt2, pt3: _pt3, pt4: _pt4, line: _line, pointArr: _pointArr })
                    }
                }
            });
            if (wallArr.length != 0) {
                wallArr.forEach(item => {
                    const { id, wall, pl: _pl, pt1: _pt1, pt2: _pt2, pt3: _pt3, pt4: _pt4, line: _line, pointArr: _pointArr } = item;
                    /**
                     * 根据交点位置判断是否是拐点
                     * 中心相交的交点与比对中心线端点距离小于1/2墙宽的都是拐点
                     */
                    const insterPointArr = (line as McDbLine).IntersectWith(_line as McDbLine, McDb.Intersect.kExtendBoth);
                    if (insterPointArr.length() > 0) {
                        const insterPt = insterPointArr.at(0);
                        this.insterPointArr.push(insterPt);
                        const dist1 = insterPt.distanceTo(_line.startPoint);
                        const dist2 = insterPt.distanceTo(_line.endPoint);
                        const dist3 = insterPt.distanceTo(line.startPoint);
                        const dist4 = insterPt.distanceTo(line.endPoint);
                        let vec, _vec;
                        dist3 < dist4 ? vec = (line as McDbLine).endPoint.sub(insterPt).normalize() :
                            vec = (line as McDbLine).startPoint.sub(insterPt).normalize();
                        dist1 < dist2 ? _vec = (_line as McDbLine).endPoint.sub(insterPt).normalize() :
                            _vec = (_line as McDbLine).startPoint.sub(insterPt).normalize();
                        const angle1 = vec.angleTo2(McGeVector3d.kXAxis, McGeVector3d.kNegateZAxis);
                        const angle2 = _vec.angleTo2(McGeVector3d.kXAxis, McGeVector3d.kNegateZAxis);
                        if ((dist1 <= wall.wallWidth / 2 || dist2 <= wall.wallWidth / 2) &&
                            (dist3 <= this.wallWidth / 2 || dist4 <= this.wallWidth / 2)
                        ) {
                            // 绘制拐角
                            const turnArr = []
                            const line1 = new McDbLine(pt1, pt4);
                            const line2 = new McDbLine(pt2, pt3);
                            const _line1 = new McDbLine(_pt1, _pt4);
                            const _line2 = new McDbLine(_pt2, _pt3);
                            let line1ClosePt, line2ClosePt;
                            pt1.distanceTo(insterPt) < pt4.distanceTo(insterPt) ? line1ClosePt = pt1.clone() : line1ClosePt = pt4.clone();
                            pt2.distanceTo(insterPt) < pt3.distanceTo(insterPt) ? line2ClosePt = pt2.clone() : line2ClosePt = pt3.clone();
                            const lineInsterPts1 = line1.IntersectWith(_line1, McDb.Intersect.kExtendBoth)
                            const lineInsterPts2 = line1.IntersectWith(_line2, McDb.Intersect.kExtendBoth)
                            const lineInsterPts3 = line2.IntersectWith(_line1, McDb.Intersect.kExtendBoth)
                            const lineInsterPts4 = line2.IntersectWith(_line2, McDb.Intersect.kExtendBoth)
                            const _point1 = lineInsterPts1.length() > 0 ? lineInsterPts1.at(0) : line1ClosePt;
                            const _point2 = lineInsterPts2.length() > 0 ? lineInsterPts2.at(0) : line1ClosePt;
                            const _point3 = lineInsterPts3.length() > 0 ? lineInsterPts3.at(0) : line2ClosePt;
                            const _point4 = lineInsterPts4.length() > 0 ? lineInsterPts4.at(0) : line2ClosePt;
                            const inflectioPoints = [_point1, _point2, _point3, _point4];
                            const r = inflectioPoints.filter((pt) => {
                                const v = pt.sub(insterPt);
                                const angle3 = v.angleTo2(McGeVector3d.kXAxis, McGeVector3d.kNegateZAxis);
                                return this.isAngleBetween(angle3, angle1, angle2)
                            })
                            if (r.length) {
                                turnArr.push(r[0])
                                const point = r[0];
                                const _r = inflectioPoints.filter((pt) => {
                                    const v = pt.sub(insterPt);
                                    const _v = point.sub(insterPt)
                                    const angle = v.angleTo2(_v, McGeVector3d.kNegateZAxis);
                                    return Math.abs(angle - Math.PI) < this.dol
                                })
                                if (_r.length) turnArr.push(_r[0])
                            }
                            this.turnPointArr.push(...turnArr);
                            this.breakPtsCount.push(0);
                            const _wall = wall.clone() as McDbTestWall;
                            _wall.turnPoints = [..._wall.turnPoints, ...turnArr];
                            _wall.addInsterPoints([insterPt]);
                            _wall.addBreakPtsCount([0]);
                            MxCpp.getCurrentMxCAD().drawEntity(_wall);
                            id.erase();
                        } else {
                            // 没有拐角直接根据断点绘制
                            this.pointArr.push(..._pointArr);
                            this.breakPtsCount.push(_pointArr.length);
                            const _wall = wall.clone() as McDbTestWall;
                            _wall.addBreakPoints(_pointArr);
                            _wall.addInsterPoints([insterPt]);
                            _wall.addBreakPtsCount([_pointArr.length]);
                            MxCpp.getCurrentMxCAD().drawEntity(_wall);
                            id.erase();
                        }
                    }

                });
            }
        }
    }
    // 绘制墙体
    private draw(): McObjectId {
        const mxcad = MxCpp.getCurrentMxCAD();
        const wall = new McDbTestWall();
        wall.setEndPoint(this.endPoint);
        wall.setStartPoint(this.startPoint);
        wall.wallWidth = this.wallWidth;
        wall.doorHandles = this.doorHandles;
        if (this.pointArr && this.pointArr.length) {
            wall.addBreakPoints(this.pointArr)
        }
        wall.turnPoints = this.turnPointArr ? this.turnPointArr : [];
        if (this.insterPointArr && this.insterPointArr.length) {
            wall.addInsterPoints(this.insterPointArr)
        }
        if (this.breakPtsCount && this.breakPtsCount.length) {
            wall.addBreakPtsCount(this.breakPtsCount)
        }
        return mxcad.drawEntity(wall);
    }
    // 更新墙体
    public updateWall(wallId: McObjectId, isRedraw: Boolean) {
        const wall = wallId.getMcDbEntity() as McDbTestWall;
        if (wall.objectName === "McDbCustomEntity" && (wall as McDbCustomEntity).getTypeName() === "McDbTestWall") {
            const wallClone = wall.clone() as McDbTestWall;
            if (wallClone.doorHandles.length > 0) {
                this.doorHandles = [...wallClone.doorHandles];
                // 找到关联门窗并更新
                const v = wallClone.wallCenterPoint.sub(wallClone.wallOldCenterPoint);
                const _v = wallClone.getEndPoint().sub(wallClone.getStartPoint());
                const angle = v.angleTo1(_v);
                const dataBase: McDbDatabase = MxCpp.getCurrentMxCAD().getDatabase();
                this.doorHandles.forEach(handle => {
                    const objectId = dataBase.handleToIdIndex(handle);
                    const entity = objectId.getMcDbEntity();
                    if (this.doorHandles.includes(entity?.getHandle())) {
                        // 如果墙体上下左右移动，则门窗也要跟着移动
                        if (Number(angle.toFixed(5)) > 0 && Number(angle.toFixed(5)) < Number(Math.PI.toFixed(5))) {
                            const door = entity as (McDbTestSingleDoor | McDbTestStandardWindow);
                            const matrix = new McGeMatrix3d();
                            matrix.setToTranslation(v);
                            door.transformBy(matrix);
                            door.assertObjectModification(true);
                        }

                    }
                })
            }
            // 先修改移动前相交墙体里的断点和拐点
            const oldWallObjIds = this.getIntersectingWalls(wall.oldStartPoint, wall.oldEndPoint, wall.wallWidth);
            // 更新与墙体相交的墙
            oldWallObjIds.forEach(id => {
                const _wall = (id.getMcDbEntity()) as McDbTestWall;
                _wall.breakPoints = _wall.breakPoints.filter(pt => {
                    const r = wallClone.breakPoints.filter(point => point.distanceTo(pt) < this.dol);
                    return r.length === 0;
                });
                _wall.turnPoints = _wall.turnPoints.filter(pt => {
                    const r = wallClone.turnPoints.filter(point => point.distanceTo(pt) < this.dol);
                    return r.length === 0;
                });
                const indexArr = [];
                _wall.insterPoints = _wall.insterPoints.filter((pt, index) => {
                    const r = wallClone.insterPoints.filter(point => point.distanceTo(pt) < this.dol);
                    if (r.length === 0) {
                        indexArr.push(index);
                        return true
                    }
                });
                _wall.breakPtsCounts = _wall.breakPtsCounts.filter((count, index) => indexArr.includes(index));
                _wall.assertObjectModification(true);
            });
            // 是否重绘墙体，重新计算墙体点位
            if (isRedraw) {
                wallId.erase();
                setTimeout(() => {
                    this.drawWall(wall.getStartPoint(), wall.getEndPoint(), wall.wallWidth);
                }, 0)
            };
        } else {
            return
        }
    }
}

export function asyncDrawWall(startPoint: McGePoint3d, endPoint: McGePoint3d, wallWidth: number): Promise<McObjectId> {
    return new Promise((resolve, reject) => {
        try {
            const newWall = new MxdrawWalls();
            const objectId = newWall.drawWall(startPoint, endPoint, wallWidth);
            resolve(objectId);
        } catch (error) {
            reject(error);
        }
    });
}

// 绘制墙体
export async function drawWall() {
    const getWallWidth = new MxCADUiPrDist();
    getWallWidth.setMessage(`\n${t('请设置墙体宽度')}:`);
    let wallWidth = await getWallWidth.go();
    if (!wallWidth) wallWidth = 30;
    while (true) {
        const getPoint1 = new MxCADUiPrPoint();
        getPoint1.setMessage(`\n${t('请设置墙体起点')}:`);
        const pt1 = await getPoint1.go();
        if (!pt1) break;

        const getDist = new MxCADUiPrDist();
        getDist.setBasePt(pt1);
        getDist.setMessage(`\n${t('请设置墙体长度')}:`);
        let pt2 = new McGePoint3d();
        getDist.setUserDraw((pt, pw) => {
            const v = pt1.sub(pt).normalize().perpVector().mult(wallWidth / 2);
            const line1 = new McDbLine(pt1.clone().addvec(v), pt.clone().addvec(v));
            pw.setColor(0x0000FF);
            pw.drawMcDbEntity(line1);

            const line2 = new McDbLine(pt1.clone().addvec(v.clone().negate()), pt.clone().addvec(v.clone().negate()));
            pw.setColor(0xFF0000);
            pw.drawMcDbEntity(line2);
            pt2 = pt.clone();
        });
        const dist = await getDist.go();
        if (!dist) break;
        const v = pt2.sub(pt1).normalize().mult(dist);
        const endPt = pt1.clone().addvec(v)
        const wall = new MxdrawWalls()
        wall.drawWall(pt1, endPt, wallWidth);
    }
}

// 绘制房间
export async function drawRoom() {
    // 设置类型
    const getTypeKey = new MxCADUiPrKeyWord();
    getTypeKey.setMessage(`\n${t('请选择绘制房间类型<C>')}`)
    getTypeKey.setKeyWords(`[${t("内部尺寸")}(I)/${t("外部尺寸")}(O)/${t("中心尺寸")}(C)]`)
    let typeKey = await getTypeKey.go();
    if (!typeKey) typeKey = "C";

    // 设置墙体宽度
    const getWallWidth = new MxCADUiPrDist();
    getWallWidth.setMessage(`\n${t('请设置墙体宽度')}`);
    let wallWidth = await getWallWidth.go();
    if (!wallWidth) wallWidth = 30;

    // 设置房间两个角点
    const getRoomCorner1 = new MxCADUiPrPoint();
    getRoomCorner1.setMessage(`\n${t('请设置房间角点1')}`);
    const roomCorner1 = await getRoomCorner1.go();
    if (!roomCorner1) return;
    const getRoomCorner2 = new MxCADUiPrPoint();
    getRoomCorner2.setMessage(`\n${t('请设置房间角点2')}`);
    getRoomCorner2.setUserDraw((pt, pw) => {
        const pl = new McDbPolyline();
        pl.isClosed = true;
        pl.addVertexAt(roomCorner1);
        pl.addVertexAt(new McGePoint3d(pt.x, roomCorner1.y));
        pl.addVertexAt(pt);
        pl.addVertexAt(new McGePoint3d(roomCorner1.x, pt.y));
        pw.drawMcDbEntity(pl);
        const line = new McDbLine(roomCorner1, pt);
        pw.drawMcDbEntity(line);
    })
    const roomCorner2 = await getRoomCorner2.go();
    if (!roomCorner2) return;
    // 绘制房间
    const pl = new McDbPolyline();
    pl.addVertexAt(roomCorner1);
    pl.addVertexAt(new McGePoint3d(roomCorner2.x, roomCorner1.y));
    pl.addVertexAt(roomCorner2);
    pl.addVertexAt(new McGePoint3d(roomCorner1.x, roomCorner2.y));
    const midPt = roomCorner1.clone().addvec(roomCorner2.sub(roomCorner1).mult(1 / 2));
    let pointArr: McGePoint3d[] = [];
    if (typeKey.toLocaleUpperCase() === "O") {
        // 外部尺寸
        pl.offsetCurves(wallWidth / 2, midPt).forEach(obj => {
            pointArr.length = 0;
            const _pl = obj.clone() as McDbPolyline;
            for (let i = 0; i < _pl.numVerts(); i++) {
                pointArr.push(_pl.getPointAt(i).val);
            }
        });
    } else if (typeKey.toLocaleUpperCase() === "I") {
        // 内部尺寸
        pl.offsetCurves(wallWidth / 2, midPt.clone().addvec(McGeVector3d.kYAxis.clone().mult(roomCorner1.distanceTo(roomCorner2)))).forEach(obj => {
            pointArr.length = 0;
            const _pl = obj.clone() as McDbPolyline;
            for (let i = 0; i < _pl.numVerts(); i++) {
                pointArr.push(_pl.getPointAt(i).val)
            }
        });
    } else if (typeKey.toLocaleUpperCase() === "C") {
        // 中心尺寸
        pointArr = [roomCorner1, new McGePoint3d(roomCorner2.x, roomCorner1.y), roomCorner2, new McGePoint3d(roomCorner1.x, roomCorner2.y)]
    };
    for (let i = 0; i < pointArr.length; i++) {
        let point1 = pointArr[i];
        let point2 = pointArr[i + 1];
        if (i === pointArr.length - 1) {
            point2 = pointArr[0]
        }
        setTimeout(() => {
            const wall = new MxdrawWalls()
            wall.drawWall(point1, point2, wallWidth);
        }, 20 * (i + 1))
    }
}

// 打断墙体
export async function breakTheWall() {
    let wall: McDbTestWall;
    let point1: McGePoint3d;
    let wallId: McObjectId;
    const dol = 0.0001;
    while (true) {
        const getWall = new MxCADUiPrPoint();
        getWall.setMessage(`${t('点取墙上的第一个点')}`);
        point1 = await getWall.go();
        if (!point1) break;
        const filter = new MxCADResbuf();
        filter.AddString("CUSTOMENTITY", 5020);
        wallId = MxCADUtility.findEntAtPoint(point1.x, point1.y, point1.z, -1, filter);
        if (wallId.id) {
            const entity = wallId.getMcDbEntity() as McDbCustomEntity;
            if (entity.getTypeName() === "McDbTestWall") {
                wall = entity.clone() as McDbTestWall;
                break;
            }
        }
    };
    if (wall) {
        const { pl, line } = getPolyline(wall.getStartPoint(), wall.getEndPoint(), wall.wallWidth);
        const _point1 = pl.getClosestPointTo(point1, false).val;
        const getPoint2 = new MxCADUiPrPoint();
        getPoint2.setMessage(`${t('点取墙上的第二个点')}`);
        getPoint2.setUserDraw((pt, pw) => {
            const line = new McDbLine(pt.x, pt.y, pt.z, _point1.x, _point1.y, _point1.z);
            pw.setColor(0xFF0000);
            pw.drawMcDbEntity(line);
        })
        const point2 = await getPoint2.go();
        if (!point2) return;
        const newPoint1 = line.getClosestPointTo(point1, false).val;
        const newPoint2 = line.getClosestPointTo(point2, false).val;
        const dist1 = newPoint1.distanceTo(line.startPoint);
        const dist2 = newPoint2.distanceTo(line.startPoint);
        wallId.erase();
        if (dist1 < dist2) {
            if (dist1 > dol) {
                await asyncDrawWall(line.endPoint, newPoint2, wall.wallWidth);
            }
            await asyncDrawWall(line.startPoint, newPoint1, wall.wallWidth);
        } else {
            if (dist2 > dol) {
                await asyncDrawWall(line.endPoint, newPoint1, wall.wallWidth);
            }
            await asyncDrawWall(line.startPoint, newPoint2, wall.wallWidth);
        };
        const newWall = new MxdrawWalls();
        newWall.updateWall(wallId, false);
    }
}

// 墙洞类
export class McDbTestHoleInTheWall extends McDbCustomEntity {
    // 定义McDbTestHoleInTheWall内部的点对象 
    // 墙洞位置
    private holePosition: McGePoint3d = new McGePoint3d();
    // 墙洞宽
    private holeWidth: number = 0;
    // 墙洞高
    private holeHeight: number = 0;
    // 墙洞所在墙起始点
    private wallPoints: McGePoint3d[] = [];
    // 墙洞开始点
    private startPoint: McGePoint3d = new McGePoint3d();
    // 墙洞结束点
    private endPoint: McGePoint3d = new McGePoint3d();
    // 墙洞中心点
    private centerPt: McGePoint3d = new McGePoint3d();
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();
    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new McDbTestHoleInTheWall(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "McDbTestHoleInTheWall";
    }
    //设置或获取墙洞宽
    public set wallHoleWidth(val: number) {
        this.holeWidth = val;
    }
    public get wallHoleWidth(): number {
        return this.holeWidth;
    }
    //设置或获取墙洞高
    public set wallHoleHeight(val: number) {
        this.holeHeight = val;
    }
    public get wallHoleHeight(): number {
        return this.holeHeight;
    }
    // 读取自定义实体数据
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.startPoint = filter.readPoint("startPoint").val;
        this.endPoint = filter.readPoint("endPoint").val;
        this.centerPt = filter.readPoint("centerPt").val;
        this.wallPoints = filter.readPoints("wallPoints").val;
        this.wallHoleHeight = filter.readDouble("wallHoleHeight").val;
        this.wallHoleWidth = filter.readDouble("wallHoleWidth").val;
        this.holePosition = filter.readPoint("holePosition").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        return true;
    }
    // 写入自定义实体数据
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("startPoint", this.startPoint);
        filter.writePoint("endPoint", this.endPoint);
        filter.writePoint("centerPt", this.centerPt);
        filter.writePoint("holePosition", this.holePosition);
        filter.writePoints("wallPoints", this.wallPoints);
        filter.writeDouble("wallHoleWidth", this.wallHoleWidth);
        filter.writeDouble("wallHoleHeight", this.wallHoleHeight);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        const line = new McDbLine(this.wallPoints[0], this.wallPoints[1]);
        const startPt = this.startPoint.clone();
        const endPt = this.endPoint.clone();
        const centerPt = this.centerPt.clone();
        if (iIndex === 0) {
            startPt.x += dXOffset;
            startPt.y += dYOffset;
            startPt.z += dZOffset;
            const pt = line.getClosestPointTo(startPt, false).val;
            this.startPoint = pt.clone();
            this.centerPt = this.startPoint.clone().addvec(this.endPoint.sub(this.startPoint).mult(1 / 2));
        } else if (iIndex === 1) {
            endPt.x += dXOffset;
            endPt.y += dYOffset;
            endPt.z += dZOffset;
            const pt = line.getClosestPointTo(endPt, false).val;
            this.endPoint = pt.clone();
            this.centerPt = this.startPoint.clone().addvec(this.endPoint.sub(this.startPoint).mult(1 / 2));
        } else if (iIndex === 2) {
            const newWidth = this.startPoint.distanceTo(this.endPoint);
            centerPt.x += dXOffset;
            centerPt.y += dYOffset;
            centerPt.z += dZOffset;
            const pt = line.getClosestPointTo(centerPt, false).val;
            const vec = this.startPoint.sub(this.endPoint).normalize().mult(newWidth / 2);
            const startPt = pt.clone().addvec(vec);
            const endPt = pt.clone().subvec(vec);
            const res1 = line.getDistAtPoint(startPt);
            const res2 = line.getDistAtPoint(endPt);
            if (res1.val && res2.val) {
                this.centerPt = pt.clone();
                this.startPoint = startPt.clone();
                this.endPoint = endPt.clone();
            }
        }
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray()
        ret.append(this.startPoint);
        ret.append(this.endPoint);
        ret.append(this.centerPt);
        return ret;
    };

    // 绘制实体 
    public worldDraw(draw: MxCADWorldDraw): void {
        const allEntityArr: McDbEntity[] = [];

        const { pl } = getPolyline(this.startPoint, this.endPoint, this.holeHeight);
        const line = new McDbLine(this.wallPoints[0], this.wallPoints[1]);
        const res1 = line.getDistAtPoint(this.startPoint);
        const res2 = line.getDistAtPoint(this.endPoint);
        if (res1.val && res2.val) {
            draw.drawEntity(pl);
            allEntityArr.push(pl)
        }

        this.getBox(allEntityArr);
    }
    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        entityArr.forEach(entity => {
            const { minPt, maxPt, ret } = entity.getBoundingBox();
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }
    // 设置墙洞位置
    public setHolePosition(pt: McGePoint3d) {
        this.holePosition = pt.clone();
        const line = new McDbLine(this.wallPoints[0], this.wallPoints[1]);
        const midPt = line.getClosestPointTo(pt, false).val;
        const vec = this.wallPoints[0].sub(midPt).normalize().mult(this.holeWidth / 2);
        this.startPoint = midPt.clone().addvec(vec);
        this.endPoint = midPt.clone().addvec(vec.negate());
        this.centerPt = this.startPoint.clone().addvec(this.endPoint.sub(this.startPoint).mult(1 / 2));
    }
    // 获取墙洞位置
    public getHolePosition() {
        return this.holePosition;
    }
    // 设置墙洞所在墙起始点
    public setWallPoints(points: McGePoint3d[]) {
        this.wallPoints = points.map(pt => pt.clone());
    }
    // 获取墙洞所在墙起始点
    public getWallPoints() {
        return this.wallPoints.map(pt => pt.clone());
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
}

// 挖墙洞
export async function digHoleInTheWall() {

    const getDist = new MxCADUiPrDist();
    getDist.setMessage(`${t('请设置墙洞宽<30>')}`);
    let dist = await getDist.go();
    if (!dist) dist = 30;

    const getTypeKey = new MxCADUiPrKeyWord();
    getTypeKey.setMessage(`\n${t('请设置墙洞插入位置<A>')}`)
    getTypeKey.setKeyWords(`[${t("任意位置")}(A)/${t("中间位置")}(M)]`)
    let typeKey = await getTypeKey.go();
    if (!typeKey) typeKey = "A";

    // 根据当前选择点获取墙
    const findWallFromPoint = (point: McGePoint3d): { wall: McDbTestWall | null } => {
        const filter = new MxCADResbuf();
        filter.AddString("CUSTOMENTITY", 5020);
        const wallId = MxCADUtility.findEntAtPoint(point.x, point.y, point.z, -1, filter);
        if (wallId.id) {
            const entity = wallId.getMcDbEntity() as McDbCustomEntity;
            if (entity.getTypeName() === "McDbTestWall") {
                const wall = entity.clone() as McDbTestWall;
                return { wall }
            }
        } else {
            return { wall: null }
        }
    }

    let point: McGePoint3d, wall: McDbTestWall;
    const hole = new McDbTestHoleInTheWall();
    while (true) {
        const getPoint = new MxCADUiPrPoint();
        getPoint.setMessage(`\n${t('点击确定墙洞插入位置')}`);
        getPoint.setUserDraw((pt, pw) => {
            const { wall: _wall } = findWallFromPoint(pt);
            if (_wall) {
                wall = _wall.clone() as McDbTestWall;
                hole.setWallPoints([wall.getStartPoint(), wall.getEndPoint()]);
                hole.wallHoleWidth = dist;
                hole.wallHoleHeight = wall.wallWidth;
                if (typeKey.toLocaleUpperCase() === "M") {
                    const midPt = wall.getStartPoint().clone().addvec(wall.getEndPoint().sub(wall.getStartPoint()).mult(1 / 2));
                    hole.setHolePosition(midPt);
                } else if (typeKey.toLocaleUpperCase() === "A") {
                    hole.setHolePosition(pt);
                };
                pw.setColor(0x0000FF);
                pw.drawMcDbEntity(hole);
            }
        })
        point = await getPoint.go();
        if (!point) break;
        break;
    };
    if (wall) {
        hole.trueColor = new McCmColor(0, 255, 255)
        MxCpp.getCurrentMxCAD().drawEntity(hole);
    }
}
