import { getIdentificationPattern } from "./PatternRecognition/hooks/usePatternRec";
import { defineAsyncComponent, createApp, reactive } from "vue";
import patternDialog from "./PatternRecognition/components/patternDialog.vue";
import compareDwg from "./CompareDwg/compareDwg.vue";
import annotationDialog from "./components/AnnotationDialog.vue";
import { ref } from 'vue';
import './icon/index.js';
import "./SymbolAnnotation/index";
import { t } from "./i18n";
import "./extools/index";
import "./MeasurementAnnotation/index";
import "./group/index"

const { utils } = MxPluginContext
const { browserCacheRef } = utils

// 图形识别
export const isShowPatternTable = browserCacheRef(false, "Mx_isShowPatternRec");

const div = document.createElement("div") 
document.body.appendChild(div)
const app = createApp(patternDialog)
app.mount(div);

// 注册图形列表弹窗
MxPluginContext.addDrawerComponent("PatternRec", {
  isShow: isShowPatternTable,
  component: defineAsyncComponent(() =>
    import("./PatternRecognition/patternRec.vue")
  ),

  width: 400,
  title: t("图形识别列表"),
  cmd: [{
    cmd: "Mx_PatternList",
    call: async (open) => {
      open(400)
    }
  }],
  onClose: (isClick) => {
    if (isClick) {
      isShowPatternTable.value = false;
    }
  }
});

MxPluginContext.addCommand("Mx_PatternRec", getIdentificationPattern);

// 图纸比对模块
export const isShowCompare = ref(false);
export const file_path = ref('');

const div_CompareDwg = document.createElement("div")
document.body.appendChild(div_CompareDwg)
const app_CompareDwg = createApp(compareDwg)
app_CompareDwg.mount(div_CompareDwg);

const getCompareFile = async () => {
  // electron 实现
  if (window.MxElectronAPI) {
    const { filePath } = await window.MxElectronAPI.convertDrawing()
    file_path.value = filePath;
    isShowCompare.value = true;
  } else {
    MxPluginContext.getMxCADUIImplement().uploadFile((param) => {
      if (param) {
        file_path.value = param.file_path;
        isShowCompare.value = true;
      }
    }, false);
  }
};

MxPluginContext.addCommand("Mx_CompareDWG", getCompareFile);

// 挂载标注弹框
export const isOpen = ref(false);
export const annotatName = reactive({ name: '', component: '' });

const div_dialog = document.createElement("div")
document.body.appendChild(div_dialog)
const app_dialog = createApp(annotationDialog)
app_dialog.mount(div_dialog);

MxPluginContext.addCommand("JTYZ", () => { annotatName.name = t('箭头引注'); annotatName.component = 'ArrowCitation'; isOpen.value = true });
MxPluginContext.addCommand("YCBZ", () => { annotatName.name = t('引出标注'); annotatName.component = 'ExtractionAnnotation'; isOpen.value = true });
MxPluginContext.addCommand("ZFBZ", () => { annotatName.name = t('做法标注'); annotatName.component = 'PracticeLabeling'; isOpen.value = true });
MxPluginContext.addCommand("SYFH", () => { annotatName.name = t('索引符号'); annotatName.component = 'IndexSymbol'; isOpen.value = true });
MxPluginContext.addCommand("SYTM", () => { annotatName.name = t('索引图名'); annotatName.component = 'IndexName'; isOpen.value = true });
MxPluginContext.addCommand("PQFH", () => { annotatName.name = t('剖切符号'); annotatName.component = 'SliceSymbol'; isOpen.value = true });
MxPluginContext.addCommand("BGBZ", () => { annotatName.name = t('标高标注'); annotatName.component = 'ElevationMarking'; isOpen.value = true });
MxPluginContext.addCommand("TMBZ", () => { annotatName.name = t('图名标注'); annotatName.component = 'MapTitleMarking'; isOpen.value = true });
MxPluginContext.addCommand("HZZX", () => { annotatName.name = t('绘制轴线'); annotatName.component = 'PlotAxis'; isOpen.value = true });
