import { drawSingleAxis } from "./PlotAxis/hooks";
import { drawWall, McDbTestWall, MxdrawWalls, drawRoom, breakTheWall, digHoleInTheWall, McDbTestHoleInTheWall } from "./Wall/hooks";
import { drawSingleDoor, McDbTestSingleDoor } from "./DoorsAndwindows/singleDoor"
import { McObject, MxCpp, McObjectId, McObjectIdType, McDbCustomEntity, McDbEntity, McDbDatabase } from 'mxcad';
import { drawStandardWindow, McDbTestStandardWindow } from "./DoorsAndwindows/standardWindow"
import { Mx_PlanView } from "./DoorsAndwindows/planView"

new McDbTestWall().rxInit();
new McDbTestHoleInTheWall().rxInit();
new McDbTestSingleDoor().rxInit();
new McDbTestStandardWindow().rxInit();

MxPluginContext.addCommand("HZZ", () => drawSingleAxis());
MxPluginContext.addCommand("HZQ", () => drawWall());
MxPluginContext.addCommand("HZFJ", () => drawRoom());
MxPluginContext.addCommand("DDQT", () => breakTheWall());
MxPluginContext.addCommand("WQD", () => digHoleInTheWall());
MxPluginContext.addCommand("DKM", () => drawSingleDoor());
MxPluginContext.addCommand("BZC", () => drawStandardWindow());
MxPluginContext.addCommand("Mx_PlanView", () => Mx_PlanView());

const mxcad: McObject = MxCpp.getCurrentMxCAD();
// 监听wall夹点变化
mxcad.mxdraw.on("objectGripEdit", (grips) => {
    grips.forEach((grip) => {
        const id = new McObjectId(grip.id, grip.type === "mxcad" ? McObjectIdType.kMxCAD : McObjectIdType.kMxDraw);
        if (id.isErase()) return;
        const ent = id.getMcDbEntity() as McDbEntity;
        if (!ent) return;
        if (ent.objectName === "McDbCustomEntity") {
            const typeName = (ent as McDbCustomEntity).getTypeName();
            // 墙体夹点变换
            if (typeName === "McDbTestWall") {
                // 更新绘制wall
                const newWall = new MxdrawWalls();
                newWall.updateWall(id, true);
                mxcad.clearMxCurrentSelect();
            } else if (typeName === "McDbTestSingleDoor" || typeName === "McDbTestStandardWindow") {
                // 单开门夹点变换，更新相关墙体
                const door = ent as McDbTestSingleDoor | McDbTestStandardWindow;
                let newWall: McDbTestWall | null = null;
                let oldWall: McDbTestWall | null = null;
                const dataBase: McDbDatabase = MxCpp.getCurrentMxCAD().getDatabase();
                if (door.wallHandle) {
                    newWall = dataBase.handleToIdIndex(door.wallHandle).getMcDbEntity() as McDbTestWall;
                }
                if (door.oldWallHandle) {
                    oldWall = dataBase.handleToIdIndex(door.oldWallHandle).getMcDbEntity() as McDbTestWall;
                }
                if (door.wallHandle === door.oldWallHandle) {
                    // 新墙体句柄与老墙体句柄相等，则更新墙体
                    if (newWall && oldWall) {
                        // 重新计算门窗在墙体中的相关信息
                        newWall.countDoorData();
                        // 更新墙体
                        newWall.assertObjectModification(true);
                    }
                } else {
                    // 新老关联墙体不相等，新墙体添加句柄，旧墙体删除句柄并更新
                    if (newWall) {
                        newWall.doorHandles = [...newWall.doorHandles, door.entityHandle];
                        newWall.assertObjectModification(true);
                    }
                    if (oldWall) {
                        oldWall.doorHandles = oldWall.doorHandles.filter(handle => handle !== door.entityHandle);
                        oldWall.assertObjectModification(true);
                    }
                }
            };
            mxcad.updateDisplay();
        };
    })
})

// 监听mxcad选择，若wall或门窗被删除则触发更新
const oldWallSelectIds: McObjectId[] = [];
const oldDoorSelectIds: McObjectId[] = [];
mxcad.on("selectChange", (ids: McObjectId[]) => {
    if (ids.length > 0) {
        oldDoorSelectIds.length = 0;
        oldWallSelectIds.length = 0;
        ids.forEach(id => {
            const entity = id.getMcDbEntity();
            if (entity && entity.objectName === "McDbCustomEntity") {
                if ((entity as McDbCustomEntity).getTypeName() === "McDbTestWall") {
                    oldWallSelectIds.push(id)
                } else if ((entity as McDbCustomEntity).getTypeName() === "McDbTestSingleDoor" || (entity as McDbCustomEntity).getTypeName() === "McDbTestStandardWindow") {
                    oldDoorSelectIds.push(id)
                }
            }
        })
    } else {
        setTimeout(() => {
            if (oldWallSelectIds.length > 0) {
                oldWallSelectIds.forEach(id => {
                    if (id.isErase()) {
                        const newWall = new MxdrawWalls();
                        newWall.updateWall(id, false);
                    }
                });
                mxcad.updateDisplay();
            }
            if (oldDoorSelectIds.length > 0) {
                oldDoorSelectIds.forEach(id => {
                    if (id.isErase()) {
                        const door = id.getMcDbEntity() as McDbTestSingleDoor | McDbTestStandardWindow;
                        let newWall: McDbTestWall | null = null;
                        const dataBase: McDbDatabase = MxCpp.getCurrentMxCAD().getDatabase();
                        if (door.wallHandle) {
                            newWall = dataBase.handleToIdIndex(door.wallHandle).getMcDbEntity() as McDbTestWall;
                            newWall.doorHandles = newWall.doorHandles.filter(handle => handle !== door.entityHandle);
                            newWall.assertObjectModification(true);
                        }
                    }
                });
                mxcad.updateDisplay();
            };
        }, 0)
    }
})
