<template>
  <div class="contents">
    <div class="static">
      <span class="static-title">{{ t("图框方向") }}</span>
      <v-radio-group v-model="formData.direct">
        <v-row justify="space-around">
          <v-col cols="6">
            <v-radio :label="t('横向图框')" :value="FrameDirect.Transverse" />
          </v-col>
          <v-col cols="6">
             <v-radio :label="t('纵向图框')" :value="FrameDirect.Portrait" />
          </v-col>
        </v-row>
      </v-radio-group>
    </div>
    <div class="static">
      <span class="static-title">{{ t("附加项") }}</span>
      <v-row>
        <v-col cols="6">
          <v-checkbox
            v-model="formData.isAddTitle"
            :label="t('加入标题栏')"
            hide-details
            density="compact"
          />
        </v-col>
        <v-col cols="6">
          <v-checkbox
            v-model="formData.isAddCos"
            :label="t('加入会签栏')"
            hide-details
            density="compact"
          />
        </v-col>
      </v-row>
    </div>
    <div class="static">
      <span class="static-title">{{ t("图框尺寸") }}</span>
      <v-row justify="space-around">
        <v-col cols="6" class="fc">
          <span>{{ t("标 准：") }}</span>
          <v-select
            :items="frameSizeList"
            v-model="frameSize"
            item-title="name"
            item-value="value"
            @update:model-value="modelChange"
            width="50%"
          >
          </v-select>
        </v-col>
        <v-col cols="6" class="fc">
          <v-checkbox
            v-model="isAutoSize"
            :label="t('自定义')"
            hide-details
            density="compact"
          />
        </v-col>
      </v-row>
      <v-row justify="space-around">
        <v-col cols="6" class="fc">
          <span>{{ t("长度：") }}</span>
          <v-text-field v-model="formData.frameSizeLength" type="number" width="50%" :readonly="!isAutoSize"/>
        </v-col>
        <v-col cols="6" class="fc">
          <span>{{ t("宽度：") }}</span>
          <v-text-field v-model="formData.frameSizeWidth" type="number" width="50%" :readonly="!isAutoSize"/>
        </v-col>
      </v-row>
    </div>
    <div class="static">
      <span class="static-title">{{ t("尺寸比例") }}</span>
      <v-row>
        <v-col cols="6" class="fc">
          <span>{{ t("比例：") }}</span>
          <v-select :items="frameScaleList" v-model="formData.scale">
          </v-select>
        </v-col>
        <v-col cols="6"/>
      </v-row>
    </div>
    <div class="footerBtn">
      <v-btn color="primary" @click="sureSet">{{ t("确定") }}</v-btn>
      <v-btn
        style="margin-left: 10px"
        @click="() => dialog.showDialog(false)"
        >{{ t("取消") }}</v-btn
      >
    </div>
  </div>
</template>

<script setup lang="ts">
import { t } from "../../../i18n";
import {
  VRadio,
  VRadioGroup,
  VSelect,
  VCheckbox,
  VRow,
  VCol,
  VTextField,
  VBtn
} from "vuetify/components";
import { FrameDirect, drawFrame } from "../../extoolsFun/InsertFrame";
import { reactive, ref } from "vue";
import { dialog } from "../dialog";

const isAutoSize = ref(false);
// 图框数据记录
const formData = reactive<{
  direct: FrameDirect;
  isAddTitle: Boolean;
  isAddCos: Boolean;
  frameSizeLength: number;
  frameSizeWidth: number;
  scale: string;
}>({
  direct: FrameDirect.Transverse,
  isAddTitle: true,
  isAddCos: true,
  frameSizeLength: 297,
  frameSizeWidth: 210,
  scale: "1:10",
});

// 图框尺寸
const frameSize = ref(4);
const frameSizeList = reactive([
  { name: "A1", length: 1189, width: 841, value: 1 },
  { name: "A2", length: 841, width: 594, value: 2 },
  { name: "A3", length: 420, width: 297, value: 3 },
  { name: "A4", length: 297, width: 210, value: 4 },
]);

// 图框尺寸更新
const modelChange = () => {
  const res = frameSizeList.find((item) => item.value === frameSize.value);
  if (res) {
    formData.frameSizeLength = res.length;
    formData.frameSizeWidth = res.width;
  }
};

// 图框比例
const frameScaleList = reactive([
  "1:1",
  "1:5",
  "1:10",
  "1:20",
  "1:25",
  "1:30",
  "1:50",
  "1:100",
]);

// 确定
const sureSet = () => {
  dialog.showDialog(false);
  drawFrame(formData);
};
</script>

<style scoped lang="scss">
.contents {
  padding: 5px;
  position: relative;
  .static {
    border: 1px solid rgb(204, 204, 204, 0.5);
    padding: 10px;
    position: relative;
    margin-top: 20px;
    .static-title {
      position: absolute;
      top: -10px;
      left: 10px;
      background-color: rgb(var(--v-theme-dialog-card-text));
    }
  }
  .footerBtn {
    display: flex;
    position: absolute;
    bottom: -35px;
    right: 10px;
  }
}
.fc{
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>