import { ref } from "vue";

   // 弹框要显示的数据
   type RevealData = { title: string };
   // 弹框确认要传入的数据
   type ConfirmData = { data: number };
   // 弹框取消要传入的数据
   type CancelData = { data: number };
   export const dialog = MxPluginContext.useDialogIsShow<
     RevealData,
     ConfirmData,
     CancelData
   >();

   export const isLoading = ref(false);