import { McDbCustomEntity, McGePoint3d, IMcDbDwgFiler, McGePoint3dArray, MxCADWorldDraw, McDb, MxCpp, McDbPolyline, McDbLine, McDbText, MxCADUiPrPoint, MxCADUiPrDist, McDbEntity, McDbCircle, McGeVector3d, McDbMText, MxCADUtility } from "mxcad";
import { MxFun } from "mxdraw";
import { t } from '../../i18n';



// 索引图名
export class McDbTestIndexImgName extends McDbCustomEntity {
    // 定义McDbTestIndexImgName内部的点对象 
    // 索引号半径
    private _indexRadius: number = MxFun.screenCoordLong2Doc(30);
    // 索引图号
    private _indexDrawNumber: string = "";
    // 索引编号
    private _indexNumber: string = "";
    // 文字高度
    private height: number = this._indexRadius / 2;
    // 索引位置
    private _indexPos: McGePoint3d = new McGePoint3d();
    // 比例位置
    private scalePos: McGePoint3d = new McGePoint3d();
    // 是否显示比例
    private _scale: boolean = false;
    // 比例值
    private _scaleValue: string = "";
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();
    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new McDbTestIndexImgName(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "McDbTestIndexImgName";
    }
    //设置或获取文本字高
    public set textHeight(val: number) {
        this.height = val;
    }
    public get textHeight(): number {
        return this.height;
    }
    //设置或获取文字索引号位置
    public set indexPos(pt: McGePoint3d) {
        this._indexPos = pt.clone();
        this.scalePos = new McGePoint3d(this._indexPos.x + this._indexRadius, this._indexPos.y - this._indexRadius);
    }
    public get indexPos(): McGePoint3d {
        return this._indexPos;
    }
    //设置或获取索引图标半径
    public set indexRadius(val: number) {
        this._indexRadius = val;
    }
    public get indexRadius(): number {
        return this._indexRadius;
    }
    //设置或获取索引图号
    public set indexDrawNumber(val: string) {
        this._indexDrawNumber = val;
    }
    public get indexDrawNumber(): string {
        return this._indexDrawNumber;
    }
    // 设置或获取索引编号
    public set indexNumber(val: string) {
        this._indexNumber = val;
    }
    public get indexNumber(): string {
        return this._indexNumber;
    }
    // 设置或获取比例值
    public set scaleValue(val: string) {
        this._scaleValue = val;
    }
    public get scaleValue(): string {
        return this._scaleValue;
    }
    // 设置或获取是否显示比例
    public set scale(val: boolean) {
        this._scale = val;
    }
    public get scale(): boolean {
        return this._scale;
    }
    // 读取自定义实体数据
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this._indexPos = filter.readPoint("indexPos").val;
        this.scalePos = filter.readPoint("scalePos").val;
        this.height = filter.readDouble("height").val;
        this._indexRadius = filter.readDouble("indexRadius").val;
        this._indexDrawNumber = filter.readString("indexDrawNumber").val;
        this._indexNumber = filter.readString("indexNumber").val;
        this._scaleValue = filter.readString("scaleValue").val;
        this.scale = filter.readLong("scale").val == 1 ? true : false;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        return true;
    }
    // 写入自定义实体数据
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("indexPos", this._indexPos);
        filter.writePoint("scalePos", this.scalePos);
        filter.writeDouble("height", this.height);
        filter.writeDouble("indexRadius", this._indexRadius);
        filter.writeString("indexDrawNumber", this._indexDrawNumber);
        filter.writeString("indexNumber", this._indexNumber);
        filter.writeString("scaleValue", this._scaleValue);
        filter.writeLong("scale", this._scale ? 1 : 0);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        if (iIndex === 0) {
            this._indexPos.x += dXOffset;
            this._indexPos.y += dYOffset;
            this._indexPos.z += dZOffset;
            if (this._scale) {
                this.scalePos.x += dXOffset;
                this.scalePos.y += dYOffset;
                this.scalePos.z += dZOffset;
            }
        } else if (iIndex === 1) {
            this.scalePos.x += dXOffset;
            this.scalePos.y += dYOffset;
            this.scalePos.z += dZOffset;
        }
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray();
        ret.append(this._indexPos);
        if (this._scale) ret.append(this.scalePos);
        return ret;
    };
    // 绘制实体 
    public worldDraw(draw: MxCADWorldDraw): void {
        const allEntityArr: McDbEntity[] = [];
        // 绘制图标
        this.drawIndexIcon().forEach(entity => {
            draw.drawEntity(entity)
            allEntityArr.push(entity)
        })
        // 绘制比例
        if (this._scale) {
            const text = new McDbText();
            text.height = this.height;
            text.textString = this._scaleValue;
            text.horizontalMode = McDb.TextHorzMode.kTextLeft;
            text.position = text.alignmentPoint = this.scalePos;
            draw.drawEntity(text);
            allEntityArr.push(text)
        }

        this.getBox(allEntityArr);
    }
    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbText) {
                const ent = new McDbMText()
                ent.contents = entity.textString;
                ent.textHeight = entity.height;
                ent.location = entity.position;
                switch (entity.horizontalMode) {
                    case McDb.TextHorzMode.kTextLeft:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                    case McDb.TextHorzMode.kTextCenter:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    case McDb.TextHorzMode.kTextRight:
                        ent.attachment = McDb.AttachmentPoint.kBottomRight;
                        break;
                    case McDb.TextHorzMode.kTextMid:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    default:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                }
                // 将ent的文字样式设置为当前文字样式
                const textStyleId = mxcad.getDatabase().getCurrentlyTextStyleId();
                ent.textStyleId = textStyleId;
                ent.reCompute();
                result = MxCADUtility.getTextEntityBox(ent, false);
            } else if (entity instanceof McDbMText) {
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }
    // 绘制索引图标
    private drawIndexIcon(): McDbEntity[] {
        const center = this._indexPos;
        const circle = new McDbCircle();
        circle.radius = this._indexRadius;
        circle.center = center;
        const line = new McDbLine(center.clone().addvec(McGeVector3d.kXAxis.clone().mult(this._indexRadius)), center.clone().subvec(McGeVector3d.kXAxis.clone().mult(this._indexRadius)));

        const text1 = new McDbText();
        text1.height = this.height * (5 / 4);
        text1.textString = this._indexNumber.toString();
        text1.horizontalMode = McDb.TextHorzMode.kTextCenter;
        text1.position = text1.alignmentPoint = new McGePoint3d(center.x, center.y + this.height / 4);
        const id1 = MxCpp.getCurrentMxCAD().drawEntity(text1);
        const { minPt, maxPt } = id1.getMcDbEntity().getBoundingBox();
        id1.erase();
        text1.widthFactor = this._indexRadius * (2 / 3) / (maxPt.x - minPt.x);

        const text2 = new McDbText();
        text2.height = this.height * (5 / 4);
        text2.textString = this._indexDrawNumber;
        text2.horizontalMode = McDb.TextHorzMode.kTextCenter;
        text2.position = text2.alignmentPoint = new McGePoint3d(center.x, center.y - this.height * (5 / 4));
        const id = MxCpp.getCurrentMxCAD().drawEntity(text2);
        const { minPt: _minPt, maxPt: _maxPt } = id.getMcDbEntity().getBoundingBox();
        id.erase();
        text2.widthFactor = this._indexRadius * (2 / 3) / (_maxPt.x - _minPt.x);
        return [circle, line, text1, text2]
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
};

export async function drawIndexName(indexDrawNumber: string, indexNumber: string, scale: boolean, scaleValue: string) {
    const indexName = new McDbTestIndexImgName();
    indexName.indexDrawNumber = indexDrawNumber;
    indexName.indexNumber = indexNumber;
    indexName.scale = scale;
    indexName.scaleValue = scaleValue;

    const getPoint1 = new MxCADUiPrPoint();
    getPoint1.setMessage(`\n${t('请指定索引图名的位置')}:`);
    const point1 = await getPoint1.go();
    if (!point1) return;
    indexName.indexPos = point1;
    const mxcad = MxCpp.getCurrentMxCAD();
    mxcad.drawEntity(indexName);
}