<template>
    <div v-show="isShowPatternTable">
        <table border="1">
            <thead>
                <tr>
                    <th>{{ t('序号') }}</th>
                    <th>{{ t('名称') }}</th>
                    <th>{{ t('数量') }}</th>
                    <th>{{ t('操作') }}</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in patternList" :key="item.name">
                    <td>{{ index + 1 }}</td>
                    <td>{{ item.name }}</td>
                    <td>{{ item?.graphBoxsPos?.length || 0 }}</td>
                    <td>
                        <div class="options">
                            <span @click="() => lookForDetail(index)">{{ t('详情') }}</span>
                            <span @click="() => deleteDetail(index)">{{ t('删除') }}</span>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
        <div v-if="!patternList.length" class="noDataBox" style="margin-bottom: 30px;">
            <span>{{ t('暂无数据') }}</span>
        </div>
        <div style="width: 100%;display: flex;justify-content: center;margin-top: 10px;" v-if="patternList.length">
            <img :src="isChange ? _imageUrl : imageUrl" alt="graph" style="width: 50%;" />
        </div>
        <table border="1">
            <thead>
                <tr>
                    <th>{{ t('序号') }}</th>
                    <th>{{ t('坐标') }}</th>
                    <th>{{ t('操作') }}</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in isChange ? _showList : showList" :key="index">
                    <td>{{ index + 1 }}</td>
                    <td>
                        <span>X:{{ item.x.toFixed(3) }}</span>
                        <span style="margin-left: 10px;">Y:{{ item.y.toFixed(3) }}</span>
                    </td>
                    <td>
                        <div class="options">
                            <span @click="() => lookForPos(item)">{{ t('查看') }}</span>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
        <div v-if="!patternList.length" class="noDataBox" style="margin-bottom: 30px;">
            <span>{{ t('暂无数据') }}</span>
        </div>
    </div>
</template>

<script lang="ts" setup>
import { MxFun } from "mxdraw";
import { patternList } from "./hooks/usePatternRec";
import { ref, reactive, computed, onUpdated } from "vue";
import { MxCpp } from "mxcad";
import { isShowPatternTable } from '../index';
import { t } from "../i18n";

const isChange = ref(false);
const showList = computed(() => {
    if (isChange.value) isChange.value = false;
    return patternList[patternList.length - 1]?.graphBoxsPos || []
});
const imageUrl = computed(() => {
    if (isChange.value) isChange.value = false;
    return patternList[patternList.length - 1]?.patternImgUrl || ''
});
const oldLength = ref(0);
onUpdated(() => {
    if (oldLength.value < patternList.length) {
        lookForDetail(patternList.length - 1);
    }
})
const lookForPos = (item) => {
    let mxdraw = MxFun.getCurrentDraw();
    mxdraw.getTempMarkDraw().clear();
    mxdraw.getTempMarkDraw().drawCircleMark(item.toVector3(), mxdraw.viewCoordLong2Cad(50), 0xFF0000);
    mxdraw.updateDisplay();
   
    MxCpp.getCurrentMxCAD().zoomCenter(item.x, item.y)
}
const _showList = reactive([]);
const _imageUrl = ref('');
const nowIndex = ref(null)
const lookForDetail = (index) => {
    _showList.length = 0; // 清空之前的数据
    _showList.push(...patternList[index].graphBoxsPos);
    _imageUrl.value = patternList[index].patternImgUrl;
    if (!isChange.value) isChange.value = true;
    oldLength.value = patternList.length;
    nowIndex.value = index;

}
const deleteDetail = (index) => {
    patternList.splice(index, 1);
    if (nowIndex.value === index && patternList.length > 0) {
        lookForDetail(patternList.length-1);
    };
    if(patternList.length === 0){
        isChange.value = false;
    }
}
</script>

<style lang="scss" scoped>
/* 可以添加一些样式 */
table {
    width: calc(100% - 20px);
    border-collapse: collapse;
    margin: 10px 10px 0px;
}

th,
td {
    border: 1px solid #ddd;
    padding: 4px;
    vertical-align: middle;
    line-height: 20px;
    height: 20px;
    text-align: center;
    font-size: var(--mx-font-size);
}

.noDataBox {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    width: calc(100% - 20px);
    margin: 0px 10px 10px;
    border: 1px solid #ddd;
    border-top: none
}

.options {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-around;

    span {
        text-decoration: underline;
        cursor: pointer;

        &:hover {
            color: #89cbfa;
        }
    }
}
</style>