<template>
  <div>
    <p style="margin:5px 0px">{{ t("输入两点之间实际尺寸来设置标注比例:") }}</p>
    <v-text-field v-model="distance" @change="changeDistance" type="number"/>
  </div>
</template>

<script setup lang="ts">
import { VTextField } from "vuetify/components";
import { t } from "../../../i18n";
import { ref, onMounted } from "vue";
import { dialogWidth } from '../dialog'

const props = defineProps({
  paramData: {
    type: Object,
    default: () => ({}),
  },
});

const distance = ref(0);
const emit = defineEmits(["receiveParameters"]);
const changeDistance = () => {
  emit("receiveParameters", {dimScale:distance.value/props.paramData.distance});
};
onMounted(() => {
  dialogWidth.value = 250;
  if(props.paramData.distance) {
    distance.value = props.paramData.distance;
  }
})
</script>

