<script setup lang="ts">
import { onMounted, reactive, ref } from "vue";
import ExtoolsDialog from "./components/ExtoolsDialog.vue";
import SetDimScaleDialog from "./components/SetDimScaleDialog.vue";
import MeasurementSet from "./components/MeasurementSet.vue";
import CategoryManage from "./components/CategoryManage.vue";
import MeasurementStatistics from "./components/MeasurementStatistics.vue";
import AnnotationSettingDialog from './components/AnnotationSettingDialog.vue';
import MatchPropertiesDialog from './components/MatchPropertiesDialog.vue';
import InsterFrameDialog from './components/InsterFrameDialog.vue';

import { dialog, dialogComponentName, dialogWidth, isShowDialog } from "./dialog";
import { t } from "../../i18n";
import { MxCpp } from "mxcad";

const { MxDialog } = MxPluginContext.components;
const { isShow, onReveal } = dialog;
const title = ref("弹框标题");
const paramData = ref({});
onReveal((data) => {
  // 接收到显示的消息
  if (data.title) title.value = data.title;
  if (data?.paramData) paramData.value = data.paramData;
  footerBtnList.length = 0;
  if (data?.isShowFooterBtn) {
    footerBtnList.push(
      {
        name: t("取消"),
        fun: () => {
          const mxcad = MxCpp.getCurrentMxCAD();
          dialog.cancel(dialogParams);
          isShow.value = false;
          mxcad.mxdraw.clearMxCurrentSelect();
          mxcad.updateDisplay();
        },
      },
      {
        name: t("确定"),
        primary: true,
        fun: () => {
          dialog.confirm(dialogParams);
          isShow.value = false;
        },
      }
    );
  }
});

const cpn = {
  ExtoolsDialog,
  SetDimScaleDialog,
  MeasurementSet,
  CategoryManage,
  MeasurementStatistics,
  AnnotationSettingDialog,
  MatchPropertiesDialog,
  InsterFrameDialog
};
const footerBtnList = reactive([
  {
    name: t("取消"),
    fun: () => {
      const mxcad = MxCpp.getCurrentMxCAD();
      dialog.cancel(dialogParams);
      isShow.value = false;
      mxcad.mxdraw.clearMxCurrentSelect();
      mxcad.updateDisplay();
    },
  },
  {
    name: t("确定"),
    primary: true,
    fun: () => {
      dialog.confirm(dialogParams);
      isShow.value = false;
    },
  },
]);

let dialogParams: any;
const receiveParameters = (params: any) => {
  dialogParams = params;
};
</script>
<template>
  <div v-show="isShowDialog">
    <MxDialog
      :title="title"
      v-model="isShow"
      :max-width="dialogWidth"
      :footerBtnList="footerBtnList"
    >
      <component
        :is="cpn[dialogComponentName]"
        @receiveParameters="receiveParameters"
        :paramData="paramData"
      ></component>
    </MxDialog>
  </div>
</template>