import { McCmColor, McDb, McDbDimension, McDbEntity, McDbHatch, McDbMText, McDbPolyline, McDbText, McGePoint3d, McObject, McObjectId } from 'mxcad';

// 特性匹配
import { MxCADSelectionSet, MxCADUiPrEntity, MxCADUiPrPoint, MxCADUtility, MxCpp } from "mxcad";
import { t } from "../i18n";
import { MrxDbgUiPrBaseReturn, MxFun } from "mxdraw";
import { baseProperties, specialProperties } from './index'
import { dialog, dialogComponentName } from './dialog/dialog';

export enum ColorNameType {
    "随块",
    "红色",
    "黄色",
    "绿色",
    "青色",
    "蓝色",
    "洋红色",
    "白色",
    "随层" = 256,
}

export async function Mx_MatchProperties() {
            
    const getOriginEnt = new MxCADUiPrEntity();
    getOriginEnt.setMessage(t('请选择源对象'));
    const originEntId = await getOriginEnt.go();
    if (!originEntId.isValid()) return;
    const originEnt = originEntId.getMcDbEntity();
    originEnt.highlight(true);

    // 获取源对象特性
    baseProperties.forEach(item => {
        if (item.isChecked && !item.isReadOnly && item.value) {
            if (item.value == 'trueColor') {
                // 颜色索引
                item.entProperty = originEnt.colorIndex > 8 && originEnt.colorIndex < 256 ? originEnt.colorIndex : ColorNameType[originEnt.colorIndex];
                const color = new McCmColor();
                color.colorIndex = originEnt.colorIndex;
                color.setColorIndex(originEnt.colorIndex);
                item.color = `rgb(${color.red},${color.green},${color.blue})`;
            } else if (item.value === 'n') {
                item.entProperty = originEnt.trueColor.n;
            } else if (item.value === 'lineweight') {
                switch (originEnt.lineweight) {
                    case McDb.LineWeight.kLnWtByLayer: item.entProperty = t('随层'); break;
                    case McDb.LineWeight.kLnWtByBlock: item.entProperty = t('随块'); break;
                    case McDb.LineWeight.kLnWtByLwDefault: item.entProperty = t('缺省'); break;
                    default: item.entProperty = originEnt.lineweight; break;
                }
            } else if (item.value === 'linetypeScale') {
                item.entProperty = originEnt.linetypeScale.toFixed(3);
            } else {
                item.entProperty = originEnt[item.value];
            }
        }
    });

    let activeSetting = '';
    [baseProperties, specialProperties].forEach(item => {
        activeSetting += item.filter(i => i.isChecked).map(str => str.name.replace(/\s*\([^)]*\)/g, '')).join(' ')
        activeSetting += ' '
    })
    MxFun.acutPrintf(t('当前激活的设置：') + activeSetting);

    while (true) {
        const getEntityPt = new MxCADUiPrPoint();
        getEntityPt.setMessage(t('选择目标对象'));
        getEntityPt.setKeyWords("[设置(S)]");
        getEntityPt.setDisableOsnap(true);
        getEntityPt.setDisableDynInput(true);
        getEntityPt.disableAllTrace(true);

        const pt = await getEntityPt.go();
        if (getEntityPt.getStatus() == MrxDbgUiPrBaseReturn.kKeyWord) {
            if (getEntityPt.isKeyWordPicked('S')) {
                dialogComponentName.value = 'MatchPropertiesDialog';
                dialog.showDialog(true, { title: t('特性设置'), isShowFooterBtn: false });
            };
        } else if (getEntityPt.getStatus() == MrxDbgUiPrBaseReturn.kCancel || getEntityPt.getStatus() == MrxDbgUiPrBaseReturn.kNone) {
            break;
        }
        if (pt) {
            const entId = MxCADUtility.findEntAtPoint(pt.x, pt.y, pt.z);
            if (entId.isValid()) {
                brushFormat(entId, originEnt)
            } else {
                getEntityPt.setUserDraw((point, pw) => {
                    const pts = [pt, new McGePoint3d(pt.x, point.y), point, new McGePoint3d(point.x, pt.y)]
                    // 设置范围框颜色即位置
                    let pl = new McDbPolyline();
                    pl.isClosed = true;
                    pts.forEach(pt => pl.addVertexAt(pt));
                    pw.setColor(0xFFFFFF);
                    pw.drawMcDbEntity(pl);

                    // 动态绘制矩形填充框
                    const geometry = new THREE.BufferGeometry();
                    geometry.setFromPoints([
                        new THREE.Vector3(pt.x, pt.y, pt.z),
                        new THREE.Vector3(pt.x, point.y, point.z),
                        new THREE.Vector3(point.x, point.y, point.z),
                        new THREE.Vector3(point.x, pt.y, pt.z)
                    ]);
                    geometry.attributes.uv = new THREE.BufferAttribute(new Float32Array([ 0, 0,1, 0, 1, 1,0, 1]), 2);
                    geometry.setIndex([0, 1, 2, 0, 2, 3]);
                    // 创建材质（半透明的颜色）
                    const material = new THREE.MeshBasicMaterial({
                        color: 0x004D00,
                        transparent: true,
                        opacity: 0.5,
                        side: THREE.DoubleSide
                    });
                    const mesh = new THREE.Mesh(geometry, material);
                    pw.drawEntity(mesh);
                });
                const nextPt = await getEntityPt.go();
                if (!nextPt) break;
                const ss = new MxCADSelectionSet();
                await ss.crossingSelect(pt.x, pt.y, nextPt.x, nextPt.y);
                ss.forEach(id => {
                    brushFormat(id, originEnt)
                });
            }
        }
    };
    originEnt.highlight(false);
    MxCpp.getCurrentMxCAD().updateDisplay();
}

const brushFormat = (entId: McObjectId, originEnt: McDbEntity) => {
    const ent = entId.getMcDbEntity();
    // 基础特性
    baseProperties.forEach(item => {
        if (item.isChecked && !item.isReadOnly) {
            if (item.value === 'trueColor') {
                ent.trueColor = originEnt.trueColor;
                ent.colorIndex = originEnt.colorIndex;
            } else if (item.value === "n") {
                ent.trueColor.n = originEnt.trueColor.n;
            } else {
                ent[item.value] = originEnt[item.value]
            }
        }
    })

    // 特殊特性
    specialProperties.forEach(item => {
        if (item.isChecked && !item.isReadOnly) {
            if (item.value === 'DIM' && ent instanceof McDbDimension && originEnt instanceof McDbDimension) {
                // 标注=》直接修改标注样式
                ent.dimensionStyle = originEnt.dimensionStyle;
                ent.elevation = originEnt.elevation;
                ent.textRotation = originEnt.textRotation;
                ent.textAttachment = originEnt.textAttachment;
                ent.horizontalRotation = originEnt.horizontalRotation;
            } else if (item.value === 'TEXT' && (ent instanceof McDbMText || ent instanceof McDbText) && (originEnt instanceof McDbMText || originEnt instanceof McDbText)) {
                // 文字
                ent.textStyleId = originEnt.textStyleId;
                if (ent instanceof McDbText && originEnt instanceof McDbText) {
                    ent.oblique = originEnt.oblique;
                    ent.rotation = originEnt.rotation;
                    ent.height = originEnt.height;
                    ent.widthFactor = originEnt.widthFactor;
                    ent.horizontalMode = originEnt.horizontalMode;
                    ent.verticalMode = originEnt.verticalMode;
                }
                if (ent instanceof McDbMText && originEnt instanceof McDbMText) {
                    ent.rotation = originEnt.rotation;
                    ent.width = originEnt.width;
                    ent.textHeight = originEnt.textHeight;
                    ent.attachment = originEnt.attachment;
                }
            } else if (item.value === 'PL' && ent instanceof McDbPolyline && originEnt instanceof McDbPolyline) {
                // 多段线
                ent.isClosed = originEnt.isClosed;
                ent.constantWidth = originEnt.constantWidth;
            } else if (item.value === 'HATCH' && ent instanceof McDbHatch && originEnt instanceof McDbHatch) {
                // 填充
                ent.clearPatternDefinition();
                const num = originEnt.numPatternDefinitions;
                if (num) {
                    for (let i = 0; i < num; i++) {
                        const res = originEnt.getPatternDefinitionAt(i);
                        ent.addPatternDefinition(res.angle, res.baseX, res.baseY, res.offsetX, res.offsetY, res.aryDashes)
                    }
                }
                ent.setPattern(originEnt.patternType(), originEnt.patternName());
                ent.patternAngle = originEnt.patternAngle;
                ent.patternScale = originEnt.patternScale;
                ent.patternSpace = originEnt.patternSpace;
                ent.patternDouble = originEnt.patternDouble;
                ent.setHatchStyle(originEnt.hatchStyle());
            }
        }
    })
}