<template>
  <div class="body" v-show="isOpen">
    <div class="dialog" @mousedown="dragModal" ref="dialog">
      <div class="header">
        <div style="display: flex;align-items: center;">
          <img :src="utils.getLogoImgSrc()" alt="">
          <span>{{ annotatName.name }}</span>
        </div>
        <a href="#!" @click="hideModal" role="button" class="close" aria-label="close this modal">
          <svg aria-hidden="true" fill="rgb(var(--v-theme-on-dialog-card-text))">
            <use xlink:href="#icon-cha"></use>
          </svg>
        </a>
      </div>
      <component :is="cpn[annotatName.component]"></component>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from "vue";
import ArrowCitation from '../SymbolAnnotation/ArrowCitation/index.vue';
import ExtractionAnnotation from "../SymbolAnnotation/ExtractionAnnotation/index.vue";
import PracticeLabeling from '../SymbolAnnotation/PracticeLabeling/index.vue';
import IndexSymbol from '../SymbolAnnotation/IndexSymbol/index.vue';
import IndexName from "../SymbolAnnotation/IndexImgName/index.vue";
import SliceSymbol from "../SymbolAnnotation/SliceSymbol/index.vue";
import ElevationMarking from "../SymbolAnnotation/ElevationMarking/index.vue";
import MapTitleMarking from '../SymbolAnnotation/MapTitleMarking/index.vue';
import PlotAxis from "../HomeDecoration/PlotAxis/index.vue";
import { isOpen, annotatName } from "../index";
const { utils } = MxPluginContext;
const cpn = { ArrowCitation, ExtractionAnnotation, PracticeLabeling, IndexSymbol, 
              IndexName, SliceSymbol, ElevationMarking, MapTitleMarking, PlotAxis };
// 关闭弹窗
const hideModal = () => {
  isOpen.value = false;
}
// 移动设置窗口
const dialog = ref(null);
const dragModal = (event) => {
  if (!(event.target?.className === "header")) return;
  const dragBox = dialog.value;
  const disX = event.clientX - dragBox.offsetLeft;
  const disY = event.clientY - dragBox.offsetTop;

  const moveDrag = (event) => {
    dragBox.style.left = event.clientX - disX + "px";
    dragBox.style.top = event.clientY - disY + "px";
  };

  document.addEventListener("mousemove", moveDrag);
  document.addEventListener("mouseup", () => {
    document.removeEventListener("mousemove", moveDrag);
  });
};
</script>

<style scoped lang="scss">

.body {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}

.dialog {
  border: 8px;
  box-shadow: -20px 20px 35px 1px rgba(10, 49, 86, 0.18);
  position: absolute;
  top: calc(50% - 212px);
  left: calc(50% - 165px);
  color: rgb(var(--v-theme-on-dialog-card)) !important;
  background-color: rgb(var(--v-theme-dialog-card)) !important;
  --v-theme-overlay-multiplier: var(--v-theme-dialog-card-overlay-multiplier);
  overflow: auto;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: .85rem;
    padding: 4px 0px;

    img {
      width: 24px;
      height: 24px;
      margin-left: 4px;
      margin-right: 8px;
    }

    .close {
      background: none;
      border: none;
      cursor: pointer;
      display: flex;
      width: 16px;
      text-decoration: none;
      height: 16px;
      margin-right: 8px;
    }
  }
}
</style>