export enum ShowType {
  ActualValue,//实际数值
  SquareMeter//图纸单位为mm,面积数值按照m平方显示
}
// 枚举标注类型
export enum DimType {
    Area = "面积",
    Length = "长度",
    Coordinate = "坐标",
    Angle = "角度",
    Radius = "半径"
}
export interface DimSettingData {
  type: DimType,
  fixedNum: number,
  showType: ShowType
}
// 标注比例
export const dimScale = ref<number>(1);
// 标注文字高度
export const dimTextHeight = ref<number>(0)
// 标注设置对象
export const dimSettingForm = reactive<DimSettingData[]>([
  { type: DimType.Length, fixedNum: -1, showType: ShowType.ActualValue },
  { type: DimType.Area, fixedNum: -1, showType: ShowType.ActualValue },
  { type: DimType.Coordinate, fixedNum: -1, showType: ShowType.ActualValue },
])
// 当前标注颜色
