// 通过继承mxcad里的自定义实体类定义一个cad标注抽象基类
export abstract class MxMeasureEntity extends McDbCustomEntity {
  abstract create(imp?: any): McDbCustomEntity;
  abstract getTypeName(): string;
  abstract dwgInFields(filter: IMcDbDwgFiler): boolean;
  abstract dwgOutFields(filter: IMcDbDwgFiler): boolean;
  abstract worldDraw(draw: MxCADWorldDraw): void;
  abstract moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number): void;
  abstract getGripPoints(): McGePoint3dArray;
  abstract dimTextHeight: number;
  abstract dimType: DimType;
  // 分类名
  abstract categoryName: string;
  // 标注设置
  abstract setDimSettingForm(param:DimSettingData[]);
  abstract getDimSettingForm():DimSettingData[];
}
// 绘制测量标注
export function drawMeasurementAnnotation(entity: MxMeasureEntity): McObjectId {
  const currentCategory = measurementCategory.value.find(item => item.current);
  const mxcad = MxCpp.getCurrentMxCAD();
  mxcad.showLineWeight(true);
  // 获取原本图层
  const layerName = mxcad.getDatabase().getCurrentlyLayerName();
  const layerTable = mxcad.getDatabase().getLayerTable();
  if (!layerTable.has("Mx_Dim_Layer")) {
    mxcad.addLayer("Mx_Dim_Layer");
  }
  mxcad.drawLayer = "Mx_Dim_Layer";
  // 实体的绘制顺序永远在最高层
  let minmaxOrder = MxCpp.getCurrentDatabase().currentSpace.getMinMaxDrawOrder();
  // 把对象放到最上面。
  entity.drawOrder = minmaxOrder.maxDrawOrder + 1;
  entity.trueColor = currentCategory ? new McCmColor(...hexToRgb(currentCategory.color)) : new McCmColor(...hexToRgb(defaultColor.value));
  entity.categoryName = currentCategory ? currentCategory.name : "未分类";
  const id = mxcad.drawEntity(entity);
  mxcad.drawLayer = layerName;
  return id
}

// 标注分类管理对象
export const measurementCategory = ref<{
  id: number;
  name: string;
  color: string;
  visible: boolean;
  current: boolean;
}[]>([]);

// 将hex颜色转换为rgb颜色
export function hexToRgb(hex: string): [number, number, number] {
  // 去掉可能存在的 #
  hex = hex.replace(/^#/, "");

  // 如果是3位的简写形式，扩展为6位
  if (hex.length === 3) {
    hex = hex
      .split("")
      .map((char) => char + char)
      .join("");
  }

  const bigint = parseInt(hex, 16);
  const r = (bigint >> 16) & 255;
  const g = (bigint >> 8) & 255;
  const b = bigint & 255;

  return [r, g, b];
}

// 将rgb颜色转换为hex颜色
export function rgbToHex(r: number, g: number, b: number): string {
  // 确保数值在合法范围内
  [r, g, b] = [r, g, b].map(n => Math.max(0, Math.min(255, n)));

  // 转换为十六进制，并补零保证两位数
  const toHex = (n) => {
    const hex = n.toString(16);
    return hex.length === 1 ? '0' + hex : hex;
  };

  return '#' + toHex(r) + toHex(g) + toHex(b);
}

// 获取图纸测量分类
function MxTest_DictionaryData(dict: McDbDictionary): any[] {
  let id = dict.getAt("MyListRecord");
  if (!id.isValid()) return [];
  let obj = id.getMcDbObject();
  if (!obj || !(obj instanceof McDbXrecord)) return [];
  let xrec: McDbXrecord = obj as McDbXrecord;
  let data = [];
  xrec.getData().forEach((val, type, dxf) => {
    data = JSON.parse(val);
  });
  return data
}
export const getMeasurementCategory = (): any[] => {
  let mxcad = MxCpp.getCurrentMxCAD();
  let dict = mxcad.getDatabase().getNamedObjectsDictionary();
  const id = dict.getAt("MyCategoryList");
  if (!id.isValid()) return [];
  let obj = id.getMcDbObject();
  if (obj instanceof McDbDictionary) {
    let dict: McDbDictionary = obj;
    const data = MxTest_DictionaryData(dict);
    return data
  } else {
    return []
  }
};

// 读取图纸全局字典
export function readDictionaryData() {
  const data = getMeasurementCategory();
  if (data && data.length) {
    const r = data.find(item => item.current);
    currentAnnotationColor.value = r ? r.color : defaultColor.value;
    measurementCategory.value = data;
  } else {
    currentAnnotationColor.value = defaultColor.value;
  }
}
