import { MxCpp } from "mxcad";

export function Mx_PlanView() {
    const { useLoading } = MxPluginContext;
    const { showLoading, hideLoading } = useLoading(); // loading图标
    const mxcad = MxCpp.getCurrentMxCAD();
    const url = new URL("../../assets/户型图.mxweb", import.meta.url).href
    showLoading()
    mxcad.openWebFile(url, () => {
        mxcad.zoomAll();
        hideLoading();
    });

}