
<template>
  <div v-show="isShowDialog">
    <MxDialog
      :isShowCloseBtn="false"
      :title="title"
      v-model="isShow"
      :width="dialogWidth"
      :footerBtnList="footerBtnList"
    >
      <div v-show="isShowManageContent" class="contents">
        <div class="table-container">
          <table style="width: 100%; overflow: auto">
            <thead>
              <tr>
                <th
                  v-for="header in headers"
                  :key="header.text"
                  :style="{ width: header.width }"
                >
                  {{ header.text }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="group in filteredGroupArr"
                :key="group.name"
                :class="{
                  'current-row': group.id === currentGroup.id,
                }"
                @click="setCurrentGroup(group)"
              >
                <td>
                  <span>{{ group.name }}</span>
                </td>
                <td>
                  <span>{{ group.group.isSelectable ? "是" : "否" }}</span>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="static">
          <span class="static-title">{{ t("编组标识") }}</span>
          <v-row>
            <v-col cols="4">
              <span>{{ t("编组名:") }}</span>
            </v-col>
            <v-col cols="8">
              <v-text-field
                @focus="() => (isShowTips = false)"
                v-model="currentGroup.name"
                :disabled="formData.isUnnamed"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="4">
              <span>{{ t("说明:") }}</span>
            </v-col>
            <v-col cols="8">
              <v-text-field
                v-model="currentGroup.description"
                @focus="() => (isShowTips = false)"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="4">
              <v-btn @click="searchName">{{ t("查找名称") }}</v-btn>
            </v-col>
            <v-col cols="4">
              <v-btn :disabled="!currentGroup.group" @click="highLight">{{
                t("亮显")
              }}</v-btn>
            </v-col>
            <v-col cols="4">
              <v-checkbox
                @update:model-value="
                  () => {
                    isShowTips = false;
                  }
                "
                v-model="formData.isIncludeUnnamed"
                :label="t('包含未命名的')"
                hide-details
                density="compact"
              />
            </v-col>
          </v-row>
        </div>
        <div class="static">
          <span class="static-title">{{ t("创建编组") }}</span>
          <v-row>
            <v-col cols="4">
              <v-btn @click="createGroup">{{ t("新建") }}</v-btn>
            </v-col>
            <v-col cols="4">
              <v-checkbox
                @update:model-value="
                  () => {
                    isShowTips = false;
                  }
                "
                v-model="formData.isOptional"
                :label="t('可选择的')"
                hide-details
                density="compact"
              />
            </v-col>
            <v-col cols="4">
              <v-checkbox
                @update:model-value="
                  () => {
                    isShowTips = false;
                  }
                "
                v-model="formData.isUnnamed"
                :label="t('未命名的')"
                hide-details
                density="compact"
              />
            </v-col>
          </v-row>
        </div>
        <div class="static">
          <span class="static-title">{{ t("修改编组") }}</span>
          <div
            style="
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin-bottom: 10px;
            "
          >
            <v-btn :disabled="!currentGroup.group" @click="delectGroup">{{
              t("删除")
            }}</v-btn>
            <v-btn :disabled="!currentGroup.group" @click="addGroup">{{
              t("添加")
            }}</v-btn>
            <v-btn :disabled="!currentGroup.group" @click="renameGroup">{{
              t("重命名")
            }}</v-btn>
            <v-btn @click="rearrangementGroup">{{ t("重排") }}</v-btn>
          </div>
          <v-row>
            <v-col cols="4">
              <v-btn :disabled="!currentGroup.group" @click="setNewDes">{{
                t("说明")
              }}</v-btn>
            </v-col>
            <v-col cols="4">
              <v-btn
                :disabled="!currentGroup.group"
                @click="decompositionGroup"
                >{{ t("分解") }}</v-btn
              >
            </v-col>
            <v-col cols="4">
              <v-btn
                :disabled="!currentGroup.group"
                @click="
                  () => {
                    if (currentGroup.group) {
                      currentGroup.group.isSelectable =
                        !currentGroup.group.isSelectable;
                    }
                  }
                "
                >{{ t("可选择的") }}</v-btn
              >
            </v-col>
          </v-row>
        </div>
        <div class="tips" v-show="isShowTips">
          <p :style="tips.includes('!') ? '' : 'color:red'">{{ tips }}</p>
        </div>
      </div>
      <div v-show="isShowGroupList" class="table-container">
        <table style="width: 100%">
          <tbody>
            <tr v-for="group in groupList" :key="group.name">
              <td>
                <span>{{ group.name }}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-show="isShowSort" class="contents">
        <div class="table-container" style="max-height: 100px; overflow: auto">
          <table style="width: 100%">
            <thead>
              <tr>
                <th
                  v-for="header in headers"
                  :key="header.text"
                  :style="{ width: header.width }"
                >
                  {{ header.text }}
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="group in filteredGroupArr"
                :key="group.name"
                :class="{
                  'current-row': group.id === currentGroup.id,
                }"
                @click="setCurrentGroup(group)"
              >
                <td>
                  <span>{{ group.name }}</span>
                </td>
                <td>
                  <span>{{ group.group.isSelectable ? "是" : "否" }}</span>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="static">
          <span class="static-title">{{ t("说明") }}</span>
          <p>{{ currentGroup.description || "无" }}</p>
        </div>
        <div class="static">
          <v-row>
            <v-col cols="8">
              <span>{{
                t("删除位置:") +
                `(0-${currentGroup?.group?.getAllEntityId()?.length - 1 || -1})`
              }}</span>
            </v-col>
            <v-col cols="4">
              <v-text-field
                v-model="sortData.delectIndex"
                type="number"
                :disabled="!currentGroup.group"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="8">
              <span>{{
                t("输入对象的新位置编号:") +
                `0-${currentGroup?.group?.getAllEntityId()?.length - 1 || -1}`
              }}</span>
            </v-col>
            <v-col cols="4">
              <v-text-field
                v-model="sortData.newIndex"
                type="number"
                :disabled="!currentGroup.group"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="8">
              <span>{{
                t("对象数目:") +
                `1-${currentGroup?.group?.getAllEntityId()?.length || -1}`
              }}</span>
            </v-col>
            <v-col cols="4">
              <v-text-field
                v-model="sortData.objNum"
                type="number"
                :disabled="!currentGroup.group"
              />
            </v-col>
          </v-row>
        </div>
        <v-row justify="center" style="margin-top: 10px">
          <v-col cols="4">
            <div class="d-flex justify-center align-center">
              <v-btn :disabled="!currentGroup.group" @click="doReorder">{{
                t("重排序")
              }}</v-btn>
            </div>
          </v-col>
          <v-col cols="4">
            <div class="d-flex justify-center align-center">
              <v-btn :disabled="!currentGroup.group" @click="doShining">{{
                t("亮显")
              }}</v-btn>
            </div>
          </v-col>
          <v-col cols="4">
            <div class="d-flex justify-center align-center">
              <v-btn :disabled="!currentGroup.group" @click="doReverseOrder">{{
                t("逆序")
              }}</v-btn>
            </div>
          </v-col>
        </v-row>
        <div class="tips" v-show="isShowTips">
          <p>{{ tips }}</p>
        </div>
      </div>
      <div v-show="isShowShining" class="contents">
        <v-row style="margin-top: 10px">
          <v-col cols="6">
            <div class="d-flex justify-center align-center">
              <v-btn
                :disabled="
                  currentIndex ===
                  currentGroup?.group?.getAllEntityId()?.length - 1
                "
                @click="changeShowEnt(1)"
                >{{ t("下一组") }}</v-btn
              >
            </div>
          </v-col>
          <v-col cols="6">
            <div class="d-flex justify-center align-center">
              <v-btn
                :disabled="currentIndex === 0"
                @click="changeShowEnt(-1)"
                >{{ t("上一组") }}</v-btn
              >
            </div>
          </v-col>
        </v-row>
        <div class="tips" v-show="isShowTips">
          <p>{{ tips }}</p>
        </div>
      </div>
    </MxDialog>
  </div>
</template>

   <script setup lang="ts">
import { t } from "../../i18n";
import {
  computed,
  onMounted,
  onUnmounted,
  onUpdated,
  reactive,
  ref,
} from "vue";
import { VBtn, VCol, VRow, VCheckbox, VTextField } from "vuetify/components";
import { dialog, isLoading } from "./dialog";
import { getGroupForEntity } from "../hooks";
import {
  MxCpp,
  McDbGroup,
  MxCADUiPrEntity,
  MxCADUtility,
  McDbEntity,
  MxCADUiPrPoint,
  McObject,
  McObjectId,
  McDbPolyline,
  MxCADSelectionSet,
  McGePoint3d,
} from "mxcad";
import { MrxDbgUiPrBaseReturn, MxFun } from "mxdraw";
interface GroupObject {
  id: number;
  name: string;
  group: McDbGroup;
  handle?: string;
}
const { MxDialog } = MxPluginContext.components;
const manageFooterBtn = [
  {
    name: "取消",
    fun: () => {
      isLoading.value = false;
    },
  },
  {
    name: "确定",
    fun: () => {
      isLoading.value = false;
      isSaveGroups.value = true;
    },
    primary: true,
  },
];
const footerBtnList: any = reactive([]);
const { isShow, onReveal } = dialog;
const title = ref("测试标题");
const isShowDialog = ref(true);
const isShowManageContent = ref(true);
const dialogWidth = ref<number>(350);
const tips = ref("");
const isShowTips = ref(false);
const isShowSort = ref(false);
const isSaveGroups = ref(false);
const delectGroupArr: GroupObject[] = [];

onReveal((data) => {
  // 接收到显示的消息
  if (data.title) title.value = data.title;
});

const headers = [
  { text: t("编组名"), width: "40%" },
  { text: t("可选择的"), width: "60%" },
];

const groupArr: GroupObject[] = reactive([]);
const currentGroup = reactive<{
  id: number;
  name: string;
  group: null | McDbGroup;
  description: string;
}>({ id: 0, name: "", group: null, description: "" });
const sortData = reactive<{
  delectIndex: number;
  newIndex: number;
  objNum: number;
}>({ delectIndex: 0, newIndex: 0, objNum: 0 });
onMounted(() => {
  footerBtnList.length = 0;
  footerBtnList.push(...manageFooterBtn);
  groupArr.length = 0;
  const database = MxCpp.getCurrentDatabase();
  const groupDict = database.GetGroupDictionary();
  const groupIds = groupDict.getAllObject();
  groupIds.forEach((groupId) => {
    const group = groupId.getMcDbObject() as McDbGroup;
    if (group) {
      groupArr.push({
        id: groupArr.length + 1,
        name: group.name,
        group: group.clone() as McDbGroup,
        handle: group.getHandle(),
      });
    }
  });
});

const filteredGroupArr = computed(() => {
  if (!formData.isIncludeUnnamed) {
    return groupArr.filter((cat) => !/^\*/.test(cat.name));
  } else {
    return groupArr;
  }
});

const formData = reactive({
  isIncludeUnnamed: false,
  isOptional: true,
  isUnnamed: false,
});

const isShowGroupList = ref(false);
const groupList: GroupObject[] = reactive([]);
// 查找组
const searchName = async () => {
  isShowTips.value = false;
  isShowDialog.value = false;
  isShowManageContent.value = false;
  let oldEnt: McDbEntity | null = null;
  while (true) {
    const getEnt = new MxCADUiPrEntity();
    getEnt.setMessage(t("拾取编组的成员"));
    getEnt.setUserDraw((pt, pw) => {
      if (oldEnt) oldEnt.highlight(false);
      const entId = MxCADUtility.findEntAtPoint(pt.x, pt.y, pt.z, -1);
      if (entId.isValid()) {
        const ent = entId.getMcDbEntity();
        ent?.highlight(true);
        oldEnt = ent;
      }
    });
    const entId = await getEnt.go();
    if (!entId.isValid()) {
      isShowDialog.value = true;
      isShowManageContent.value = true;
      tips.value = t("未选择任何对象");
      isShowTips.value = true;
      return;
    }
    const ent = entId.getMcDbEntity();
    if (ent) {
      const groupArr = getGroupForEntity(ent);
      if (groupArr.length) {
        isShowGroupList.value = true;
        groupList.length = 0;
        groupArr.forEach((item, index) => {
          groupList.push({ name: item.name, id: index + 1, group: item.group });
        });
        break;
      } else {
        MxFun.acutPrintf(t("不是编组成员"));
        continue;
      }
    }
  }
  if (oldEnt) (oldEnt as McDbEntity).highlight(false);
  isShowDialog.value = true;
  dialogWidth.value = 200;
  footerBtnList.length = 0;
  title.value = t("编组成员列表");
  footerBtnList.push({
    name: t("确定"),
    fun: () => {
      isShowManageContent.value = true;
      isShowGroupList.value = false;
      dialogWidth.value = 350;
      footerBtnList.length = 0;
      title.value = t("对象编组");
      footerBtnList.push(...manageFooterBtn);
    },
  });
};
// 亮显
const highLight = () => {
  isShowTips.value = false;
  const highLightEnts: McDbEntity[] = [];
  let _minPt: any, _maxPt: any;
  if (!currentGroup.group) return;
  currentGroup.group.getAllEntityId().forEach((id) => {
    const ent = id.getMcDbEntity();
    if (ent) {
      const { minPt, maxPt } = ent.getBoundingBox();
      if (!_minPt) _minPt = minPt.clone();
      if (!_maxPt) _maxPt = maxPt.clone();
      if (minPt.x < _minPt.x) _minPt.x = minPt.x;
      if (minPt.y < _minPt.y) _minPt.y = minPt.y;
      if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
      if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
      highLightEnts.push(ent);
      ent.highlight(true);
    }
  });
  MxCpp.getCurrentMxCAD().updateDisplay();
  if (!_minPt || !_maxPt) return;
  const midPt = _minPt.clone().addvec(_maxPt.sub(_minPt).mult(1 / 2));
  MxCpp.getCurrentMxCAD().zoomCenter(midPt.x, midPt.y);

  isShowManageContent.value = false;
  dialogWidth.value = 150;

  footerBtnList.length = 0;
  footerBtnList.push({
    name: "继续",
    fun: () => {
      footerBtnList.length = 0;
      footerBtnList.push(...manageFooterBtn);
      dialogWidth.value = 350;
      isShowManageContent.value = true;
      highLightEnts.forEach((ent) => ent.highlight(false));
      MxCpp.getCurrentMxCAD().updateDisplay();
    },
  });
};

const getGroupMidPt = (group: McDbGroup): McGePoint3d => {
  let _minPt: any, _maxPt: any;
  group.getAllEntityId().forEach((id) => {
    const ent = id.getMcDbEntity();
    if (ent) {
      const { minPt, maxPt } = ent.getBoundingBox();
      if (!_minPt) _minPt = minPt.clone();
      if (!_maxPt) _maxPt = maxPt.clone();
      if (minPt.x < _minPt.x) _minPt.x = minPt.x;
      if (minPt.y < _minPt.y) _minPt.y = minPt.y;
      if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
      if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
    }
  });
  const midPt = _minPt.clone().addvec(_maxPt.sub(_minPt).mult(1 / 2));
  return midPt;
};
const setCurrentGroup = (group: GroupObject) => {
  currentGroup.id = group.id;
  currentGroup.group = group.group;
  currentGroup.name = group.name;
  currentGroup.description = group.group.description;
};
type Operation = (id: McObjectId) => boolean;
const getIds = async (
  isHighLight: Boolean = true,
  fun: Operation = (id: McObjectId) => true,
  isExpendEnt: Boolean = false
): Promise<McObjectId[]> => {
  let ids: McObjectId[] = [];
  while (true) {
    const getEntityPt = new MxCADUiPrPoint();
    getEntityPt.setMessage(t("选择对象"));
    getEntityPt.setDisableOsnap(true);
    getEntityPt.setDisableDynInput(true);
    getEntityPt.disableAllTrace(true);

    const hoverSelectEnts: McDbEntity[] = [];
    getEntityPt.setUserDraw((pt, pw) => {
      if (hoverSelectEnts.length)
        hoverSelectEnts.forEach((ent) => {
          if (isExpendEnt) {
            const res = currentGroup.group
              ?.getAllEntityId()
              .filter((i) => i.id === ent.getObjectID().id);
            if (!res?.length) ent.highlight(false);
          } else {
            ent.highlight(false);
          }
        });
      hoverSelectEnts.length = 0;
      const entId = MxCADUtility.findEntAtPoint(pt.x, pt.y, pt.z, -1);
      if (entId.isValid() && !ids.map((item) => item.id).includes(entId.id)) {
        // 排除当前亮显实体
        const ent = entId.getMcDbEntity() as McDbEntity;
        if (isExpendEnt) {
          ent.highlight(true);
          hoverSelectEnts.push(ent);
        } else {
          const arr = getGroupForEntity(ent);
          if (arr.length) {
            const group = arr[0].group;
            group.getAllEntityId().forEach((id) => {
              const ent = id.getMcDbEntity();
              (ent as McDbEntity).highlight(true);
              hoverSelectEnts.push(ent as McDbEntity);
            });
          } else {
            ent.highlight(true);
            hoverSelectEnts.push(ent);
          }
        }
      }
    });
    const pt = await getEntityPt.go();
    hoverSelectEnts.forEach((ent) => ent.highlight(false));

    if (getEntityPt.getStatus() === MrxDbgUiPrBaseReturn.kNone) {
      break;
    } else if (getEntityPt.getStatus() === MrxDbgUiPrBaseReturn.kCancel) {
      ids.length = 0;
      break;
    } else {
      // 判断是否选中实体
      if (pt && hoverSelectEnts.length) {
        const selectIds = hoverSelectEnts.map((item) => {
          isHighLight ? item.highlight(true) : item.highlight(false);
          return item.getObjectID();
        });
        ids.push(...selectIds.filter((id) => fun(id)));
        continue;
      } else if (pt && !hoverSelectEnts.length) {
        getEntityPt.setUserDraw((point, pw) => {
          const pts = [
            pt,
            new McGePoint3d(pt.x, point.y),
            point,
            new McGePoint3d(point.x, pt.y),
          ];
          // 设置范围框颜色即位置
          let pl = new McDbPolyline();
          pl.isClosed = true;
          pts.forEach((pt) => pl.addVertexAt(pt));
          pw.setColor(0xffffff);
          pw.drawMcDbEntity(pl);

          // 动态绘制矩形填充框
          const geometry = new THREE.BufferGeometry();
          geometry.setFromPoints([
            new THREE.Vector3(pt.x, pt.y, pt.z),
            new THREE.Vector3(pt.x, point.y, point.z),
            new THREE.Vector3(point.x, point.y, point.z),
            new THREE.Vector3(point.x, pt.y, pt.z),
          ]);
          geometry.attributes.uv = new THREE.BufferAttribute(
            new Float32Array([0, 0, 1, 0, 1, 1, 0, 1]),
            2
          );
          geometry.setIndex([0, 1, 2, 0, 2, 3]);
          // 创建材质（半透明的颜色）
          const material = new THREE.MeshBasicMaterial({
            color: 0x004d00,
            transparent: true,
            opacity: 0.5,
            side: THREE.DoubleSide,
          });
          const mesh = new THREE.Mesh(geometry, material);
          pw.drawEntity(mesh);
        });
        const nextPt = await getEntityPt.go();
        if (!nextPt) break;
        const ss = new MxCADSelectionSet();
        await ss.crossingSelect(pt.x, pt.y, nextPt.x, nextPt.y);
        ss.forEach((id) => {
          if (!ids.map((i) => i.id).includes(id.id)) {
            const ent = id.getMcDbEntity() as McDbEntity;
            if (isExpendEnt) {
              isHighLight ? ent.highlight(true) : ent.highlight(false);
              if (fun(id)) ids.push(id);
            } else {
              const arr = getGroupForEntity(ent as McDbEntity);
              if (arr.length) {
                const group = arr[0].group;
                group.getAllEntityId().forEach((id) => {
                  const item = id.getMcDbEntity() as McDbEntity;
                  isHighLight ? item.highlight(true) : item.highlight(false);
                  if (fun(id)) ids.push(id);
                });
              } else {
                const item = id.getMcDbEntity() as McDbEntity;
                isHighLight ? item.highlight(true) : item.highlight(false);
                if (fun(id)) ids.push(id);
              }
            }
          }
        });
        continue;
      } else {
        continue;
      }
    }
  }

  return ids;
};
// 创建
const createGroup = async () => {
  isShowTips.value = false;
  let name: string = "";
  const mxcad = MxCpp.getCurrentMxCAD();
  const groupDict = mxcad.getDatabase().GetGroupDictionary();
  if (formData.isUnnamed) {
    const groupNames = groupArr.map((item) => item.name);
    let num = 0;
    groupNames.forEach((item) => {
      if (/^\*/.test(item)) {
        num += 1;
      }
    });
    name = `*A${num + 1}`;
  } else {
    name = currentGroup.name;
  }
  if ((/^\*/.test(name) || !name) && !formData.isUnnamed) {
    isShowTips.value = true;

    tips.value = t("编组名无效");
    return;
  }

  const res = groupArr.find((item) => item.name === name);
  if (res) {
    isShowTips.value = true;
    tips.value = t("编组名重复");
    return;
  }
  // 选择对象
  isShowDialog.value = false;
  const ids = await getIds();
  isShowDialog.value = true;
  if (!ids.length) {
    tips.value = t("未选择对象，未创建编组");
    isShowTips.value = true;
  } else {
    ids.forEach((id) => {
      const ent = id.getMcDbEntity();
      if (ent) ent.highlight(false);
    });
    // 创建组
    const res = mxcad.getDatabase().CreateGroup(ids, name);
    if (res) {
      tips.value = t("编组创建成功！");
      const group = groupDict.getAt(name).getMcDbObject() as McDbGroup;
      group.isSelectable = true;
      group.description = currentGroup.description;
      groupArr.push({ name: name, group: group, id: groupArr.length + 1 });
    }
  }
  currentGroup.name = "";
  currentGroup.group = null;
  currentGroup.description = "";
};
// 删除
const delectGroup = async () => {
  isShowTips.value = false;

  const mxcad = MxCpp.getCurrentMxCAD();

  if (!currentGroup.group) return;
  const ents = currentGroup.group
    .getAllEntityId()
    .map((id) => id.getMcDbEntity());
  ents.forEach((ent) => ent?.highlight(true));
  isShowDialog.value = false;
  const midPt = getGroupMidPt(currentGroup.group as McDbGroup);
  MxCpp.getCurrentMxCAD().zoomCenter(midPt.x, midPt.y);

  MxFun.acutPrintf(t("选择要从编组中删除的对象"));
  const ids = await getIds(
    false,
    (id: McObjectId) => {
      const res = ents.filter((item) => item?.getObjectID().id === id.id);
      if (res.length) {
        return true;
      } else {
        MxFun.acutPrintf(t("无法删除"));
        return false;
      }
    },
    true
  );

  ents.forEach((ent) => ent?.highlight(false));
  if (ids.length) {
    currentGroup.group.clear();
    ents.forEach((ent) => {
      const id = ent?.getObjectID() as McObjectId;
      if (!ids.filter((i) => i.id === id.id).length && currentGroup.group)
        currentGroup.group.append(id);
    });
    mxcad.updateDisplay();
  }
  isShowDialog.value = true;
};
// 添加
const addGroup = async () => {
  isShowTips.value = false;
  const mxcad = MxCpp.getCurrentMxCAD();
  if (!currentGroup.group) return;
  const ents = currentGroup.group
    .getAllEntityId()
    .map((id) => id.getMcDbEntity());
  ents.forEach((ent) => ent?.highlight(true));
  isShowDialog.value = false;
  const midPt = getGroupMidPt(currentGroup.group as McDbGroup);
  MxCpp.getCurrentMxCAD().zoomCenter(midPt.x, midPt.y);

  MxFun.acutPrintf(t("选择添加的对象"));
  const ids = await getIds(
    true,
    (id: McObjectId) => {
      const res = ents.filter((item) => item?.getObjectID().id === id.id);
      if (!res.length) {
        return true;
      } else {
        MxFun.acutPrintf(t("无法添加"));
        return false;
      }
    },
    true
  );

  ents.forEach((ent) => ent?.highlight(false));
  if (ids.length) {
    ids.forEach((id) => {
      id.getMcDbEntity()?.highlight(false);
      if (
        !ents.filter((ent) => ent?.getObjectID().id === id.id).length &&
        currentGroup.group
      )
        currentGroup.group.append(id);
    });
    mxcad.updateDisplay();
  }
  isShowDialog.value = true;
};
// 重命名
const renameGroup = async () => {
  isShowTips.value = false;
  const res = groupArr.find((item) => item.name === currentGroup.name);
  if (res) {
    tips.value = t("编组名重复");
    isShowTips.value = true;
    return;
  }
  if (/^\*/.test(currentGroup.name) || !currentGroup.name) {
    tips.value = t("编组名无效");
    isShowTips.value = true;
    return;
  }
  if (currentGroup.group) currentGroup.group.name = currentGroup.name;
  groupArr.forEach((item) => {
    if (item.id === currentGroup.id) {
      item.name = currentGroup.name;
    }
  });
};
// 说明
const setNewDes = () => {
  isShowTips.value = false;
  if (currentGroup.description && currentGroup.group) {
    currentGroup.group.description = currentGroup.description;
    isShowTips.value = true;
    tips.value = t("编组说明已更新") + "!";
  }
};
// 分解
const decompositionGroup = () => {
  isShowTips.value = false;
  if (currentGroup.group) {
    const index = groupArr.findIndex((item) => item.id === currentGroup.id);
    if (index > -1) {
      if (groupArr[index].handle) delectGroupArr.push(groupArr[index]);
      groupArr.splice(index, 1);
    }
    currentGroup.description = "";
    currentGroup.group = null;
    currentGroup.name = "";
  }
};

const sortBtnList = [
  {
    name: t("确定"),
    primary: true,
    fun: () => {
      isShowTips.value = false;
      const group = currentGroup.group;
      group?.clear();
      group?.appendArray(sortArr);
      isShowManageContent.value = true;
      isShowSort.value = false;
      footerBtnList.length = 0;
      title.value = t("对象编组");
      footerBtnList.push(...manageFooterBtn);
    },
  },
  {
    name: t("取消"),
    fun: () => {
      isShowTips.value = false;
      title.value = t("对象编组");
      isShowSort.value = false;
      isShowManageContent.value = true;
      footerBtnList.length = 0;
      footerBtnList.push(...manageFooterBtn);
    },
  },
];

const sortArr: McObjectId[] = [];
// 重排
const rearrangementGroup = () => {
  sortData.delectIndex = 0;
  sortData.newIndex = 0;
  sortData.objNum = 0;
  isShowTips.value = false;
  isShowManageContent.value = false;
  isShowSort.value = true;
  footerBtnList.length = 0;
  title.value = t("编组排序");
  footerBtnList.push(...sortBtnList);
  if (!currentGroup.group) return;
  sortArr.length = 0;
  sortArr.push(...currentGroup.group.getAllEntityId());
};

// 亮显
const isShowShining = ref(false);
const currentIndex = ref(0);
const doShining = () => {
  isShowTips.value = true;

  if (!currentGroup.group) return;
  tips.value = t("对象：") + currentIndex.value;
  isShowSort.value = false;
  title.value = t("对象编组");
  isShowShining.value = true;
  dialogWidth.value = 200;
  footerBtnList.length = 0;
  footerBtnList.push({
    name: t("确定"),
    fun: () => {
      dialogWidth.value = 350;
      footerBtnList.length = 0;
      title.value = t("编组排序");
      footerBtnList.push(...sortBtnList);
      isShowSort.value = true;
      isShowShining.value = false;
      isShowTips.value = false;
      currentIndex.value = 0;
      sortArr.forEach((id) => {
        id.getMcDbEntity()?.highlight(false);
      });
      MxCpp.getCurrentMxCAD().updateDisplay();
    },
  });

  if (sortArr.length) {
    const midPt = getGroupMidPt(currentGroup.group as McDbGroup);
    MxCpp.getCurrentMxCAD().zoomCenter(midPt.x, midPt.y);
    sortArr.forEach((id, index) => {
      if (index === currentIndex.value) {
        id.getMcDbEntity()?.highlight(true);
      } else {
        id.getMcDbEntity()?.highlight(false);
      }
    });
    MxCpp.getCurrentMxCAD().updateDisplay();
  } else {
    sortArr.length = 0;
    sortArr.push(...currentGroup.group.getAllEntityId());
  }
};
// 切换亮显实体
const changeShowEnt = (num: number) => {
  currentIndex.value += num;
  tips.value = t("对象：") + currentIndex.value;
  sortArr.forEach((id, index) => {
    if (index === currentIndex.value) {
      id.getMcDbEntity()?.highlight(true);
    } else {
      id.getMcDbEntity()?.highlight(false);
    }
  });
  MxCpp.getCurrentMxCAD().updateDisplay();
};
// 逆序
const doReverseOrder = () => {
  if (!currentGroup.group) return;
  if (!sortArr.length) {
    sortArr.length = 0;
    sortArr.push(...currentGroup.group.getAllEntityId());
  }
  sortArr.reverse();
  isShowTips.value = true;
  tips.value = t("对象顺序已反转");
};

// 重新排序
function reorderArray(
  array: McObjectId[],
  startPos: number,
  newPos: number,
  numElements: number
): McObjectId[] {
  // 增强参数验证
  if (startPos < 0 || startPos >= array.length) {
    tips.value = t("无法从该位置删除");
    return [];
  }
  if (newPos < 0 || newPos > array.length) {
    // 允许插入到末尾
    tips.value = t("无法从该位置插入");
    return [];
  }
  if (numElements <= 0) {
    numElements = 1;
  }
  // 正常处理逻辑
  const newArray = [...array];
  const elementsToMove = newArray.splice(startPos, numElements);

  // 调整新位置（如果要移动的元素在新位置之前）
  const adjustedPos = startPos < newPos ? newPos - numElements : newPos;

  newArray.splice(adjustedPos, 0, ...elementsToMove);
  return newArray;
}
const doReorder = () => {
  if (!currentGroup.group) return;
  if (!sortArr.length) {
    sortArr.length = 0;
    sortArr.push(...currentGroup.group.getAllEntityId());
  }
  tips.value = "";
  const newArr = reorderArray(
    sortArr,
    Number(sortData.delectIndex),
    Number(sortData.newIndex),
    Number(sortData.objNum)
  );
  isShowTips.value = true;
  if (newArr.length > 0) {
    tips.value = t("编组已重新排序");
    sortArr.length = 0;
    sortArr.push(...newArr);
  } else {
    if (!tips.value) tips.value = t("无法重排！请重新设置！");
  }
};

onUnmounted(() => {
  if (isSaveGroups.value) {
    delectGroupArr.forEach((item) => {
      const groupId = MxCpp.getCurrentMxCAD()
        .getDatabase()
        .handleToIdIndex(item.handle || "");
      if (groupId.isValid()) {
        const group = groupId.getMcDbGroup();
        if (group) {
          group.clear();
          const groupDict =
            MxCpp.getCurrentMxCAD().database.GetGroupDictionary();
          groupDict.remove(group.name);
        }
      }
    });
    groupArr.forEach((item) => {
      if (item.handle) {
        const groupId = MxCpp.getCurrentMxCAD()
          .getDatabase()
          .handleToIdIndex(item.handle);
        if (groupId.isValid()) {
          const group = groupId.getMcDbGroup();
          if (group) {
            group.name = item.name;
            group.description = item.group.description;
            group.clear();
            group.appendArray(item.group.getAllEntityId());
            group.isSelectable = item.group.isSelectable;
          }
        }
      }
    });
  } else {
    // 删除创建的组
    groupArr.forEach((item) => {
      if (!item.handle) {
        item.group.clear();
        const groupDict = MxCpp.getCurrentMxCAD().database.GetGroupDictionary();
        groupDict.remove(item.group.name);
      }
    });
  }
  MxCpp.getCurrentMxCAD().updateDisplay();
});
</script>

