import { DxfCode, IMcDbDwgFiler, McCmColor, McDb, McDbArc, McDbCircle, McDbCustomEntity, McDbEntity, McDbHatch, McDbLine, McDbMText, McDbPolyline, McDbText, McGePoint3d, McGePoint3dArray, McGeVector3d, McObjectId, MxCADResbuf, MxCADSelectionSet, MxCADUiPrEntity, MxCADUiPrPoint, MxCADUtility, MxCADWorldDraw, MxCpp } from 'mxcad';
import { MxMeasureEntity, drawMeasurementAnnotation, dimScale, currentAnnotationColor, getMeasurementCategory, measurementCategory, readDictionaryData, ShowType, dimTextHeight, DimSettingData, dimSettingForm, DimType } from './hooks';
import { MxFun } from 'mxdraw';
import { t } from '../languages';
import { dialog, dialogComponentName } from '../extools/dialog/dialog';

// 保留小数有效三位值
function formatNumber(num, decimalPlaces = 2) {
    if (decimalPlaces < 0) decimalPlaces = 2;
    if (isNaN(num)) return "NaN";

    // 处理科学计数法
    const str = num.toString().includes('e')
        ? num.toFixed(decimalPlaces + 2)
        : num.toString();

    // 分离整数和小数部分
    const [integerPart, decimalPart = ''] = str.split('.');
    if (decimalPlaces == 0) return integerPart

    // 情况1：强制保留3位小数（不足补零）
    if (Number(integerPart) !== 0) {
        const paddedDecimal = decimalPart.padEnd(decimalPlaces, '0').slice(0, decimalPlaces);
        return `${integerPart}.${paddedDecimal}`;
    }

    // 情况2&3：处理小于1的数字
    let firstNonZeroIndex = 0;
    while (firstNonZeroIndex < decimalPart.length && decimalPart[firstNonZeroIndex] === '0') {
        firstNonZeroIndex++;
    }

    // 计算需要保留的总位数（前导零 + 3位）
    const keepCount = firstNonZeroIndex + decimalPlaces;
    const resultDecimal = decimalPart.slice(0, keepCount);

    return `0.${resultDecimal.padEnd(keepCount, '0')}`;
}
// 自定义面积标注类
class MxAreaComment extends MxMeasureEntity {
    /** 点数组 */
    private ptArr: McGePoint3d[] = [];
    /** 标注点 */
    private pt: McGePoint3d;
    /** 字高 */
    private height: number = 0;
    /** 凸度数组 */
    private dbulges: number[] = [];
    /** 测量对象是否为矩形 */
    private isReactFlag: boolean = false;
    // 设置标注线宽
    private lineWidth: number = 15;
    // 标注比例
    private scale: number = 1;
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();
    // 是否计算多个区域面积
    private isMultipleArea: boolean = false;
    // 多个区域点数组，凸度数组集合
    private ptArrArr: { points: McGePoint3d[], dbulges: number[] }[] = [];
    // 分类名
    private typeName: string = "未分类";
    // 面积值
    private areaVal: number = 0;
    // 周长值
    private weeklyVal: number = 0;
    // 标注设置
    private dimSettingForm: DimSettingData[] = [];
    constructor(imp?: any) {
        super(imp);
    }
    public create(imp: any) {
        return new MxAreaComment(imp)
    }
    /** 获取类名 */
    public getTypeName(): string {
        return "MxAreaComment";
    }
    //设置或获取文本字高
    public set dimTextHeight(val: number) {
        this.height = val;
    }
    public get dimTextHeight(): number {
        return this.height;
    }
    //设置或获取测量对象是否为矩形
    public set isReact(val: boolean) {
        this.isReactFlag = val;
    }
    public get isReact(): boolean {
        return this.isReactFlag;
    }
    // 设置或获取标注线宽
    public set dimWidth(val: number) {
        this.lineWidth = val;
    }
    public get dimWidth(): number {
        return this.lineWidth;
    }
    // 设置或获取标注比例
    public set dimScale(val: number) {
        this.scale = val;
    }
    public get dimScale(): number {
        return this.scale;
    }
    // 获取标注类型
    public get dimType(): DimType {
        return DimType.Area;
    }
    // 分类名
    public get categoryName(): string {
        return this.typeName;
    }
    public set categoryName(val: string) {
        this.typeName = val;
    }
    // 面积
    public get areaValue(): number {
        return this.areaVal;
    }
    // 周长
    public get weeklyValue(): number {
        return this.weeklyVal;
    }
    public setDimSettingForm(param: DimSettingData[]) {
        param.forEach(item => { if (item.fixedNum < 0) item.fixedNum = 2; });
        this.dimSettingForm = param;
    }
    public getDimSettingForm(): DimSettingData[] {
        return this.dimSettingForm
    }
    /** 读取数据 */
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.ptArr = filter.readPoints('ptArr').val;
        this.pt = filter.readPoint('pt').val;
        const _dbulges = filter.readString("dbulges").val;
        this.dbulges = _dbulges.split(',').map(Number);
        this.isReactFlag = filter.readLong("isReactFlag").val ? true : false;
        this.lineWidth = filter.readDouble("lineWidth").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        this.scale = filter.readDouble("scale").val;
        this.isMultipleArea = filter.readLong("isMultipleArea").val ? true : false;
        if (this.isMultipleArea) {
            const _dbulgesArr = JSON.parse(filter.readString('dbulgesArr').val);
            const _pointsArr = JSON.parse(filter.readString('pointsArr').val);
            _dbulgesArr.forEach((item, index) => {
                this.ptArrArr.push({
                    points: _pointsArr[index].map(obj => new McGePoint3d(obj.x, obj.y)),
                    dbulges: item
                })
            })
        }
        this.typeName = filter.readString("typeName").val;
        this.height = filter.readDouble("height").val;
        this.areaVal = filter.readDouble("areaVal").val;
        this.weeklyVal = filter.readDouble("weeklyVal").val;
        this.dimSettingForm = JSON.parse(filter.readString("dimSettingForm").val);
        return true;
    }
    /** 写入数据 */
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoints("ptArr", this.ptArr);
        filter.writePoint("pt", this.pt);
        const _dbulges = this.dbulges.toString();
        filter.writeString("dbulges", _dbulges);
        filter.writeLong("isReactFlag", this.isReactFlag ? 1 : 0);
        filter.writeDouble("lineWidth", this.lineWidth);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        filter.writeDouble("scale", this.scale);
        filter.writeLong("isMultipleArea", this.isMultipleArea ? 1 : 0);
        if (this.isMultipleArea) {
            const _dbulgesArr = this.ptArrArr.map(item => item.dbulges);
            filter.writeString("dbulgesArr", JSON.stringify(_dbulgesArr));
            const pointsArr = this.ptArrArr.map(item => item.points);
            const _pointsArr = [];
            pointsArr.forEach(item => {
                const ptsData = item.map(pt => {
                    return { x: pt.x, y: pt.y }
                })
                _pointsArr.push(ptsData)
            })
            filter.writeString("pointsArr", JSON.stringify(_pointsArr));
        }
        filter.writeString("typeName", this.typeName);
        filter.writeDouble("height", this.height);
        filter.writeDouble("areaVal", this.areaVal);
        filter.writeDouble("weeklyVal", this.weeklyVal);
        filter.writeString('dimSettingForm', JSON.stringify(this.dimSettingForm))
        return true;
    }
    /** 移动夹点 */
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        this.ptArr.forEach((point, index) => {
            if (index === iIndex) {
                point.x += dXOffset;
                point.y += dYOffset;
                point.z += dZOffset;
            }
        });
        if (iIndex === this.ptArr.length) {
            this.pt.x += dXOffset;
            this.pt.y += dYOffset;
            this.pt.z += dZOffset;
        }
    };
    /** 获取夹点 */
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray();
        this.ptArr.forEach(point => {
            ret.append(point);
        })
        ret.append(this.pt);
        return ret;
    };
    /** 动态绘制 */
    public worldDraw(draw: MxCADWorldDraw): void {
        const allEntityArr = this.getAllEntity();
        allEntityArr.forEach(entity => {
            entity.trueColor = this.trueColor;
            entity.drawOrder = this.drawOrder;
            entity.layerId = this.layerId;
            draw.setupForEntity(entity);
            draw.drawEntity(entity);
        })
    }

    public getAllEntity(): McDbEntity[] {
        const allEntityArr: McDbEntity[] = [];
        let area = 0;
        let length = 0;
        let width = 0;

        if (!this.isMultipleArea) {
            const pl = new McDbPolyline();
            pl.isClosed = true;
            if (this.isReactFlag) {
                // 测量的是矩形，得到的就是矩形的两个角点
                pl.addVertexAt(this.ptArr[0]);
                pl.addVertexAt(new McGePoint3d(this.ptArr[1].x, this.ptArr[0].y));
                pl.addVertexAt(this.ptArr[1]);
                pl.addVertexAt(new McGePoint3d(this.ptArr[0].x, this.ptArr[1].y));
            } else {
                // 测量的是多边形
                this.ptArr.forEach((pt, index) => {
                    pl.addVertexAt(pt, this.dbulges[index]);
                });
            }
            pl.lineweight = this.lineWidth;
            // 获取对象数据信息
            area = pl.getArea().val;
            length = pl.getLength().val;
            allEntityArr.push(pl);
            const { minPt, maxPt } = pl.getBoundingBox();
            width = Math.abs(maxPt.x - minPt.x);
        } else {
            this.ptArrArr.forEach(item => {
                const pl = new McDbPolyline();
                pl.isClosed = true;
                pl.lineweight = this.lineWidth;
                item.points.forEach((pt, index) => {
                    pl.addVertexAt(pt, item.dbulges[index]);
                });
                area += pl.getArea().val;
                length += pl.getLength().val;
                const { minPt, maxPt } = pl.getBoundingBox();
                if (width < minPt.distanceTo(maxPt)) {
                    width = minPt.distanceTo(maxPt);
                }
                allEntityArr.push(pl);
            })
        }
        // 构造对文本对象
        const text = new McDbMText();
        let str = '';
        const areaVal = area * this.scale * this.scale;
        const areaSetting = this.dimSettingForm.find(item => item.type === DimType.Area) || { showType: ShowType.ActualValue, fixedNum: 2 };
        if (areaSetting.showType === ShowType.ActualValue) {
            str += `面积：${formatNumber(areaVal, areaSetting.fixedNum)}\\P`;
        } else if (areaSetting.showType === ShowType.SquareMeter) {
            str += `面积：${formatNumber(areaVal * 0.000001, areaSetting.fixedNum)}m{\\S2^;}\\P`
        }
        const weeklyVal = length * this.scale;
        const weeklySetting = this.dimSettingForm.find(item => item.type === DimType.Length) || { showType: ShowType.ActualValue, fixedNum: 2 };
        if (weeklySetting.showType === ShowType.ActualValue) {
            str += `周长：${formatNumber(weeklyVal, weeklySetting.fixedNum)}`
        } else if (weeklySetting.showType === ShowType.SquareMeter) {
            str += `周长：${formatNumber(weeklyVal * 0.001, weeklySetting.fixedNum)}m`
        }
        text.contents = str;
        text.attachment = McDb.AttachmentPoint.kMiddleCenter;
        text.location = this.pt;
        if (!this.height) this.height = width / 10;
        text.textHeight = this.height;
        allEntityArr.push(text);

        this.areaVal = Number((area * this.scale * this.scale).toFixed(areaSetting.fixedNum));
        this.weeklyVal = Number((length * this.scale).toFixed(weeklySetting.fixedNum));
        return allEntityArr;
    }

    private getBox(entityArr: McDbEntity[]) {
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbMText) {
                entity.textStyleId = MxCpp.getCurrentMxCAD().database.getCurrentlyTextStyleId();
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }

    /** 设置标注点 */
    public setPoint(pt: McGePoint3d) {
        this.assertWrite();
        this.pt = pt.clone();
    }
    /** 增加顶点 */
    public addVertex(pt: McGePoint3d, dbulge?: number) {
        this.assertWrite();
        this.ptArr.push(pt.clone());
        this.dbulges.push(dbulge ? dbulge : 0);
    }
    /** 获取标注点 */
    public getPoint() {
        return this.pt;
    }
    /** 设置多个区域点数组，凸度数组集合 */
    public setPtArrArr(ptArrArr: { points: McGePoint3d[], dbulges: number[] }[]) {
        this.ptArrArr = ptArrArr;
        this.isMultipleArea = true;
    }
    /** 获取多个区域点数组，凸度数组集合 */
    public getPtArrArr() {
        return this.ptArrArr;
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        this.getBox(this.getAllEntity());
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
}

// 面积
async function Mx_Area() {
    const ptArr: McGePoint3d[] = [];

    const getPt1 = new MxCADUiPrPoint();
    getPt1.setMessage(t('请选择面积的第一个点'));
    const pt1 = await getPt1.go();
    if (!pt1) return;
    ptArr.push(pt1);

    while (true) {
        const getPt = new MxCADUiPrPoint();
        getPt.setMessage(t('请选择下一个点'));
        getPt.setKeyWords("[回退(B)/结束(O)]");
        getPt.setUserDraw((pt, pw) => {
            pw.setColor(new THREE.Color(parseInt(currentAnnotationColor.value.slice(1), 16)))
            const draw_pl = new McDbPolyline();
            ptArr.forEach(point => {
                draw_pl.addVertexAt(point, 0, 0.1, 0.1);
            });
            draw_pl.addVertexAt(pt);
            pw.drawMcDbEntity(draw_pl);
        });
        const pt = await getPt.go();
        const key = getPt.keyWordPicked();
        if (key === 'B') {
            if (ptArr.length > 1) {
                ptArr.pop();
            }
        } else if (key === 'O') {
            break;
        } else {
            if (!pt) break;
            ptArr.push(pt);
        }
    };

    if (ptArr.length < 3) return;
    const area = new MxAreaComment();
    area.dimScale = dimScale.value;
    area.setDimSettingForm(dimSettingForm);
    if (dimTextHeight.value) area.dimTextHeight = dimTextHeight.value;
    ptArr.forEach((point) => {
        area.addVertex(point, 0);
    });
    const position = new MxCADUiPrPoint();
    position.setMessage(t('请选择面积标注的位置'));
    position.setDisableOsnap(true);
    position.setDisableDynInput(true);
    position.disableAllTrace(true);
    position.setUserDraw((pt, pw) => {
        pw.setColor(new THREE.Color(parseInt(currentAnnotationColor.value.slice(1), 16)))
        area.setPoint(pt);
        pw.drawMcDbEntity(area);
    })
    const positionPt = await position.go();
    if (!positionPt) return;
    area.setPoint(positionPt);
    drawMeasurementAnnotation(area);
}
// 矩形面积
async function Mx_ReactArea() {
    const getPt1 = new MxCADUiPrPoint();
    getPt1.setMessage(t('请选择矩形的角点1'));
    const pt1 = await getPt1.go();
    if (!pt1) return;

    const getPt2 = new MxCADUiPrPoint();
    getPt2.setMessage(t('请选择矩形的角点2'));
    getPt2.setUserDraw((pt, pw) => {
        pw.setColor(new THREE.Color(parseInt(currentAnnotationColor.value.slice(1), 16)))
        const pl = new McDbPolyline();
        pl.isClosed = true;
        pl.addVertexAt(pt1);
        pl.addVertexAt(new McGePoint3d(pt.x, pt1.y));
        pl.addVertexAt(pt);
        pl.addVertexAt(new McGePoint3d(pt1.x, pt.y));
        pw.drawMcDbEntity(pl);
    })
    const pt2 = await getPt2.go();
    if (!pt2) return;

    const reactArea = new MxAreaComment();
    reactArea.dimScale = dimScale.value;
    reactArea.setDimSettingForm(dimSettingForm);
    if (dimTextHeight.value) reactArea.dimTextHeight = dimTextHeight.value;

    reactArea.isReact = true;
    reactArea.addVertex(pt1);
    reactArea.addVertex(pt2);

    const getPos = new MxCADUiPrPoint();
    getPos.setMessage(t('请设置面积标注的位置'));
    getPos.setDisableOsnap(true);
    getPos.setDisableDynInput(true);
    getPos.disableAllTrace(true);
    getPos.setUserDraw((pt, pw) => {
        pw.setColor(new THREE.Color(parseInt(currentAnnotationColor.value.slice(1), 16)))
        reactArea.setPoint(pt);
        pw.drawMcDbEntity(reactArea);
    });
    const position = await getPos.go();
    if (!position) return;
    reactArea.setPoint(position);
    drawMeasurementAnnotation(reactArea);
}

// 自定义测量坐标标注类
class MxCoordinateLabeling extends MxMeasureEntity {
    // 定义McDbTestCoordinateLabeling内部的点对象 
    // 点对象
    private point: McGePoint3d = new McGePoint3d();
    // 标注点
    private position: McGePoint3d = new McGePoint3d();
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();
    // 字高
    private height: number = MxFun.screenCoordLong2Doc(30);
    // 分类名
    private typeName: string = "未分类";
    // 保留小数位数
    public fixedNum: number = 2;
    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    private dimSettingForm: DimSettingData[] = [];

    public setDimSettingForm(param: DimSettingData[]) {
        param.forEach(item => { if (item.fixedNum < 0) item.fixedNum = 2; });
        this.dimSettingForm = param;
    }
    public getDimSettingForm(): DimSettingData[] {
        return this.dimSettingForm
    }

    // 创建函数
    public create(imp: any) {
        return new MxCoordinateLabeling(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "MxCoordinateLabeling";
    }
    //设置或获取测量点坐标
    public set coordinatePoint(val: McGePoint3d) {
        this.point = val.clone();
    }
    public get coordinatePoint(): McGePoint3d {
        return this.point;
    }
    //设置或获取字高
    public set dimTextHeight(val: number) {
        this.height = val;
    }
    public get dimTextHeight(): number {
        return this.height;
    }
    // 获取标注类型
    public get dimType(): DimType {
        return DimType.Coordinate;
    }
    // 分类名
    public get categoryName(): string {
        return this.typeName;
    }
    public set categoryName(val: string) {
        this.typeName = val;
    }

    // 读取自定义实体数据point、position、height
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.point = filter.readPoint("point").val;
        this.position = filter.readPoint("position").val;
        this.height = filter.readDouble("height").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        this.typeName = filter.readString("typeName").val;
        this.fixedNum = filter.readDouble("fixedNum").val;
        this.dimSettingForm = JSON.parse(filter.readString("dimSettingForm").val);

        return true;
    }
    // 写入自定义实体数据point、position、height
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("point", this.point);
        filter.writePoint("position", this.position);
        filter.writeDouble("height", this.height);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        filter.writeString("typeName", this.typeName);
        filter.writeDouble("fixedNum", this.fixedNum);
        filter.writeString('dimSettingForm', JSON.stringify(this.dimSettingForm))

        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        if (iIndex === 0) {
            this.point.x += dXOffset;
            this.point.y += dYOffset;
            this.point.z += dZOffset;
        } else {
            this.position.x += dXOffset;
            this.position.y += dYOffset;
            this.position.z += dZOffset;
        }
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray()
        ret.append(this.point);
        ret.append(this.position);
        return ret;
    };
    // 绘制实体
    public worldDraw(draw: MxCADWorldDraw): void {
        const allEntityArr = this.getAllEntity();
        allEntityArr.forEach(entity => {
            entity.trueColor = this.trueColor;
            entity.drawOrder = this.drawOrder;
            entity.layerId = this.layerId;
            draw.setupForEntity(entity);
            draw.drawEntity(entity);
        })
    }
    public getAllEntity(): McDbEntity[] {
        const allEntityArr: McDbEntity[] = [];
        // 比较position和point的x坐标大小,判断是否反向
        const isReverse = this.position.x < this.point.x;

        const text = new McDbMText();
        text.textHeight = this.height;
        text.attachment = isReverse ? McDb.AttachmentPoint.kMiddleRight : McDb.AttachmentPoint.kMiddleLeft;
        const coordSetting = this.dimSettingForm.find(item => item.type === DimType.Coordinate) || { showType: ShowType.ActualValue, fixedNum: 2 };
        if (coordSetting.showType === ShowType.ActualValue) {
            text.contents = `X=${formatNumber(this.point.x, coordSetting.fixedNum)}\\PY=${formatNumber(this.point.y, coordSetting.fixedNum)}`;
        } else if (coordSetting.showType === ShowType.SquareMeter) {
            text.contents = `X=${formatNumber(this.point.x * 0.001, coordSetting.fixedNum)}\\PY=${formatNumber(this.point.y * 0.001, coordSetting.fixedNum)}`;
        }

        const v = McGeVector3d.kXAxis.clone().mult(text.textHeight / 3);
        text.location = this.position.clone().addvec(isReverse ? v.negate() : v);
        allEntityArr.push(text);

        text.textStyleId = MxCpp.getCurrentMxCAD().database.getCurrentlyTextStyleId();
        text.reCompute();
        const { minPt, maxPt } = MxCADUtility.getTextEntityBox(text, false);

        const length = Math.abs(maxPt.x - minPt.x);
        const x = isReverse ? this.position.x - length * 1.2 : this.position.x + length * 1.2;
        const pt3 = new McGePoint3d(x, this.position.y);
        const pl = new McDbPolyline();
        pl.addVertexAt(this.point);
        pl.addVertexAt(this.position);
        pl.addVertexAt(pt3);
        allEntityArr.push(pl);

        return allEntityArr;
    }
    private getBox(entityArr: McDbEntity[]) {
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbMText) {
                entity.textStyleId = MxCpp.getCurrentMxCAD().database.getCurrentlyTextStyleId();
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        this.getBox(this.getAllEntity());
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
    // 获取position
    public setPosition(pt: McGePoint3d) {
        this.assertWrite();
        this.position = pt.clone();
    }
    // 获取position
    public getPosition() {
        return this.position;
    }
}

