// 设置标注比例
async function Mx_SetDimScale() {
    const getPoint = new MxCADUiPrPoint();
    getPoint.setMessage(t('请选择一条已知直线的起点'));
    const pt1 = await getPoint.go();
    if (!pt1) return;
    getPoint.setMessage(t('请选择已知直线的终点'));
    getPoint.setUserDraw((pt, pw) => {
        pw.setColor(new THREE.Color(parseInt(currentAnnotationColor.value.slice(1), 16)))
        const line = new McDbLine(pt1, pt);
        pw.drawMcDbEntity(line);
    });
    const pt2 = await getPoint.go();
    if (!pt2) return;
    const distance = Number(pt1.distanceTo(pt2).toFixed(2));
    dialogComponentName.value = 'SetDimScaleDialog';
    dialog.showDialog(true, { title: t("输入实际尺寸"), paramData: { distance }, isShowFooterBtn: true }).then(async ({ data, isCanceled }) => {
        if (!isCanceled && data?.dimScale) {
            dimScale.value = data.dimScale;
        }
    });
}
