import { McDbCustomEntity, McGePoint3d, IMcDbDwgFiler, McGePoint3dArray, MxCADWorldDraw, McDb, MxCpp, McDbPolyline, McDbLine, McDbText, MxCADUiPrPoint, MxCADUiPrDist, McDbEntity, McDbCircle, McGeVector3d, McDbMText, MxCADUtility } from "mxcad";
import { MxFun } from "mxdraw";
import { t } from '../../i18n';

export enum markStyle {
    NationalMarking,
    TraditionalMarking
}
// 图名标注类
export class McDbTestMapTitleMark extends McDbCustomEntity {
    // 定义McDbTestMapTitleMark内部的点对象 
    // 图名
    private _textName: string = "";
    // 文字高度
    private height: number = 0;
    // 标注位置
    private _markPos: McGePoint3d = new McGePoint3d();
    // 比例位置
    private scalePos: McGePoint3d = new McGePoint3d();
    // 是否显示比例
    private _scale: boolean = false;
    // 比例值
    private _scaleValue: string = "";
    // 样式标注
    private _markStyle: number = markStyle.NationalMarking;
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();

    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new McDbTestMapTitleMark(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "McDbTestMapTitleMark";
    }
    //设置或获取文本字高
    public set textHeight(val: number) {
        this.height = val;
    }
    public get textHeight(): number {
        return this.height;
    }
    //设置或获取标注样式
    public set markStyle(val: number) {
        this._markStyle = val;
    }
    public get markStyle(): number {
        return this._markStyle;
    }
    //设置或获取标注位置
    public set markPos(pt: McGePoint3d) {
        this._markPos = pt.clone();
        if (!this.height) this.height = MxFun.screenCoordLong2Doc(15);
        const { minPt, maxPt } = this.getTextLength();
        this.scalePos = new McGePoint3d(maxPt.x, minPt.y).addvec(McGeVector3d.kXAxis.clone().mult(this.height * (2 / 3)));
    }
    public get markPos(): McGePoint3d {
        return this._markPos;
    }
    //设置或获取图名
    public set textName(val: string) {
        this._textName = val;
        const { minPt, maxPt } = this.getTextLength();
        this.scalePos = new McGePoint3d(maxPt.x, minPt.y).addvec(McGeVector3d.kXAxis.clone().mult(this.height * (2 / 3)));
    }
    public get textName(): string {
        return this._textName;
    }
    // 设置或获取比例值
    public set scaleValue(val: string) {
        this._scaleValue = val;
    }
    public get scaleValue(): string {
        return this._scaleValue;
    }
    // 设置或获取是否显示比例
    public set scale(val: boolean) {
        this._scale = val;
    }
    public get scale(): boolean {
        return this._scale;
    }
    // 读取自定义实体数据
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this._markPos = filter.readPoint("markPos").val;
        this.scalePos = filter.readPoint("scalePos").val;
        this.height = filter.readDouble("height").val;
        this._textName = filter.readString("textName").val;
        this._scaleValue = filter.readString("scaleValue").val;
        this.scale = filter.readLong("scale").val == 1 ? true : false;
        this._markStyle = filter.readLong("markStyle").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        return true;
    }
    // 写入自定义实体数据
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("markPos", this._markPos);
        filter.writePoint("scalePos", this.scalePos);
        filter.writeDouble("height", this.height);
        filter.writeString("textName", this._textName);
        filter.writeString("scaleValue", this._scaleValue);
        filter.writeLong("scale", this._scale ? 1 : 0);
        filter.writeLong("markStyle", this._markStyle);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        if (iIndex === 0) {
            this._markPos.x += dXOffset;
            this._markPos.y += dYOffset;
            this._markPos.z += dZOffset;
            this.scalePos.x += dXOffset;
            this.scalePos.y += dYOffset;
            this.scalePos.z += dZOffset;
        } else if (iIndex === 1) {
            const line = new McDbLine(this.scalePos.x, this.scalePos.y, this.scalePos.z, this.markPos.x, this.markPos.y, this.markPos.z);
            const pt = this.scalePos.clone();
            pt.x += dXOffset;
            pt.y += dYOffset;
            pt.z += dZOffset;
            this.scalePos = line.getClosestPointTo(pt, true).val;
        }
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray();
        ret.append(this._markPos);
        if (this._scale) ret.append(this.scalePos);
        return ret;
    };
    // 绘制实体 
    public worldDraw(draw: MxCADWorldDraw): void {
        const allEntityArr: McDbEntity[] = [];
        //  绘制文字
        const { minPt, maxPt, text } = this.getTextLength();
        draw.drawEntity(text);
        allEntityArr.push(text)

        let endPt: McGePoint3d;
        endPt = new McGePoint3d(maxPt.x, minPt.y);

        // 绘制比例
        if (this._scale) {
            const scaleText = new McDbText();
            scaleText.height = this.height * (2 / 3);
            scaleText.textString = this._scaleValue;
            scaleText.position = scaleText.alignmentPoint = this.scalePos;
            scaleText.horizontalMode = McDb.TextHorzMode.kTextLeft;
            const id = MxCpp.getCurrentMxCAD().drawEntity(scaleText);
            const { minPt: _minPt, maxPt: _maxPt } = id.getMcDbEntity().getBoundingBox();
            id.erase();
            if (this._markStyle === markStyle.TraditionalMarking) {
                endPt = new McGePoint3d(_maxPt.x, minPt.y);
            }
            draw.drawEntity(scaleText);
            allEntityArr.push(scaleText)

        }

        // 绘制图名线
        const v = McGeVector3d.kYAxis.clone().negate().mult(this.height / 4);
        const movePt = this._markPos.clone().addvec(v);
        const width = this.height / 5;
        const startPt = minPt.clone();
        const pl = new McDbPolyline();
        pl.addVertexAt(startPt, 0, width, width);
        pl.addVertexAt(endPt, 0, width, width);
        pl.move(this._markPos, movePt);
        pl.isClosed = true;
        draw.drawEntity(pl);
        allEntityArr.push(pl)

        const line = new McDbLine(startPt.x, startPt.y, startPt.z, endPt.x, endPt.y, endPt.z);
        line.move(this._markPos, movePt.clone().addvec(v.clone()));
        draw.drawEntity(line);
        allEntityArr.push(line)

        this.getBox(allEntityArr);
    }
    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbText) {
                const ent = new McDbMText()
                ent.contents = entity.textString;
                ent.textHeight = entity.height;
                ent.location = entity.position;
                switch (entity.horizontalMode) {
                    case McDb.TextHorzMode.kTextLeft:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                    case McDb.TextHorzMode.kTextCenter:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    case McDb.TextHorzMode.kTextRight:
                        ent.attachment = McDb.AttachmentPoint.kBottomRight;
                        break;
                    case McDb.TextHorzMode.kTextMid:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    default:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                }
                // 将ent的文字样式设置为当前文字样式
                const textStyleId = mxcad.getDatabase().getCurrentlyTextStyleId();
                ent.textStyleId = textStyleId;
                ent.reCompute();
                result = MxCADUtility.getTextEntityBox(ent, false);
            } else if (entity instanceof McDbMText) {
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }
    // 计算文字长度
    private getTextLength() {
        const text = new McDbText();
        text.height = this.height;
        text.textString = this._textName;
        text.position = text.alignmentPoint = this._markPos;
        text.horizontalMode = McDb.TextHorzMode.kTextCenter;
        const id = MxCpp.getCurrentMxCAD().drawEntity(text);
        const { minPt, maxPt } = id.getMcDbEntity().getBoundingBox();
        id.erase();
        return { minPt, maxPt, text }
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
};

export async function drawTitleMark(textName: string, scale: boolean, scaleValue: string, markStyle: number) {
    const titleMark = new McDbTestMapTitleMark();
    titleMark.textName = textName;
    titleMark.scale = scale;
    titleMark.scaleValue = scaleValue;
    titleMark.markStyle = markStyle;

    const getPoint1 = new MxCADUiPrPoint();
    getPoint1.setMessage(`\n${t('请指定图名标注位置')}:`);
    getPoint1.setUserDraw((pt, pw) => {
        const _clone = titleMark.clone() as McDbTestMapTitleMark;
        _clone.markPos = pt;
        pw.drawMcDbEntity(_clone);
    })
    const point1 = await getPoint1.go();
    if (!point1) return;
    titleMark.markPos = point1;
    const mxcad = MxCpp.getCurrentMxCAD();
    mxcad.drawEntity(titleMark);
}