import { McDbCustomEntity, McGePoint3d, IMcDbDwgFiler, McGePoint3dArray, MxCADWorldDraw, McDb, MxCpp, McDbPolyline, McDbLine, McDbText, MxCADUiPrPoint, McDbHatch, McGeVector3d, McDbEntity, McDbMText, MxCADUtility } from "mxcad";
import { MxFun } from "mxdraw";
import { t } from '../../i18n';

export enum cutStyle {
    // 正交剖切
    crossSection,
    // 断面剖切
    sectionCutting,
}
export enum markPos {
    // 沿剖切位置线
    bothEnds,
    // 始端
    start,
    // 末端
    end
}
export enum markDirect {
    // 沿剖视方向线
    sectionLine,
    // 沿剖切位置线
    positionLine,
}

// 剖切符号类
export class McDbTestSliceSymbol extends McDbCustomEntity {
    // 定义McDbTestSliceSymbol内部的点对象 
    // 剖切开始点
    private _startPoint: McGePoint3d = new McGePoint3d();
    // 剖切开始点文字位置
    private startPos: McGePoint3d = new McGePoint3d();
    private _startPos: McGePoint3d = new McGePoint3d();
    // 剖切结束点
    private _endPoint: McGePoint3d = new McGePoint3d();
    // 剖切结束点文字位置
    private endPos: McGePoint3d = new McGePoint3d();
    private _endPos: McGePoint3d = new McGePoint3d();
    // 是否调整剖切方向
    private _sectionDirection: boolean = false;
    // 是否显示剖面图号
    private _isShowSec: boolean = false;
    // 标注位置
    private _markPos: number = markPos.bothEnds;
    // 标注方向
    private _markDirect: number = markDirect.sectionLine;
    // 剖切编号
    private _cutNum: string = "";
    // 剖切线长
    private sliceLength: number = 0;
    // 文字高度
    private height: number = 0;
    // 剖切类型
    private _cutStyle: number = cutStyle.crossSection;
    // 剖切面号
    private _secNum: string = "";
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();

    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new McDbTestSliceSymbol(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "McDbTestSliceSymbol";
    }
    //设置或获取文本字高
    public set textHeight(val: number) {
        this.height = val;
    }
    public get textHeight(): number {
        return this.height;
    }
    //设置或获取剖切开始点
    public set startPoint(pt: McGePoint3d) {
        this._startPoint = pt.clone();
    }
    public get startPoint(): McGePoint3d {
        return this._startPoint;
    }
    //设置或获取剖切结束点
    public set endPoint(pt: McGePoint3d) {
        this._endPoint = pt.clone();
        if (!this.sliceLength) this.sliceLength = MxFun.screenCoordLong2Doc(30);
        if (!this.height) this.height = this.sliceLength / 2;
        this.getTextPos();
    }
    public get endPoint(): McGePoint3d {
        return this._endPoint;
    }
    //设置或获取是否转换剖切方向
    public set sectionDirection(val: boolean) {
        this._sectionDirection = val;
        this.getTextPos();
    }
    public get sectionDirection(): boolean {
        return this._sectionDirection;
    }
    public set isShowSec(val: boolean) {
        this._isShowSec = val;
        this.getTextPos();
    }
    public get isShowSec(): boolean {
        return this._isShowSec;
    }
    //设置或获取剖切编号
    public set cutNum(val: string) {
        this._cutNum = val;
    }
    public get cutNum(): string {
        return this._cutNum;
    }
    //设置或获取剖切面号
    public set secNum(val: string) {
        this._secNum = val;
    }
    public get secNum(): string {
        return this._secNum;
    }
    //设置或获取剖切类型
    public set cutStyle(val: number) {
        this._cutStyle = val;
    }
    public get cutStyle(): number {
        return this._cutStyle;
    }
    public set markPos(val: number) {
        this._markPos = val;
    }
    public get markPos(): number {
        return this._markPos;
    }
    public set markDirect(val: number) {
        this._markDirect = val;
    }
    public get markDirect(): number {
        return this._markDirect;
    }
    // 读取自定义实体数据
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this._startPoint = filter.readPoint("startPoint").val;
        this.startPos = filter.readPoint("startPos").val;
        this._startPos = filter.readPoint("_startPos").val;
        this._endPoint = filter.readPoint("endPoint").val;
        this.endPos = filter.readPoint("endPos").val;
        this._endPos = filter.readPoint("_endPos").val;
        this.height = filter.readDouble("height").val;
        this._cutStyle = filter.readLong("cutStyle").val;
        this._markDirect = filter.readLong("markDirect").val;
        this._markPos = filter.readLong("markPos").val;
        this.sliceLength = filter.readDouble("sliceLength").val;
        this._cutNum = filter.readString("cutNum").val;
        this._secNum = filter.readString("secNum").val;
        this._sectionDirection = filter.readLong("sectionDirection").val === 1 ? true : false;
        this._isShowSec = filter.readLong("isShowSec").val === 1 ? true : false;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        return true;
    }
    // 写入自定义实体数据
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("startPoint", this._startPoint);
        filter.writePoint("startPos", this.startPos);
        filter.writePoint("_startPos", this._startPos);
        filter.writePoint("endPoint", this._endPoint);
        filter.writePoint("endPos", this.endPos);
        filter.writePoint("_endPos", this._endPos);
        filter.writeLong("sectionDirection", this._sectionDirection ? 1 : 0);
        filter.writeLong("isShowSec", this._isShowSec ? 1 : 0);
        filter.writeLong("cutStyle", this._cutStyle);
        filter.writeLong("markPos", this._markPos);
        filter.writeLong("markDirect", this._markDirect);
        filter.writeLong("cutStyle", this._cutStyle);
        filter.writeString("cutNum", this._cutNum);
        filter.writeString("secNum", this._secNum);
        filter.writeDouble("height", this.height);
        filter.writeDouble("sliceLength", this.sliceLength);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        const line = new McDbLine(this._startPoint, this._endPoint);
        const vec = this.endPoint.sub(this.startPoint);
        if (this.endPoint.x < this.startPoint.x) vec.negate();
        if (iIndex === 0) {
            this._startPoint.x += dXOffset;
            this._startPoint.y += dYOffset;
            this._startPoint.z += dZOffset;
            this._startPoint = line.getClosestPointTo(this._startPoint, true).val;
            if (this._startPoint.x > this._endPoint.x) {
                const pt = this.startPoint.clone();
                this._startPoint = this.endPoint.clone();
                this.endPoint = pt.clone();
            }
            this.getTextPos();
        }
        if (iIndex === 1) {
            this._endPoint.x += dXOffset;
            this._endPoint.y += dYOffset;
            this._endPoint.z += dZOffset;
            this._endPoint = line.getClosestPointTo(this._endPoint, true).val;
            if (this._startPoint.x > this._endPoint.x) {
                const pt = this.startPoint.clone();
                this._startPoint = this.endPoint.clone();
                this.endPoint = pt.clone();
            }
            this.getTextPos();
        } else if (iIndex === 2) {
            const angle1 = vec.angleTo2(this.startPos.sub(this.startPoint), McGeVector3d.kNegateZAxis);
            this.startPos.x += dXOffset;
            this.startPos.y += dYOffset;
            this.startPos.z += dZOffset;
            const angle2 = vec.angleTo2(this.startPos.sub(this.startPoint), McGeVector3d.kNegateZAxis);
            const pt = line.getClosestPointTo(this.endPos, true).val
            if (angle1 - Math.PI < 0 !== angle2 - Math.PI < 0) {
                this._sectionDirection = !this._sectionDirection;
                this.endPos.addvec(this.endPos.sub(pt).negate().mult(2));
            }
        } else if (iIndex === 3) {
            const angle1 = vec.angleTo2(this.endPos.sub(this.endPoint), McGeVector3d.kNegateZAxis);
            this.endPos.x += dXOffset;
            this.endPos.y += dYOffset;
            this.endPos.z += dZOffset;
            const angle2 = vec.angleTo2(this.endPos.sub(this.endPoint), McGeVector3d.kNegateZAxis);
            const pt = line.getClosestPointTo(this.startPos, true).val
            if (angle1 - Math.PI < 0 !== angle2 - Math.PI < 0) {
                this._sectionDirection = !this._sectionDirection;
                this.startPos.addvec(this.startPos.sub(pt).negate().mult(2));
            }
        }
        const length = this.getGripPoints().length();
        if (this._isShowSec) {
            if ((this._markPos === markPos.bothEnds && iIndex === length - 2) || (this._markPos === markPos.start && iIndex === length - 1)) {
                this._startPos.x += dXOffset;
                this._startPos.y += dYOffset;
                this._startPos.z += dZOffset;
            }
            if ((this._markPos === markPos.bothEnds && iIndex === length - 1) || (this._markPos === markPos.end && iIndex === length - 1)) {
                this._endPos.x += dXOffset;
                this._endPos.y += dYOffset;
                this._endPos.z += dZOffset;
            }
        }
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray();
        ret.append(this._startPoint);
        ret.append(this._endPoint);
        ret.append(this.startPos);
        ret.append(this.endPos);
        if (this._isShowSec) {
            if (this._markPos === markPos.bothEnds || this._markPos === markPos.start) {
                ret.append(this._startPos);
            }
            if (this._markPos === markPos.bothEnds || this._markPos === markPos.end) {
                ret.append(this._endPos);
            }
        }
        return ret;
    };
    private drawSliceIcon(): McDbEntity[] {
        const entityArr = [];
        const { startPoint, endPoint } = this.getDirection();
        const vec = endPoint.sub(startPoint).normalize();
        const pointArr = [startPoint, endPoint];
        pointArr.forEach((point, index) => {
            if (index === 1) vec.negate();
            const pt1 = point.clone().addvec(vec.clone().mult(this.sliceLength));
            const _vec = (index === 0) ? vec.clone().perpVector().mult(this.sliceLength / 2) : vec.negate().clone().perpVector().mult(this.sliceLength / 2);
            if (this._sectionDirection) _vec.negate();
            const pt2 = point.clone().subvec(_vec);
            const pl = new McDbPolyline();
            const width = this.sliceLength / 10;
            pl.addVertexAt(pt1, 0, width, width);
            pl.addVertexAt(point, 0, width, width);
            if (this._cutStyle === cutStyle.crossSection) {
                pl.addVertexAt(pt2, 0, width, width);
            }
            const text = new McDbText();
            text.textString = this._cutNum;
            text.height = this.height;
            const v = McGeVector3d.kYAxis.clone().mult(this.height / 2)
            text.position = text.alignmentPoint = (index === 0 ? this.startPos.clone().subvec(v) : this.endPos.clone().subvec(v));
            text.horizontalMode = McDb.TextHorzMode.kTextCenter;
            entityArr.push(...[text, pl]);
        })
        const text = new McDbText();
        text.textString = this._secNum;
        text.height = this.height * (3 / 4);
        text.horizontalMode = McDb.TextHorzMode.kTextCenter;
        const v = McGeVector3d.kYAxis.clone().mult(this.height / 2)
        if (this._isShowSec) {
            if (this._markPos === markPos.bothEnds || this._markPos === markPos.end) {
                const _text = text.clone() as McDbText;
                _text.position = _text.alignmentPoint = this._endPos.clone();
                const angle = vec.angleTo2(McGeVector3d.kYAxis);
                if (this._markDirect === markDirect.positionLine) {
                    _text.rotate(this._endPos, angle);
                }
                entityArr.push(_text);
            }
            if (this._markPos === markPos.bothEnds || this._markPos === markPos.start) {
                const _text = text.clone() as McDbText;
                _text.position = _text.alignmentPoint = this._startPos.clone();
                const angle = vec.angleTo2(McGeVector3d.kYAxis);
                if (this._markDirect === markDirect.positionLine) {
                    _text.rotate(this._startPos, angle);
                }
                entityArr.push(_text);
            }
        }
        return entityArr
    }
    private getTextPos() {
        const { startPoint, endPoint } = this.getDirection();
        const vec = endPoint.sub(startPoint).normalize();
        const pointArr = [startPoint, endPoint];
        pointArr.forEach((point, index) => {
            if (index === 1) vec.negate();
            const _vec = index === 0 ? vec.clone().perpVector().mult(this.sliceLength / 2) : vec.negate().clone().perpVector().mult(this.sliceLength / 2);
            if (this._sectionDirection) _vec.negate();
            let pos: McGePoint3d = new McGePoint3d();
            if (this._cutStyle === cutStyle.crossSection) {
                pos = point.clone().subvec(_vec.clone().normalize().mult(this.height * 2));
            } else if (this._cutStyle === cutStyle.sectionCutting) {
                pos = point.clone().subvec(_vec.clone().normalize().mult(this.height));
            }
            index === 0 ? this.startPos = pos : this.endPos = pos;
            if (this._isShowSec) {
                const pt2 = point.clone().subvec(_vec);
                const _pos = this.getDrawTextPos(vec, pt2, index, point)
                index === 0 ? this._startPos = _pos : this._endPos = _pos;
            }
        })
    }
    private getDirection(): { startPoint: McGePoint3d, endPoint: McGePoint3d } {
        let startPoint, endPoint;
        if (this._startPoint.x < this._endPoint.x) {
            startPoint = this._startPoint.clone();
            endPoint = this._endPoint.clone();
        } else {
            startPoint = this._endPoint.clone()
            endPoint = this._startPoint.clone();
        }
        return { startPoint, endPoint };
    }
    private getDrawTextPos(vec, pt2, index, point) {
        const _v = vec.clone().perpVector().normalize();
        if (this._sectionDirection) _v.negate()
        let _textPos = new McGePoint3d()
        if (index === 0) {
            if (this._markDirect === markDirect.positionLine) {
                if (this._cutStyle === cutStyle.crossSection) {
                    _textPos = pt2.clone().subvec(vec.clone().mult(this.height));
                } else if (this._cutStyle === cutStyle.sectionCutting) {
                    _textPos = point.clone().addvec(_v.mult(this.height * (1 / 3)));
                    _textPos.addvec(vec.clone().mult(this.height * (1 / 3)))
                }
            } else if (this._markDirect === markDirect.sectionLine) {
                _textPos = point.clone().addvec(vec.clone().mult(this.sliceLength));
                _textPos.addvec(_v.clone().mult(this.height));
            }
            return _textPos
        }
        if (index === 1) {
            if (this._markDirect === markDirect.positionLine) {
                if (this._cutStyle === cutStyle.crossSection) {
                    _textPos = pt2.clone().addvec(vec.clone().normalize().mult(this.height * (1 / 3)));
                } else if (this._cutStyle === cutStyle.sectionCutting) {
                    _textPos = point.clone().addvec(_v.mult(this.height * (1 / 3)));
                    _textPos.subvec(vec.clone().mult(this.height))
                }
            } else if (this._markDirect === markDirect.sectionLine) {
                _textPos = point.clone().addvec(vec.clone().negate().mult(this.sliceLength));
                _textPos.addvec(_v.clone().mult(this.height))
            }
            return _textPos
        }
    }
    // 绘制实体 
    public worldDraw(draw: MxCADWorldDraw): void {
        const allEntityArr: McDbEntity[] = [];
        this.drawSliceIcon().forEach(entity => {
            draw.drawEntity(entity);
            allEntityArr.push(entity)
        });
        this.getBox(allEntityArr);
    }
    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbText) {
                const ent = new McDbMText()
                ent.contents = entity.textString;
                ent.textHeight = entity.height;
                ent.location = entity.position;
                switch (entity.horizontalMode) {
                    case McDb.TextHorzMode.kTextLeft:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                    case McDb.TextHorzMode.kTextCenter:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    case McDb.TextHorzMode.kTextRight:
                        ent.attachment = McDb.AttachmentPoint.kBottomRight;
                        break;
                    case McDb.TextHorzMode.kTextMid:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    default:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                }
                // 将ent的文字样式设置为当前文字样式
                const textStyleId = mxcad.getDatabase().getCurrentlyTextStyleId();
                ent.textStyleId = textStyleId;
                ent.reCompute();
                result = MxCADUtility.getTextEntityBox(ent, false);
            } else if (entity instanceof McDbMText) {
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
};

export async function drawSliceSymbol(cutNum: string, cutStyle: number, isShowSec: boolean, markPos: number, markDirect: number, secNum: string) {
    const slice = new McDbTestSliceSymbol();
    slice.cutStyle = cutStyle;
    slice.cutNum = cutNum;
    slice.isShowSec = isShowSec;
    slice.markPos = markPos;
    slice.markDirect = markDirect;
    slice.secNum = secNum;
    const getPoint1 = new MxCADUiPrPoint();
    getPoint1.setMessage(`\n${t('请指定第一个剖切点')}:`);
    const point1 = await getPoint1.go();
    if (!point1) return;
    slice.startPoint = point1;

    const getPoint2 = new MxCADUiPrPoint();
    getPoint2.setMessage(`\n${t('请指定第二个剖切点')}:`);
    getPoint2.setUserDraw((pt, pw) => {
        const _clone = slice.clone() as McDbTestSliceSymbol;
        _clone.endPoint = pt;
        pw.drawMcDbEntity(_clone)
    })
    const point2 = await getPoint2.go();
    if (!point2) return;
    slice.endPoint = point2;
    const getPoint3 = new MxCADUiPrPoint();
    getPoint3.setMessage(`\n${t('请指定剖视方向')}:`);
    getPoint3.setUserDraw((pt, pw) => {
        const _clone = slice.clone() as McDbTestSliceSymbol;
        const fx = (pt.x - point1.x) * (point2.y - point1.y) - (pt.y - point1.y) * (point2.x - point1.x);
        if (fx < 0) _clone.sectionDirection = !_clone.sectionDirection;
        if (point2.x < point1.x) {
            _clone.sectionDirection = !_clone.sectionDirection;
        }
        pw.drawMcDbEntity(_clone)
    })
    const point3 = await getPoint3.go();
    if (!point3) return;
    const fx = (point3.x - point1.x) * (point2.y - point1.y) - (point3.y - point1.y) * (point2.x - point1.x);
    if (fx < 0) slice.sectionDirection = !slice.sectionDirection;
    if (point2.x < point1.x) {
        slice.sectionDirection = !slice.sectionDirection;
    }
    const mxcad = MxCpp.getCurrentMxCAD();
    mxcad.drawEntity(slice);
}
