<template>
    <div class="content" v-show="!isShow">
        <div style="display:flex;align-items: center;margin-bottom: 10px;">
            <span>{{ t('轴线样式：') }}</span>
            <div class="radio" v-for="item in typeArr" :key="item.id" style="margin:0 10px;">
                <input type="radio" :id="item.name" v-model="dataForm.axisStyle" :value="item.id" />
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
            <button class="btn_normal" @click="sureDraw" style="margin: 5px 0px 5px;color: #fff!important;">{{
                t('确定') }}</button>
        </div>
    </div>
    <div class="content" v-show="isShow">
        <div style="display:flex;align-items: center;margin-bottom: 10px;">
            <div class="radio" v-for="item in direactArr" :key="item.id" style="margin:0 10px;">
                <input type="radio" :id="item.name" v-model="dataForm.direct" :value="item.id" />
                <label :for="item.name">{{ item.name }}</label>
            </div>
            <button class="btn" @click="clearAll">{{
                t('清空') }}</button>
        </div>
        <div style="width: 100%; height: 100% ;display: flex;justify-content: center;align-items: center;">
            <table>
                <tr>
                    <th>{{ t('个数') }}</th>
                    <th>{{ t('间距') }}</th>
                    <th>{{ t('操作') }}</th>
                </tr>
                <tr v-for="(item, index) in dataArr[dataForm.direct]" :key="index">
                    <td>
                        <input type="number" v-model="item.num" @focus="() => { setCommandFocus(false) }"
                            @blur="() => { setCommandFocus(true); }" min="1">
                    </td>
                    <td>
                        <input type="number" v-model="item.interval" @focus="() => { setCommandFocus(false) }"
                            @blur="() => { setCommandFocus(true); }" min="0">
                    </td>
                    <td>
                        <div class="options">
                            <span @click="optionList(index, 1)">{{ t('添加') }}</span>
                            <span v-show="dataArr[dataForm.direct].length > 1" @click="optionList(index, -1)">{{ t('删除') }}</span>
                        </div>
                    </td>
                </tr>
            </table>
        </div>
        <div style="display: flex;align-items: center;margin: 20px 0px 10px;">
            <input type="checkbox" id="al" value="al" v-model="dataForm.isAddLabel">
            <span>{{ t('添加标注') }}</span>
        </div>
        <div style="display: flex;justify-content: space-between;align-items: center;" v-show="dataForm.isAddLabel">
            <div class="inputBox">
                <span>{{ `${t('文字大小')}：` }}</span>
                <input type="number" v-model="dataInfo.fontSizel" @focus="() => { setCommandFocus(false) }"
                    :class="`${theme}`"
                    @blur="() => { setCommandFocus(true); }" style="width: 100px;" min="0" step="0.01"/>
            </div>
            <div class="inputBox">
                <span>{{ `${t('箭头大小')}：` }}</span>
                <input type="number" v-model="dataInfo.arrawSize" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" style="width: 100px;" min="0" step="0.01"/>
            </div>
        </div>
        <div style="display: flex;align-items: center;margin-bottom: 10px;" >
            <input type="checkbox" id="ai" value="ai" v-model="dataForm.isAddId">
            <span>{{ t('添加编号') }}</span>
        </div>
        <div style="display: flex;justify-content: space-between;align-items: center;margin-bottom: 10px;" v-show="dataForm.isAddId">
            <div class="inputBox">
                <span>{{ `${t('文字大小')}：` }}</span>
                <input type="number" v-model="dataInfo.fontSizeI" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" style="width: 100px;" min="0"/>
            </div>
            <div class="inputBox">
                <span>{{ `${t('圆框半径')}：` }}</span>
                <input type="number" v-model="dataInfo.radius" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" style="width: 100px;" min="0"/>
            </div>
        </div>
        <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
            <button class="btn_normal btn_back" @click="()=>isShow = false" style="margin: 5px 10px 5px;color: #fff!important;">{{
                t('返回') }}</button>
            <button class="btn_normal" @click="_sureDraw" style="margin: 5px 0px 5px;color: #fff!important;">{{
                t('确定') }}</button>
        </div>
    </div>
</template>

<script setup lang="ts">
import { reactive, ref } from 'vue';
import { t } from '../../i18n';
import { axisStyle, drawSingleAxis, drawAxisGrid } from "./hooks";
import { isOpen } from "../../index";

const theme = MxPluginContext.useTheme().isDark? 'dark' : 'light';

enum direact {
    left, right, up, down
}

const isShow = ref(false);
const dataForm: any = reactive({
    axisStyle: axisStyle.SingleAxis,
    direct: direact.left,
    isAddLabel: true,
    isAddId: true
});
const { store } = MxPluginContext;
const { setCommandFocus } = store.useFocus();

const typeArr = reactive([
    { name: t('单条轴线'), id: axisStyle.SingleAxis },
    { name: t('轴线网格'), id: axisStyle.AxisGrid }
]);

const direactArr = reactive([
    { name: t('左边'), id: direact.left },
    { name: t('右边'), id: direact.right },
    { name: t('上边'), id: direact.up },
    { name: t('下边'), id: direact.down }
]);
const dataArr = reactive({
    0: [{ num: 1, interval: 0 }],
    1: [{ num: 1, interval: 0 }],
    2: [{ num: 1, interval: 0 }],
    3: [{ num: 1, interval: 0 }]
});

const dataInfo = reactive({
    fontSizel: 0.15,
    arrawSize: 0.1,
    fontSizeI: 35,
    radius: 35
})
// 确定绘制轴线
const sureDraw = async () => {
    if (dataForm.axisStyle === axisStyle.SingleAxis) {
        isOpen.value = false;
        drawSingleAxis()
    } else if (dataForm.axisStyle === axisStyle.AxisGrid) {
        isShow.value = true;
    }
}

const _sureDraw = () => {
    isOpen.value = false;
    isShow.value = false;
    drawAxisGrid(dataArr, dataForm.isAddLabel, dataForm.isAddId, dataInfo);
}

// 清空
const clearAll = () => {
    dataArr[dataForm.direct] = [];
    dataArr[dataForm.direct].push({ num: 1, interval: 0 })
}
// 操作列表
const optionList = (index, num) => {
    if (num > 0) {
        dataArr[dataForm.direct].splice(index + 1, 0, { num: 1, interval: 0 })
    } else {
        dataArr[dataForm.direct].splice(index, 1)
    }
}

</script>

<style lang="scss" scoped>
@import '../../assets/dialogContens.scss';

.btn {
    padding: 0px 20px;
    font-size: var(--mx-font-size);
    border-radius: 4px;
    color: rgb(var(--v-theme-on-undertint-bg)) !important;
    background-color: rgb(var(--v-theme-undertint-bg));
    height: 25px;
    display: flex;
    align-items: center;
    border: 1.5px solid transparent;
    margin-left: 15px;

    &:hover {
        background-color: rgb(var(--v-theme-btn-hover));
        border: 1.5px solid rgba(0, 0, 0, var(--v-border-opacity));
    }
}
.btn_back{
    color: rgb(var(--v-theme-on-undertint-bg)) !important;
    background-color: rgb(var(--v-theme-undertint-bg)); 
    border: 1.5px solid transparent;
    &:hover {
        background-color: rgb(var(--v-theme-btn-hover));
        border: 1.5px solid rgba(0, 0, 0, var(--v-border-opacity));
    }
}
table {
    border-collapse: collapse;
    margin: 10px 10px 0px;
    background-color: rgb(var(--v-theme-undertint-bg));
}

th,
td {
    border: 1px solid rgb(var(--v-theme-on-undertint-bg)) !important;
    padding: 4px;
    vertical-align: middle;
    line-height: 20px;
    height: 20px;
    text-align: center;
    font-size: var(--mx-font-size);

    input {
        height: 20px;
        line-height: 20px;
        vertical-align: middle;
        padding: 0 10px;
        text-align: center;
        width: 80px;
        outline: none;
    }
}

.options {
    height: 100%;
    width: 100px;
    display: flex;
    justify-content: space-around;

    span {
        text-decoration: underline;
        cursor: pointer;

        &:hover {
            color: #178fe6;
        }
    }
}
</style>