import { t } from "../i18n";
import { init } from './dialog/index'
import { dialogComponentName, dialog } from './dialog/dialog';
import { init as initExtools } from './extoolsFun/fitTools';
import { init as initBlockTools } from './extoolsFun/blockTools';
import { init as initTextTools } from './extoolsFun/textTools';
import { init as initAnnotationsTools } from './extoolsFun/annotationsTools';
import { init as initDrawTools } from './extoolsFun/drawTools';
import '../icon/extoolsIcon.js'
import { reactive } from "vue";
import { Mx_MatchProperties } from "./MatchProperties";
import { i18nScope } from "../languages";

// 显示扩展工具弹框
async function Mx_ShowMoreExtools() {
    dialogComponentName.value = 'ExtoolsDialog';
    dialog.showDialog(true, { title: t("扩展工具") });
}

init();
initExtools();
initBlockTools();
initTextTools();
initAnnotationsTools();
initDrawTools();
MxPluginContext.addCommand('Mx_ShowMoreExtools', Mx_ShowMoreExtools);


// 匹配特性
export interface PropertiesForm {
    name: string;
    key: string;
    value: string;
    isChecked: boolean;
    isReadOnly: boolean;
    entProperty?: any;
    color?: string;
}

// 基本特性
export const baseProperties = reactive<PropertiesForm[]>([
    { name: t("颜色(C)"), key: 'C', value: 'trueColor', isChecked: true, isReadOnly: false },
    { name: t('图层(L)'), key: 'L', value: 'layer', isChecked: true, isReadOnly: false },
    { name: t('线型(Y)'), key: 'Y', value: 'linetype', isChecked: true, isReadOnly: false },
    { name: t('线型比例(I)'), key: 'I', value: 'linetypeScale', isChecked: true, isReadOnly: false },
    { name: t('线宽(W)'), key: 'W', value: 'lineweight', isChecked: true, isReadOnly: false },
    { name: t('透明度(R)'), key: 'R', value: 'n', isChecked: true, isReadOnly: false },
    { name: t('厚度(T)'), key: 'T', value: '', isReadOnly: true, isChecked: false },
    { name: t('打印样式(S)'), key: 'S', value: '', isReadOnly: true, isChecked: false },
]);
// 特殊特性
export const specialProperties = reactive<PropertiesForm[]>([
    { name: t('标注(D)'), key: 'D', value: 'DIM', isChecked: true, isReadOnly: false },
    { name: t('文字(X)'), key: 'X', value: 'TEXT', isChecked: true, isReadOnly: false },
    { name: t('图案填充(H)'), key: 'H', value: 'HATCH', isChecked: true, isReadOnly: false },
    { name: t('多段线(P)'), key: 'P', value: 'PL', isChecked: true, isReadOnly: false },
    { name: t('视口(V)'), key: 'V', value: '', isReadOnly: true, isChecked: false },
    { name: t('表格(B)'), key: 'B', value: '', isReadOnly: true, isChecked: false },
    { name: t('材质(M)'), key: 'M', value: '', isReadOnly: true, isChecked: false },
    { name: t('阴影显示(O)'), key: 'O', value: '', isReadOnly: true, isChecked: false },
    { name: t('多重引线(U)'), key: 'U', value: '', isReadOnly: true, isChecked: false },
]);

MxPluginContext.addCommand('Mx_MatchProperties', Mx_MatchProperties);
setTimeout(async () => {
    const uiConfig = await MxPluginContext.getUiConfig()

    const data = {
        isResponsive: true,
        "icon": "texingpipei",
        "prompt": t("特性匹配"),
        "cmd": "Mx_MatchProperties"
    }
    i18nScope.on("change", () => {
        data.prompt = t("特性匹配")
    })
    uiConfig.mTopButtonBarData?.push(data)

}, 500);