
import { DxfCode,MxCADResbuf, MxCADSelectionSet} from "mxcad";
import { reactive, ref } from "vue";
import { SearchSamePattern } from './searchSamePattern';
import { t } from "../../i18n";

export const isShowPatternRec = ref(false);

export const patternImgUrl = ref(null);

export const patternList = reactive([]);

export let searchPattern: SearchSamePattern;

/** 获取需要识别的图形 */
export const getIdentificationPattern = async () => {
    // 阻止浏览器点击右键默认行为
    document.addEventListener('contextmenu', function (e) { e.preventDefault(); });
    // 获取需要识别的图形
    const ss = new MxCADSelectionSet();// 选择集
    const filter = new MxCADResbuf([DxfCode.kEntityType, "LINE,ARC,CIRCLE,LWPOLYLINE,INSERT"])
    if (!await ss.userSelect(`${t('请点击左键或拉框选择需要识别的图形')}:`, filter)) return;
    if (ss.count() == 0) return;
    // 搜索图形
    searchPattern = new SearchSamePattern();
    patternImgUrl.value = await searchPattern.setSelectset(ss);
    if (!patternImgUrl.value) return;
    isShowPatternRec.value = true;
}









