import { init } from './hooks';
import './dialog/index'
import { t, i18nScope } from '../languages';
// 原始数据
const originalData = {
  isResponsive: true,
  tab: [t("组")],
  icon: "zu",
  list: [
    {
      tab: t("创建组"),
      icon: "chuangjianzu",
      cmd: "Mx_Group"
    },
    {
      tab: t("解除编组"),
      icon: "jiezu",
      cmd: "Mx_Ungroup"
    },
    {
      tab: t("编辑组"),
      icon: "bianjizu",
      cmd: "Mx_EditGroup"
    },
    {
      tab: t("编组管理器"),
      icon: "zuguanli",
      cmd: "Mx_Classicgroup"
    },
  ]
}

setTimeout(async () => {
  MxPluginContext.getUiConfig().then((uiConfig) => {
    const data = JSON.parse(JSON.stringify(originalData));
    data.tab = t(data.tab[0]);
    data.list.forEach((item) => {
      item.tab = t(item.tab);
    });
    const dataClone = JSON.parse(JSON.stringify(data));
    i18nScope.on("change", () => {
      dataClone.tab = t(originalData.tab[0]);
      dataClone.list.forEach((item, index) => {
        item.tab = t(originalData.list[index].tab);
      });
      data.tab = dataClone.tab
      data.list.forEach((item, index) => {
        item.tab = dataClone.list[index].tab
      });
    })
    uiConfig.mMenuBarData.forEach(item => {
      if (item.tab === "工具(A)") {
        item.list.push(data as any)
      }
    })
  });
  MxPluginContext.getSketchAndAnnotationUiConfig().then((uiConfig) => {
    const setData = () => {
      const data = {
        isResponsive: true,
        "tab": t("组"),
        "list": [
          {
            "tab": t("创建组"),
            "icon": "chuangjianzu",
            "cmd": "Mx_Group",
            "size": "large",
            "labelWithArrowLayout": true,
            "col": true,
            "isShowLabel": true,
            "isShowToMainPanel": true,
            "props": {
              "style": {
                "width": "68px"
              }
            }
          },
          {
            "type": "group",
            "props": {
              "class": "d-flex flex-column"
            },
            "isShowToMainPanel": true,
            "list": [
              {
                "icon": "jiezu",
                "cmd": "Mx_Ungroup",
                "tab": t("解除编组"),
                "isShowLabel": true
              },
              {
                "icon": "bianjizu",
                "cmd": "Mx_EditGroup",
                "tab": t("编辑组"),
                "isShowLabel": true,
                "isShowToMainPanel": true
              },
              {
                "icon": "zuguanli",
                "cmd": "Mx_Classicgroup",
                "tab": t("编组管理器"),
                "isShowLabel": true
              }
            ]
          }
        ]
      }
      const num = uiConfig?.mMenuData[0]?.list?.length || 0;
      if (num === 5) {
        uiConfig?.mMenuData[0]?.list.splice(num - 1, 0, data);
      } else if (num === 6) {
        uiConfig?.mMenuData[0]?.list.splice(num - 2, 1, data);
      };
    }
    setData();
    i18nScope.on("change", () => {
      setData();
    });
  })
}, 500);

init();

