<template>
    <div class="compareList" v-if="isShowCompare">
      <div style="display: flex; align-items: center; justify-content: center;cursor: pointer;margin-right: 10px;" @click="showSet = !showSet">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-shezhi"></use>
        </svg>
        <span>{{ t('DWG比对') }}</span>
      </div>
      <svg class="icon" aria-hidden="true" @click="() => changeAllStatus()" v-if="status === false" style="cursor: pointer;margin-right: 10px;">
        <use xlink:href="#icon-dengpao"></use>
      </svg>
      <svg class="icon" aria-hidden="true" @click="() => changeAllStatus()" v-else style="cursor: pointer;margin-right: 10px;">
        <use xlink:href="#icon-dengpao1"></use>
      </svg>
      <svg class="icon" aria-hidden="true" @click="complete" style="cursor: pointer;">
        <use xlink:href="#icon-duigou"></use>
      </svg>
      <div class="modal" ref="modal" @mousedown="dragModal" v-show="showSet">
        <div style="
            display: flex;
            justify-content: space-between;
            align-items: center;
          ">
          <span>{{ t('区别') }}</span>
          <svg class="icon" aria-hidden="true" @click="isFixed = false" v-if="isFixed" style="cursor: pointer;">
            <use xlink:href="#icon-guding"></use>
          </svg>
          <svg class="icon" aria-hidden="true" @click="isFixed = true" v-else style="cursor: pointer;">
            <use xlink:href="#icon-guding1"></use>
          </svg>
        </div>
        <div class="fcb" v-for="(item, index) in camResult" :key="item.id">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-dengpao1"></use>
          </svg>
          <span style="display: flex;width: 60%;"> {{item.name}}({{ item.arr.length }} {{ t('个') }})</span>
        </div>
        <div class="camList">
          <button class="btn" v-for="item in camResult" :key="item.type" :style="code === item.type
              ? 'background-color:rgb(var(--v-theme-btn-hover))'
              : ''
            " @click="code = item.type">
            {{ item.name }}
          </button>
        </div>
        <div class="listBox">
          <ul v-if="camResult.length > 0">
            <li style="position: sticky; top: 0px; z-index: 10">
              <span>{{ t('实体名称') }}</span>
              <span>{{ t('实体位置') }}</span>
            </li>
            <li v-for="res in showList" :key="res.id" @click="showDef(res)">
              <span>{{ res.name }}</span>
              <div style="width: 10px;"></div>
              <div>
                <p>X:{{ res.pos.x.toFixed(4) }}</p>
                <p>Y:{{ res.pos.y.toFixed(4) }}</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </template>
  
  <script setup lang="ts">
  import { ref, watch, defineEmits, reactive, computed } from "vue";
  import {
    MxCpp,
    MxCompare,
    McDbEntity,
  } from "mxcad";
  import { MxFun } from "mxdraw";
  import { isShowCompare, file_path} from "../index";
  import { t } from "../i18n"

  const showSet = ref(true); // 显示设置界面
  const isFixed = ref(false); // 是都固定设置界面
  const emit = defineEmits(["update-path"]);
  const status = ref(true); // 是否处于比对状态
  const code = ref(2); // 比对结果列表序号
  const camResult = reactive([]); // 定义比对结果数组
  const modal = ref(null); // 设置窗口
  
  watch(
    () => file_path.value,
    (newValue, oldValue) => {
      // 如果之前以及存在图纸先关闭背景图
      if (oldValue) {
        const mxcad = MxCpp.getCurrentMxCAD();
        mxcad.mxdraw.makeCurrent();
  
        //清除比对背景
        mxcad.loadDwgBackground(oldValue, (isok: boolean) => {
          let backgroundEntity = mxcad.getBackgroundEntity();
          // 清除背景图纸的显示。
          backgroundEntity.clearAll();
        });
      }
      // 如果拿到了比对图纸的路径，则进行图纸比对
      if (newValue) {
        camResult.length = 0; // 清空比对结果数组
        Mx_MxCompareImp(newValue); // 比对图纸
      }
    },
    { immediate: true }
  );
  
  // 计算展示列表
  const showList = computed(() => {
    const res = camResult.filter((i) => i.type === code.value);
    return res[0].arr;
  });
  
  // 比对完成
  const complete = () => {
    const mxcad = MxCpp.getCurrentMxCAD();
    const mxdraw = MxFun.getCurrentDraw();
    mxcad.mxdraw.makeCurrent();
    //清除比对背景
    mxcad.loadDwgBackground(file_path.value, (isok: boolean) => {
      let backgroundEntity = mxcad.getBackgroundEntity();
      // 清除背景图纸的显示。
      backgroundEntity.clearAll();
      emit("update-path", "");
    });
    mxdraw.getTempMarkDraw().clear();
    isShowCompare.value = false;
    status.value = true;
  };
  
  // 比对图纸
  async function Mx_MxCompareImp(_fileUrl: string) {
    const fileUrl = _fileUrl.replace('dwgmxweb', 'dwg.mxweb');
    // 把要比较的图纸，当着背景加载当前内存中，方便查看不一样的地方.
    const mxcad = MxCpp.getCurrentMxCAD();
    mxcad.mxdraw.makeCurrent();
    mxcad.loadDwgBackground(
      fileUrl,
      async (isok: boolean) => {
        let backgroundEntity = mxcad.getBackgroundEntity();
        // 暂时隐藏背景图纸的显示。
        backgroundEntity.setShow(fileUrl, false);
        let database = backgroundEntity.getBackgroundDatabase(fileUrl);
        if (database == null) {
          return;
        }
        // 开始把当前图纸与背景图纸进行比较。
        let cmp = new MxCompare();
        if (cmp.do(database)) {
          // 得到比较结果.
          let result = cmp.getResult();
          console.log('比对结果：', JSON.stringify(result));
          backgroundEntity.setShow(fileUrl, true);
          camResult.push(
            {
              type: 2,
              name: t("已修改"),
              arr: [],
            },
            {
              type: 3,
              name: t("已删除"),
              arr: [],
            },
            {
              type: 4,
              name: t("已添加"),
              arr: [],
            }
          );
          result.forEach((item) => {
            const ent = item.id.getMcDbEntity() as McDbEntity;
            item.name = ent.objectName;
            if (item.type === 2) {
              camResult[0].arr.push(item); // 修改
            } else if (item.type === 3) {
              camResult[1].arr.push(item); // 删除
            } else if (item.type === 4) {
              camResult[2].arr.push(item); // 添加
            }
          });
          isShowCompare.value = true;
        }
      },
      0xc90696969
    );
  }
  
  // 移动设置窗口
  const dragModal = (event) => {
    const dragBox = modal.value;
    if (isFixed.value) return;
    const disX = event.clientX - dragBox.offsetLeft;
    const disY = event.clientY - dragBox.offsetTop;
  
    const moveDrag = (event) => {
      dragBox.style.left = event.clientX - disX + "px";
      dragBox.style.top = event.clientY - disY + "px";
    };
  
    document.addEventListener("mousemove", moveDrag);
    document.addEventListener("mouseup", () => {
      document.removeEventListener("mousemove", moveDrag);
    });
  };
  
  // 修改全局比对状态
  const changeAllStatus = () => {
    status.value = !status.value;
    // 关闭底图
    const mxcad = MxCpp.getCurrentMxCAD();
    mxcad.mxdraw.makeCurrent();
  
    //清除比对背景
    mxcad.loadDwgBackground(file_path.value, (isok: boolean) => {
      let backgroundEntity = mxcad.getBackgroundEntity();
      // 暂时隐藏背景图纸的显示。
      backgroundEntity.setShow(file_path.value, status.value);
    });
  };
  
  // 在界面标注不同点
  const showDef = (res) => {
    const mxdraw = MxFun.getCurrentDraw();
    const mxcad = MxCpp.getCurrentMxCAD();
    mxdraw.getTempMarkDraw().clear();
    mxdraw
      .getTempMarkDraw()
      .drawCircleMark(
        res.pos.toVector3(),
        mxdraw.viewCoordLong2Cad(50),
        0xFF0000
      );
    mxdraw.updateDisplay();
    mxcad.zoomCenter(res.pos.x, res.pos.y);
  };
  </script>
  
  <style lang="scss" scoped>
  .compareList {
    background-color: rgb(var(--v-theme-prominent));
    position: fixed;
    z-index: 9999;
    top: 150px;
    left: 50%;
    translate:(-50%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 5px;
    padding: 5px;
    font-size: var(--mx-font-size);
  
    .icon {
      fill: #89cbfa;
      height: 1.8em;
      width: 1.8em;
    }
  }
  
  .modal {
    position: absolute;
    padding: 10px;
    top: 55px;
    left: -45px;
    background-color: rgb(var(--v-theme-prominent));
  }
  
  .fcb {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
    margin-right: 30px;
  }
  
  .camList {
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-bottom: 1px solid #ccc;
    margin-top: 15px;
    padding-right: 10px;
  
    .btn {
      padding: 2px 4px;
      border: 1px solid #ccc;
      border-bottom: 0px;
      width: 50px;
      margin: 0px 5px;
    }
  }
  
  .listBox {
    max-height: 300px;
    overflow-y: auto;
  
    ul {
      list-style: none;
      display: block;
  
      li {
        padding: 5px 5px 5px 0px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid rgba($color: #ccc, $alpha: 0.7);
        background-color: rgb(var(--v-theme-prominent));
  
        &:hover {
          background-color: rgb(var(--v-theme-btn-hover));
        }
      }
    }
  }
  </style>