import { McDbLine, MxCADUiPrDist, MxCADUiPrPoint, McGePoint3d, McCmColor, MxCpp, McGeVector3d, McDb2LineAngularDimension, McDbAlignedDimension, McDb, McDbCircle, McDbText, McDbEntity } from "mxcad";
import { t } from '../../i18n';
import { MxFun } from "mxdraw";

export enum axisStyle {
    // 单条轴线
    SingleAxis,
    // 轴线网格
    AxisGrid
}
// 绘单条轴线
export async function drawSingleAxis() {
    while (true) {
        const getStartPt = new MxCADUiPrPoint();
        getStartPt.setMessage(`\n${t('指定轴线起点')}:`);
        const startPt = await getStartPt.go();
        if (!startPt) return;
        const getDist = new MxCADUiPrDist();
        getDist.setBasePt(startPt);
        getDist.setMessage(`\n${t('指定轴线终点或输入长度')}:`);

        const lineX = new McDbLine(startPt, new McGePoint3d(startPt.x + 1, startPt.y, 0));
        const lineY = new McDbLine(startPt, new McGePoint3d(startPt.x, startPt.y + 1, 0));
        let directPt: McGePoint3d = new McGePoint3d();
        const mxcad = MxCpp.getCurrentMxCAD();
        getDist.setUserDraw((pt, pw) => {
            const pt1 = lineX.getClosestPointTo(pt, true).val;
            const pt2 = lineY.getClosestPointTo(pt, true).val;
            let point: McGePoint3d;
            const distance = startPt.distanceTo(pt);
            if (pt1.distanceTo(pt) > pt2.distanceTo(pt)) {
                const v = pt2.sub(startPt).normalize().mult(distance);
                point = startPt.clone().addvec(v);
            } else {
                const v = pt1.sub(startPt).normalize().mult(distance);
                point = startPt.clone().addvec(v);
            }
            const line = new McDbLine(startPt, point);
            let id = mxcad.database.getLinetypeTable().get("DASHED");
            line.linetypeId = id;
            line.linetypeScale = MxFun.screenCoordLong2Doc(0.1);
            pw.setColor(0xFFFF00);
            pw.drawMcDbEntity(line);
            directPt = point;
        });
        const dist = await getDist.go();
        if (!dist) return;
        const v = directPt.sub(startPt).normalize().mult(dist);
        const endPt = startPt.clone().addvec(v);
        const line = new McDbLine(startPt, endPt);
        line.trueColor = new McCmColor(255, 0, 0);

        // 拿到当前控件的数据库图层表
        const layerTable = mxcad.getDatabase().getLayerTable();
        if (layerTable.has("A-AXIS_轴线")) {
            line.layerId = layerTable.get("A-AXIS_轴线");
        } else {
            const layerId = mxcad.addLayer("A-AXIS_轴线");
            line.layerId = layerId;
        }
        MxCpp.getCurrentMxCAD().drawEntity(line);
    }
}

// 轴线网类
class McDbAxisGrid {
    // 记录每一边的端点，再根据每一个端点绘制标注
    // 左右轴线的端点
    private leftPoints = [];
    private rightPoints = [];
    // 上下轴线的端点
    private upPoints = [];
    private downPoints = [];
    // 轴网数据数组
    public dataArr: any = [];
    // 是否添加标注
    public isAddLabel: boolean = true; 
    // 是否添加编号
    public isAddId: boolean = true;
    // 标注编号设置信息
    public dataInfo: any = { 
        fontSizel: 0.15,
        arrawSize: 0.1,
        fontSizeI: 35,
        radius: 35}
        // 轴线起点
    public point:McGePoint3d = new McGePoint3d(0,0,0);
    // 额外长度
    public len = MxFun.screenCoordLong2Doc(20);
    // 绘制轴线网
    public getAxiosLine(basePt: McGePoint3d, dataArr: any): McDbLine[]{
        this.leftPoints.length = this.rightPoints.length = this.upPoints.length = this.downPoints.length = 0;
        // 轴线集合
        const lineArr: McDbLine[] = [];
        // 左右上下长度集合
        const lengthArr = [];
        // 左右轴线间距集合
        let hSpace = [];
        // 上下轴线间距集合
        let wSpace = [];

        for (let i = 0; i < 4; i++) {
            // 记录长度
            let sideLength: number = 0;
            // 重置格式
            const new_dataArr = JSON.parse(JSON.stringify(dataArr));
            dataArr[i].forEach((item, index) => {
                if (item.num > 1) {
                    const arr = []
                    for (let j = 0; j < item.num; j++) {
                        arr.push({ num: 1, interval: item.interval })
                    };
                    new_dataArr[i].splice(index, 1, ...arr)
                }
            });
            dataArr[i] = new_dataArr[i];
            // 去除空值
            dataArr[i] = dataArr[i].filter(item => {
                if (item.interval > 0) {
                    sideLength += item.interval * item.num;
                    item.relativeDist = sideLength;
                    (i === 0 || i === 1) ? hSpace.push(sideLength) : wSpace.push(sideLength);
                    return true
                }
            });
            lengthArr.push(sideLength);
        };

        /**
         * 左右长度：从下到上ABC
         * 上下长度：从左到右123
         * 首先确定方框：上下左右的总长
         **/

        const h = Math.max(...[lengthArr[0], lengthArr[1]]);
        const w = Math.max(...[lengthArr[2], lengthArr[3]]);

        const v = McGeVector3d.kXAxis.clone();
        const pt1 = basePt.clone().addvec(v.clone().mult(w));
        const _pt1 = basePt.clone().subvec(v.clone().mult(this.len));
        const line1 = new McDbLine(pt1.clone().addvec(v.clone().mult(this.len)), _pt1);
        lineArr.push(line1);

        const v1 = McGeVector3d.kYAxis.clone();
        const pt2 = basePt.clone().addvec(v1.clone().mult(h));
        const _pt2 = basePt.clone().subvec(v1.clone().mult(this.len));
        const line2 = new McDbLine(pt2.clone().addvec(v1.clone().mult(this.len)), _pt2);
        lineArr.push(line2);

        const line4 = line1.clone() as McDbLine
        line4.move(basePt, pt2);
        lineArr.push(line4);

        const line3 = line2.clone() as McDbLine
        line3.move(basePt, pt1);
        lineArr.push(line3);

        // 记录端点
        const allNumH = Array.from(new Set(hSpace)).length;
        const allNumW = Array.from(new Set(wSpace)).length;
        this.leftPoints.push({ point: basePt, index: String.fromCharCode(64 + 1) }, { point: pt2, index: String.fromCharCode(64 + allNumH + 1) });
        this.downPoints.push({ point: basePt, index: 1 }, { point: pt1, index: allNumW + 1 });
        this.rightPoints.push({ point: pt1, index: String.fromCharCode(64 + 1) }, { point: new McGePoint3d(pt1.x, pt2.y), index: String.fromCharCode(64 + allNumH + 1) });
        this.upPoints.push({ point: pt2, index: 1 }, { point: new McGePoint3d(pt1.x, pt2.y), index: allNumW + 1 });
        // 对间距进行排序，比较相对长度
        hSpace = Array.from(new Set(hSpace)).sort((a, b) => a - b);
        hSpace.forEach((dist, index) => {
            const v_y = McGeVector3d.kYAxis.clone();
            const v_x = McGeVector3d.kXAxis.clone();

            // left,right
            const arr1 = dataArr[0].filter(item => item.relativeDist === dist);
            const arr2 = dataArr[1].filter(item => item.relativeDist === dist);
            if (arr1.length > 0 && dist != h) {
                const pt = basePt.clone().addvec(v_y.clone().mult(dist));
                const startPt = pt.clone().addvec(v_x.clone().mult(w - this.len / 2));
                const endPt = pt.clone().subvec(v_x.clone().mult(this.len));
                const line = new McDbLine(startPt, endPt);
                lineArr.push(line);
                this.leftPoints.splice(this.leftPoints.length - 1, 0, { point: pt, index: String.fromCharCode(64 + index + 2) });
            }
            if (arr2.length > 0 && dist != h) {
                const pt = pt1.clone().addvec(v_y.clone().mult(dist));
                const startPt = pt.clone().addvec(v_x.clone().mult(this.len));
                const endPt = pt.clone().subvec(v_x.clone().mult(w - this.len / 2));
                const line = new McDbLine(startPt, endPt);
                lineArr.push(line);
                this.rightPoints.splice(this.rightPoints.length - 1, 0, { point: pt, index: String.fromCharCode(64 + index + 2) });
            }
        })
        wSpace = Array.from(new Set(wSpace)).sort((a, b) => a - b);
        wSpace.forEach((dist, index) => {
            const v_y = McGeVector3d.kYAxis.clone();
            const v_x = McGeVector3d.kXAxis.clone();

            // up, down
            const arr1 = dataArr[2].filter(item => item.relativeDist === dist);
            const arr2 = dataArr[3].filter(item => item.relativeDist === dist);
            if (arr1.length > 0 && dist != w) {
                const pt = pt2.clone().addvec(v_x.clone().mult(dist));
                const startPt = pt.clone().addvec(v_y.clone().mult(this.len));
                const endPt = pt.clone().subvec(v_y.clone().mult(h - this.len / 2));
                const line = new McDbLine(startPt, endPt);
                lineArr.push(line);
                this.upPoints.splice(this.upPoints.length - 1, 0, { point: pt, index: index + 2 });
            }
            if (arr2.length > 0 && dist != w) {
                const pt = basePt.clone().addvec(v_x.clone().mult(dist));
                const startPt = pt.clone().addvec(v_y.clone().mult(h - this.len / 2));
                const endPt = pt.clone().subvec(v_y.clone().mult(this.len));
                const line = new McDbLine(startPt, endPt);
                lineArr.push(line);
                this.downPoints.splice(this.downPoints.length - 1, 0, { point: pt, index: index + 2 });
            }
        });
        return lineArr;
    };
    // 绘制标注
    private drawLabel(): McDbAlignedDimension[]{
        const arr = [this.leftPoints.map(item => item.point), this.rightPoints.map(item => item.point), this.upPoints.map(item => item.point), this.downPoints.map(item => item.point)];
        if(!arr.length) return [];
        const dimArr = []
        arr.forEach((pointsArr, index) => {
            let v: McGeVector3d;
            switch (index) {
                case 0:
                    v = McGeVector3d.kXAxis.clone().negate().mult(this.len);
                    break;
                case 1:
                    v = McGeVector3d.kXAxis.clone().mult(this.len);
                    break;
                case 2:
                    v = McGeVector3d.kYAxis.clone().mult(this.len);
                    break;
                case 3:
                    v = McGeVector3d.kYAxis.clone().negate().mult(this.len);
                    break;
                default:
                    break;
            }
            const getDim = (point1: McGePoint3d, point2: McGePoint3d, index: Number): McDbAlignedDimension | boolean => {
                if (point1.distanceTo(point2) === 0) return false;
                const mDimension = new McDbAlignedDimension();
                const pt1 = point1.clone().addvec(v);
                const pt2 = point2.clone().addvec(v);
                mDimension.xLine1Point = pt1 // 设置对象第一条线的端点
                mDimension.xLine2Point = pt2 // 设置对象第二条线的端点
                const midPt = new McGePoint3d((pt1.x + pt2.x) / 2, (pt1.y + pt2.y) / 2);
                mDimension.dimLinePoint = index === 0 ? midPt.addvec(v.clone().normalize().mult((4 / 3) * this.len)) : midPt.addvec(v.clone().normalize().mult((2 / 3) * this.len)) // 设置对象标注线的端点
                mDimension.textAttachment = McDb.AttachmentPoint.kTopCenter // 设置对象文本对齐方式
                return mDimension;
            }
            pointsArr.forEach((pt, index) => {
                if (index > 0 && pointsArr.length > 2) {
                    const dim = getDim(pointsArr[index - 1], pt, index)
                    if (dim) dimArr.push(dim);
                }
                const dist = pointsArr[0].distanceTo(pointsArr[pointsArr.length - 1]);
                if (index === 0 && dist > 0) {
                    const dim = getDim(pointsArr[0], pointsArr[pointsArr.length - 1], index);
                    if (dim) dimArr.push(dim);
                }
            })
        });
        return dimArr
    }
    // 绘制编号
    private drawId (fontSizeI, radius): McDbEntity[]{
        const entityArr = [];
        const array = [this.leftPoints, this.rightPoints, this.upPoints, this.downPoints];
        const getEntity = (arr, v, index) => {
            if(!arr.length) return [];
            arr.forEach((item) => {
                const pt = item.point.clone().addvec(v.clone().mult(4 * this.len));
                const center = pt.clone().addvec(v.clone().mult(radius));
                const circle = new McDbCircle(center.x, center.y, center.z, radius);
                const line = new McDbLine(item.point.clone().addvec(v.clone().mult(this.len)), pt);
                const text = new McDbText();
                text.textString = item.index;
                text.height = fontSizeI;
                if (index === 0) {
                    text.position = text.alignmentPoint = center.addvec(v.clone().perpVector().mult(text.height * (2 / 5)))
                } else if (index === 1) {
                    text.position = text.alignmentPoint = center.subvec(v.clone().perpVector().mult(text.height * (2 / 5)));
                } else if (index === 2) {
                    text.position = text.alignmentPoint = center.subvec(v.clone().mult(text.height * (2 / 5)));
                } else if (index === 3) {
                    text.position = text.alignmentPoint = center.addvec(v.clone().mult(text.height * (2 / 5)));
                }
                text.horizontalMode = McDb.TextHorzMode.kTextCenter
                entityArr.push(circle, line, text)
            })
        }
        array.forEach((item, index) => {
            let v: McGeVector3d;
            switch (index) {
                case 0:
                    v = McGeVector3d.kXAxis.clone().negate();
                    break;
                case 1:
                    v = McGeVector3d.kXAxis.clone();
                    break;
                case 2:
                    v = McGeVector3d.kYAxis.clone();
                    break;
                case 3:
                    v = McGeVector3d.kYAxis.clone().negate();
                    break;
                default:
                    break;
            }
            getEntity(item, v, index)
        })
        return entityArr
    }
    // 绘制轴线网
    public drawAxisGrid() {
        const mxcad = MxCpp.getCurrentMxCAD();
        const layerName = mxcad.getDatabase().getCurrentlyLayerName();
    
        const layerTable = mxcad.getDatabase().getLayerTable();
    
        if (!layerTable.has("A-AXIS_轴线")) {
            mxcad.addLayer("A-AXIS_轴线");
        }
        mxcad.drawLayer = "A-AXIS_轴线";
        // 绘制轴线网
        this.getAxiosLine(this.point, this.dataArr).forEach(line => {
            line.trueColor = new McCmColor(255, 0, 0);
            mxcad.drawEntity(line);
        })
    
        if (!layerTable.has("PUB_DIM尺寸")) {
            mxcad.addLayer("PUB_DIM尺寸");
        }
        mxcad.drawLayer = "PUB_DIM尺寸";
        // 绘制标注
        if (this.isAddLabel) {
            this.drawLabel().forEach(dim => {
                /**
                 * 41:箭头大小
                 * 140：文字高度
                 */
                const dimStyle = mxcad.drawDimStyle;
                const styleName = `MyDimStyle-${this.dataInfo.fontSizel}-${this.dataInfo.arrawSize}`
                mxcad.addDimStyle(styleName, `41,${this.dataInfo.arrawSize},141,0.09,40,200,140,${this.dataInfo.fontSizel},145,-0.00001`, "77,0,271,0,178,3,176,3,177,3", "", "");
                mxcad.drawDimStyle = styleName;
                const pt1 = dim.xLine1Point;
                const pt2 = dim.xLine2Point;
                const pt3 = dim.dimLinePoint;
                const id = mxcad.drawDimAligned(pt1.x, pt1.y, pt2.x, pt2.y, pt3.x, pt3.y);
                id.getMcDbEntity().trueColor = new McCmColor(255, 0, 0);
                mxcad.updateDisplay();
                mxcad.drawDimStyle = dimStyle;
            })
        }
        // 绘制编号
        if (this.isAddId) {
            this.drawId(this.dataInfo.fontSizeI, this.dataInfo.radius).forEach(entity => {
                entity.trueColor = new McCmColor(0, 255, 0);
                mxcad.drawEntity(entity);
            })
        };
    
        mxcad.drawLayer = layerName;
    }
}
/**
 * 绘轴线网
 * @param _dataArr 轴网数据数组
 * @param isAddLabel 是否添加标注
 * @param isAddId 是否添加编号
 * @param dataInfo 标注编号设置信息
 * @returns 
 */
export async function drawAxisGrid(_dataArr: any, isAddLabel: boolean, isAddId: boolean, dataInfo: any) {
    const dataArr = JSON.parse(JSON.stringify(_dataArr));
    const axiosGrid = new McDbAxisGrid();
    axiosGrid.dataArr = dataArr;
    axiosGrid.isAddId = isAddId;
    axiosGrid.isAddLabel = isAddLabel;
    axiosGrid.dataInfo = dataInfo;
    const getPoint = new MxCADUiPrPoint();
    getPoint.setMessage(`\n${t('点击指定轴网位置')}:`);
    getPoint.setUserDraw((pt, pw) => {
        axiosGrid.len = MxFun.screenCoordLong2Doc(20);
        axiosGrid.getAxiosLine(pt, dataArr).forEach(line => {
            pw.drawMcDbEntity(line);
        })
    })
    const point = await getPoint.go();
    if (!point) return;
    axiosGrid.point = point;
    axiosGrid.drawAxisGrid();
}