<template>
  <div style="padding: 5px; position: relative">
    <div class="static">
      <span class="static-title">{{ t("标注颜色") }}</span>
      <div class="fc" style="width: 60%">
        <span>{{ t("未分类标注颜色：") }}</span>
        <v-select
          :items="colorList"
          v-model="formData.color"
          style="width: 20%"
        >
          <template #selection>
            <span
              :style="{ backgroundColor: formData.color }"
              class="color-box"
            ></span>
          </template>
          <template v-slot:item="{ item: items, props }">
            <v-list-item
              @click="
                () => {
                  props.onClick();
                }
              "
            >
              <span
                :style="{ backgroundColor: items.value }"
                class="color-box"
              ></span>
            </v-list-item>
          </template>
        </v-select>
      </div>
      <span class="fc" style="margin-top: 10px; color: red">{{
        t(
          "提示:这里只修改未分类标注的默认颜色，如果需要修改分类标注的颜色，可以在标注分类管理界面里修改"
        )
      }}</span>
    </div>
    <div class="static">
      <span class="static-title">{{ t("文字高度") }}</span>
      <div style="display: flex; align-items: flex-end">
        <v-radio-group v-model="heightType" @change="modelChange">
          <v-radio :label="t('自动')" value="auto" />
          <v-radio :label="t('手动设置')" value="manual" />
        </v-radio-group>
        <div class="fc" style="width: 60%">
          <v-text-field
            v-model="formData.textHeight"
            type="number"
            placeholder="0.00"
            :disabled="heightType === 'auto'"
          />
          <v-btn
            class="ml-2"
            :disabled="heightType === 'auto'"
            @click="setTextHeight"
            ><span class="custom-ellipsis">{{ t("在屏幕中指定") }}</span></v-btn
          >
        </div>
      </div>
      <span class="fc" style="margin-top: 10px">{{
        t("(以上设置修改后，对新添加的标注生效)")
      }}</span>
    </div>

    <div class="static">
      <span class="static-title">{{ t("显示与精度设置") }}</span>
      <!-- Tab -->
      <v-tabs
        v-model="tabValue"
        class="mt-2 mb-2"
        bg-color="rgb(var(--v-theme-prominent))"
        @update:model-value="setData"
      >
        <v-tab
          v-for="item in tabList"
          :value="item.value"
          :key="item.name"
          :class="item.value === tabValue ? 'activeTab' : ''"
          >{{ item.name }}</v-tab
        >
      </v-tabs>
      <v-radio-group v-model="settingData.showType" @change="modelChange">
        <v-radio :label="t('按实际数值显示')" :value="ShowType.ActualValue" />
        <v-radio
          :label="`${t('图纸单位为mm,')}${
            tabList.find((item) => item.value === tabValue).name
          }${t('数值按照')}${
            tabList.find((item) => item.value === tabValue).name === DimType.Area ? 'm²' : 'm'
          }${t('显示')}`"
          :value="ShowType.SquareMeter"
        />
      </v-radio-group>
      <div class="fc" style="margin-top: 5px; width: 50%">
        <span>{{ t("显示精度：") }}</span>
        <v-select
          :items="accList"
          v-model="settingData.fixedNum"
          item-title="text"
          item-value="value"
          @update:model-value="modelChange"
        >
        </v-select>
      </div>
    </div>
    <v-btn class="save_btn" color="primary" @click="saveData">{{
      t("保存")
    }}</v-btn>
  </div>
</template>

<script setup lang="ts">
import { reactive, ref, onMounted, computed } from "vue";
import {
  VSelect,
  VBtn,
  VTab,
  VTabs,
  VRadioGroup,
  VRadio,
  VTextField,
  VListItem,
} from "vuetify/components";
import {
  colorList,
  defaultColor,
  dimTextHeight,
  ShowType,
  dimSettingForm,
  MxMeasureEntity,
} from "../../../MeasurementAnnotation/hooks";
import { DimType } from "../../../MeasurementAnnotation/hooks";
import { t } from "../../../i18n";
import { dialogWidth, isShowDialog, dialog } from "../dialog";
import { MxCADResbuf, MxCADSelectionSet, MxCADUiPrDist, MxCpp } from "mxcad";

const formData: {
  color: string;
  textHeight: number;
  accuracy: number;
} = reactive({
  color: defaultColor.value,
  textHeight: dimTextHeight.value,
  accuracy: -1,
});

const heightType = ref<"auto" | "manual">(
  dimTextHeight.value ? "manual" : "auto"
);
const valueType = ref<ShowType>(ShowType.ActualValue);
const tabValue = ref<DimType>(DimType.Length);
const settingDataArr = reactive<
  {
    type: DimType;
    fixedNum: number;
    showType: ShowType;
  }[]
>([]);

const settingData: {
  fixedNum: number;
  showType: ShowType;
} = reactive({ fixedNum: -1, showType: ShowType.ActualValue });

// 切换list
const tabList = [
  { name: t("长度"), value: DimType.Length },
  { name: t("面积"), value: DimType.Area },
  { name: t("坐标"), value: DimType.Coordinate },
];

const accList = [
  { text: t("自动"), value: -1 },
  { text: "0", value: 0 },
  { text: "0.0", value: 1 },
  { text: "0.00", value: 2 },
  { text: "0.000", value: 3 },
];

const modelChange = () => {
  settingDataArr.forEach((item) => {
    if (item.type === tabValue.value) {
      item.fixedNum = settingData.fixedNum;
      item.showType = settingData.showType;
    }
  });
};

const setData = () => {
  settingDataArr.forEach((item) => {
    if (item.type === tabValue.value) {
      settingData.fixedNum = item.fixedNum;
      settingData.showType = item.showType;
    }
  });
};
onMounted(() => {
  dialogWidth.value = 400;
  settingDataArr.length = 0;
  settingDataArr.push(...dimSettingForm);
  setData();
});

// 标注数据保存
const saveData = () => {
  defaultColor.value = formData.color;
  dimTextHeight.value = formData.textHeight;
  dimSettingForm.length = 0;
  dimSettingForm.push(...settingDataArr);
  dialog.showDialog(false);
  // 设置过滤器，过滤出自定义实体
  let filter = new MxCADResbuf();
  filter.AddString("CUSTOMENTITY", 5020);
  const ss = new MxCADSelectionSet();
  ss.allSelect(filter);
  if (ss.count() === 0) return;
  ss.forEach((id) => {
    const entity = id.getMcDbEntity();
    if (entity instanceof MxMeasureEntity) {
      entity.setDimSettingForm(dimSettingForm);
    }
  });
  MxCpp.getCurrentMxCAD().regen();
};

// 设置文字高度
const setTextHeight = async () => {
  isShowDialog.value = false;
  const getDist = new MxCADUiPrDist();
  getDist.setMessage(t("请选择一条线段的起始点，取线长作为文字的高度"));
  const heightVal = await getDist.go();
  if (heightVal) {
    formData.textHeight = Number(getDist.value().toFixed(2));
  }
  isShowDialog.value = true;
};
</script>

<style scoped lang="scss">
.color-box {
  width: 80px;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 2px;
  display: inline-block;
  vertical-align: middle;
}
.static {
  border: 1px solid rgb(204, 204, 204, 0.5);
  padding: 10px;
  position: relative;
  margin-top: 20px;
  .static-title {
    position: absolute;
    top: -10px;
    left: 10px;
    background-color: rgb(var(--v-theme-dialog-card-text));
  }
}
.fc {
  display: flex;
  align-items: center;
}
:deep(.v-selection-control-group--inline) {
  flex-direction: column;
}
:deep(.v-tab.v-tab.v-btn) {
  height: 25px;
  min-width: 20px;
  border: 1px solid #ccc;
}
:deep(.v-tabs--density-compact) {
  height: 25px;
}
.activeTab {
  background-color: rgb(137, 203, 250, 0.5);
}
.save_btn {
  position: absolute;
  bottom: -35px;
  right: 10px;
}
</style>