<script setup lang="ts">
import { DimType } from "../../../MeasurementAnnotation/hooks";
import { onMounted, ref } from "vue";
import {
  VTable,
  VCombobox,
  VAvatar,
  VList,
  VListItem,
  VListItemAction,
  VListItemTitle,
  VListItemSubtitle,
  VSelect,
} from "vuetify/components";
import {
  dimScale,
  MxMeasureEntity,
  hexToRgb,
  measurementCategory,
  colorList
} from "../../../MeasurementAnnotation/hooks";
import { McCmColor } from "mxcad";
import { dialogWidth } from "../dialog";
import { t } from "../../../i18n";

const props = defineProps({
  paramData: {
    type: Object,
    default: () => ({}),
  },
});

const measurements = ref<MxMeasureEntity[]>([]);
onMounted(() => {
  dialogWidth.value = 300;
  if (props.paramData?.measurements?.length) {
    measurements.value = props.paramData.measurements;
    const num = measurements.value.filter(
      (item) => item.dimType === DimType.Length || item.dimType === DimType.Area
    );
    const categoryList = measurementCategory.value.map(item => item.name);
    attributes.value.push(
      {
        name: t("颜色"),
        key: "trueColor",
        type: "color",
        value: measurements.value[0].trueColor
          .getColorValue(measurements.value[0].layerId)
          .replace("0x", "#"),
        options: colorList.value,
      },
      {
        name: t("分类"),
        type: "text",
        key: "categoryName",
        value:
          measurements.value.length == 1
            ? measurements.value[0].categoryName
            : "*多种*",
        options: categoryList.length > 0 ? categoryList : ["未分类"],
      },
      {
        name: t("文字高度"),
        type: "number",
        key: "dimTextHeight",
        value:
          measurements.value.length == 1
            ? Number(Number(measurements.value[0].dimTextHeight).toFixed(2))
            : "*多种*",
        options: [],
      }
    );
    if (num.length === measurements.value.length) {
      attributes.value.push({
        name: t("比例"),
        type: "number",
        key: "dimScale",
        value:
          measurements.value.length == 1
            ? Number((measurements.value[0] as any).dimScale.toFixed(3))
            : "*多种*",
        options:dimScale.value ===(measurements.value[0] as any).dimScale ? [Number(dimScale.value.toFixed(3))]: [Number((measurements.value[0] as any).dimScale.toFixed(3)), Number(dimScale.value.toFixed(3))],
      });
    }
  }
});
interface Attribute {
  name: string;
  type: "color" | "text" | "number";
  value: string | number;
  options?: (string | number)[];
  key: string;
}

const attributes = ref<Attribute[]>([]);

const emit = defineEmits(["receiveParameters"]);

const changeValue = () => {
  const obj: any = {};
  attributes.value.map((item) => {
    if (item.type === "color") {
      const pattern = /^#([A-Fa-f0-9]{3}){1,2}$/;
      if (pattern.test(item.value as string)) {
        const color = new McCmColor(...hexToRgb(item.value as string));
        obj[item.key] = color;
      }
    }else {
      if(item.type === "number" && Number(item.value)){
        obj[item.key] = Number(item.value);
      }else if(item.type === "text" && item.value && item.value !== "*多种*"){
        obj[item.key] = item.value;
      }
    }
  });
  emit("receiveParameters", obj);
};
</script>

<template>
  <div style="margin-top: 10px">
    <v-table
      class="mx-attribute-table"
      density="compact"
      style="width: 100%; border: 1px solid #ccc"
    >
      <thead>
        <tr>
          <th style="width: 40%">属性名称</th>
          <th>属性值</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, idx) in attributes" :key="idx">
          <td>{{ item.name }}</td>
          <td style="display: flex; align-items: center">
            <v-select
              :items="item.options"
              v-if="item.type === 'color'"
              v-model="item.value"
            >
              <template #selection="{ item: items }">
                <span
                  :style="{ backgroundColor: items.value }"
                  class="color-box"
                ></span>
              </template>
              <template v-slot:item="{ item: items, props }">
                <v-list-item
                  @click="
                    () => {
                      item.value = items;
                      props.onClick();
                      changeValue();
                    }
                  "
                >
                  <span
                    :style="{ backgroundColor: items.value }"
                    class="color-box"
                  ></span>
                </v-list-item>
              </template>
            </v-select>
            <v-combobox
              v-else-if="item.key === 'dimTextHeight'"
              v-model="item.value"
              :items="item.options || []"
              :hide-details="true"
              density="compact"
              style="width: 100%"
              @change="changeValue"
            />
            <v-select
              v-else
              v-model="item.value"
              :items="item.options || []"
              :hide-details="true"
              density="compact"
              style="width: 100%"
              @update:model-value="changeValue"
            />
          </td>
        </tr>
      </tbody>
    </v-table>
  </div>
</template>

<style scoped>
.color-box {
  width: 80px;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 2px;
  display: inline-block;
  vertical-align: middle;
}
.mx-attribute-table {
  border-collapse: collapse;
}
.mx-attribute-table th,
.mx-attribute-table td {
  border: 1px solid #ccc;
}
</style>