import { McDbCustomEntity, McGePoint3d, IMcDbDwgFiler, McGePoint3dArray, MxCADWorldDraw, McDb, MxCpp, McDbPolyline, McDbLine, McDbText, MxCADUiPrPoint, McDbHatch, McDbEntity, McDbMText, MxCADUtility } from "mxcad";
import { MxFun } from "mxdraw";
import { t } from '../../i18n';

export enum textAlignment {
    textEnd,
    textUp
}

// 做法标注
export class McDbTestPracticeLabeling extends McDbCustomEntity {
    // 定义McDbTestPracticeLabeling内部的点对象 
    // 圆点圆心集合
    private points: McGePoint3d[] = [];
    // 标注点位置
    private _labelPos: McGePoint3d = new McGePoint3d();
    // 文字基线位置
    private _textBasePos: McGePoint3d = new McGePoint3d();
    // 文字基线端点
    private _textEndPos: McGePoint3d = new McGePoint3d();
    // 文字位置
    private textPosition: McGePoint3d = new McGePoint3d();
    // 文字内容
    private _textContent: string = "";
    /**
     * 文字对齐方式
     * E：在线端
     * U：在线上
     */
    private _textAlignment: number = textAlignment.textEnd;
    // 文字高度
    private height: number = 0;
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();
    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new McDbTestPracticeLabeling(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "McDbTestPracticeLabeling";
    }
    //设置或获取文本字高
    public set textHeight(val: number) {
        this.height = val;
    }
    public get textHeight(): number {
        return this.height;
    }
    //设置或获取标注点
    public set labelPos(pt: McGePoint3d) {
        this._labelPos = pt.clone();
    }
    public get labelPos(): McGePoint3d {
        return this._labelPos;
    }
    //设置或获取文字基线位置
    public set textBasePos(pt: McGePoint3d) {
        this._textBasePos = pt.clone();
    }
    public get textBasePos(): McGePoint3d {
        return this._textBasePos;
    }
    //设置或获取文字基线端点
    public set textEndPos(pt: McGePoint3d) {
        this._textEndPos = new McGePoint3d(pt.x, this._textBasePos.y);
        this.setTextPos();
    }
    public get textEndPos(): McGePoint3d {
        return this._textEndPos;
    }
    //设置或获取文字对齐方式
    public set textAlignment(val: number) {
        this._textAlignment = val;
    }
    public get textAlignment(): number {
        return this._textAlignment;
    }
    //设置或获取文字内容
    public set textContent(val: string) {
        this._textContent = val;
    }
    public get textContent(): string {
        return this._textContent;
    }
    // 读取自定义实体数据
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.points = filter.readPoints("points").val;
        this._textEndPos = filter.readPoint("textEndPos").val;
        this._textBasePos = filter.readPoint("textBasePos").val;
        this._labelPos = filter.readPoint("labelPos").val;
        this.height = filter.readDouble("height").val;
        this._textAlignment = filter.readLong("textAlignment").val;
        this._textContent = filter.readString("textContent").val;
        this.textPosition = filter.readPoint("textPosition").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        return true;
    }
    // 写入自定义实体数据
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoints("points", this.points);
        filter.writePoint("textEndPos", this._textEndPos);
        filter.writePoint("textBasePos", this._textBasePos);
        filter.writePoint("labelPos", this._labelPos);
        filter.writePoint("textPosition", this.textPosition);
        filter.writeLong("textAlignment", this._textAlignment);
        filter.writeString("textContent", this._textContent);
        filter.writeDouble("height", this.height);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        if (iIndex < this.points.length) {
            this.points[iIndex].x += dXOffset;
            this.points[iIndex].y += dYOffset;
            this.points[iIndex].z += dZOffset;
            const line = new McDbLine(this._labelPos, this._textBasePos);
            this.points[iIndex] = line.getClosestPointTo(this.points[iIndex], false).val;
        }
        if (iIndex === this.points.length) {
            this._labelPos.x += dXOffset;
            this._labelPos.y += dYOffset;
            this._labelPos.z += dZOffset;
        }
        if (iIndex === this.points.length + 1) {
            const lengthArr = [];
            this.points.forEach(pt => {
                lengthArr.push(this._textBasePos.distanceTo(pt))
            })
            this._textBasePos.x += dXOffset;
            this._textBasePos.y += dYOffset;
            this._textBasePos.z += dZOffset;
            this._textEndPos.x += dXOffset;
            this._textEndPos.y += dYOffset;
            this._textEndPos.z += dZOffset;
            this.textPosition.x += dXOffset;
            this.textPosition.y += dYOffset;
            this.textPosition.z += dZOffset;
            this.points.forEach((pt, index) => {
                const v = this._labelPos.sub(this._textBasePos).normalize();
                let point = this._textBasePos.clone().addvec(v.mult(lengthArr[index]));
                if (lengthArr[index] > this._labelPos.distanceTo(this._textBasePos)) {
                    point = this._labelPos.clone();
                }
                pt.x = point.x;
                pt.y = point.y;
                pt.z = point.z;
            })
        }
        if (iIndex === this.points.length + 2) {
            this._textEndPos.x += dXOffset;
            this.setTextPos();
        }
        if (iIndex === this.points.length + 3) {
            if (this._textEndPos.x < this._textBasePos.x) {
                this.textPosition.x + dXOffset > this._textBasePos.x ? this.textPosition.x = this._textBasePos.x : this.textPosition.x += dXOffset;
            } else {
                this.textPosition.x + dXOffset < this._textBasePos.x ? this.textPosition.x = this._textBasePos.x : this.textPosition.x += dXOffset;
            }
        }
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray();
        if (this.points.length > 0) {
            this.points.forEach(pt => {
                ret.append(pt)
            });
        }
        ret.append(this._labelPos)
        ret.append(this._textBasePos)
        ret.append(this._textEndPos)
        ret.append(this.textPosition)
        return ret;
    };
    // 绘制实体 
    public worldDraw(draw: MxCADWorldDraw): void {
        const allEntityArr: McDbEntity[] = [];
        // 绘制引线
        const pl = new McDbPolyline();
        pl.addVertexAt(this._labelPos);
        pl.addVertexAt(this._textBasePos);
        pl.addVertexAt(this._textEndPos);
        draw.drawEntity(pl);
        allEntityArr.push(pl)
        // 绘制标注文字
        const text = new McDbText();
        text.textString = this._textContent;
        text.height = this.height;
        if (this._textEndPos.x < this._textBasePos.x) {
            text.horizontalMode = McDb.TextHorzMode.kTextRight;
        } else {
            text.horizontalMode = McDb.TextHorzMode.kTextLeft;
        }
        text.position = text.alignmentPoint = this.textPosition;
        draw.drawEntity(text);
        allEntityArr.push(text)
        // 绘制圆点
        if (this.points.length > 0) {
            this.points.forEach(pt => {
                const solid = new McDbHatch();
                solid.appendCircleLoop(pt.x, pt.y, this.height / 3);
                draw.drawEntity(solid);
                allEntityArr.push(solid)
            })
        }

        this.getBox(allEntityArr);
    }
    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbText) {
                const ent = new McDbMText()
                ent.contents = entity.textString;
                ent.textHeight = entity.height;
                ent.location = entity.position;
                switch (entity.horizontalMode) {
                    case McDb.TextHorzMode.kTextLeft:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                    case McDb.TextHorzMode.kTextCenter:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    case McDb.TextHorzMode.kTextRight:
                        ent.attachment = McDb.AttachmentPoint.kBottomRight;
                        break;
                    case McDb.TextHorzMode.kTextMid:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    default:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                }
                // 将ent的文字样式设置为当前文字样式
                const textStyleId = mxcad.getDatabase().getCurrentlyTextStyleId();
                ent.textStyleId = textStyleId;
                ent.reCompute();
                result = MxCADUtility.getTextEntityBox(ent, false);
            } else if (entity instanceof McDbMText) {
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }
    // 添加原点
    public addVertex(pt: McGePoint3d) {
        this.assertWrite();
        this.points.push(pt);
    }
    // 获取原点数组
    public getPoints() {
        return this.points;
    }
    // 设置文字位置
    private setTextPos() {
        if (!this.height) this.height = MxFun.viewCoordLong2Cad(20);
        let param = 1;
        if (this._textEndPos.x < this._textBasePos.x) {
            param = -1;
        }
        if (this._textAlignment === textAlignment.textEnd) {
            this.textPosition = new McGePoint3d(this._textEndPos.x + (this.height / 2) * param, this._textEndPos.y - this.height / 2);
        } else if (this._textAlignment === textAlignment.textUp) {
            this.textPosition = new McGePoint3d(this._textBasePos.x + (this.height / 2) * param, this._textBasePos.y + this.height / 3);
        };
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
};

export async function drawPracticeLabeling(textContent: string, textAlignment: number, isAddCircle: boolean) {
    const pLabeling = new McDbTestPracticeLabeling();
    pLabeling.textContent = textContent;
    pLabeling.textAlignment = textAlignment;

    const getPoint1 = new MxCADUiPrPoint();
    getPoint1.setMessage(`\n${t('请指定标注的第一点的位置')}:`);
    const point1 = await getPoint1.go();
    if (!point1) return;
    pLabeling.labelPos = point1;

    const getPoint2 = new MxCADUiPrPoint();
    getPoint2.setMessage(`\n${t('请指定文字基线的位置')}:`);
    getPoint2.setUserDraw((pt, pw) => {
        const _clone = pLabeling.clone() as McDbTestPracticeLabeling;
        _clone.textBasePos = pt;
        _clone.textEndPos = new McGePoint3d(pt.x + MxFun.viewCoordLong2Cad(30), pt.y);
        pw.drawMcDbEntity(_clone)
    })
    const point2 = await getPoint2.go();
    if (!point2) return;
    pLabeling.textBasePos = point2;
    const getPoint3 = new MxCADUiPrPoint();
    getPoint3.setMessage(`\n${t('请指定文字基线长度')}:`);
    getPoint3.setUserDraw((pt, pw) => {
        const _clone = pLabeling.clone() as McDbTestPracticeLabeling;
        _clone.textEndPos = pt;
        pw.drawMcDbEntity(_clone);
    });
    const point3 = await getPoint3.go();
    if (!point3) return;
    pLabeling.textEndPos = point3;

    if (isAddCircle) {
        pLabeling.addVertex(point1);
        const line = new McDbLine(point1, point2);
        while (true) {
            const getPt = new MxCADUiPrPoint();
            getPt.setMessage(`\n${t('指定原点的位置，右键完成')}:`);
            getPt.setUserDraw((pt, pw) => {
                const _clone = pLabeling.clone() as McDbTestPracticeLabeling;
                const point = line.getClosestPointTo(pt, false).val
                _clone.addVertex(point);
                pw.drawMcDbEntity(_clone)
            })
            const pt = await getPt.go();
            if (!pt) break;
            const point = line.getClosestPointTo(pt, false).val
            pLabeling.addVertex(point);
        }
    }
    const mxcad = MxCpp.getCurrentMxCAD();
    mxcad.drawEntity(pLabeling);
}
