<template>
    <div class="content">
            <div class="inputBox">
                <textarea v-model="dataForm.textContent" @blur="() => { setCommandFocus(true); }" 
                    @focus="() => { setCommandFocus(false) }"></textarea>
            </div>
            <div style="display:flex;justify-content:space-between;align-items: center;">
                <span>{{ t('对齐方式：') }}</span>
                <div class="radio" v-for="item in typeArr" :key="item.id">
                    <input type="radio" :id="item.name" v-model="dataForm.textAlignment" :value="item.id" />
                    <label :for="item.name">{{ item.name }}</label>
                </div>
                <div style="display:flex;align-items: center;margin-left: 10px;" class="custom-checkbox">
                    <input type="checkbox" id="ac" value="ac" v-model="dataForm.isAddCircle">
                    <span>{{ t('添加圆点') }}</span>
                </div>
            </div>
            <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
                <button class="btn_normal" @click="sureDraw" style="margin: 15px 0px 5px;color: #fff!important;">{{
                    t('确定') }}</button>
            </div>
    </div>
</template>

<script setup lang="ts">
import { reactive} from 'vue';
import { t } from '../../i18n';
import { drawPracticeLabeling, textAlignment } from "./hooks";
import { isOpen } from "../../index";

const dataForm:any = reactive({
    textAlignment:textAlignment.textEnd,
    textContent:"",
    isAddCircle:false
});
const { store } = MxPluginContext;
const { setCommandFocus } = store.useFocus();
const typeArr = reactive([
    { name: t('文字在线端'), id: textAlignment.textEnd },
    { name: t('文字在线上'), id: textAlignment.textUp }
]);
// 确定绘制箭头引注
const sureDraw = async () => {
    isOpen.value = false;
    drawPracticeLabeling(dataForm.textContent,dataForm.textAlignment,dataForm.isAddCircle );
}

</script>

<style lang="scss" scoped>
@import '../../assets/dialogContens.scss';
</style>