<template>
    <AnnotationForm @submitData="sureDraw" :componentName="componentName">
        <template v-slot:default="{ dataForm }">
            <div style="display:flex;align-items: center;margin-top: 10px;" class="custom-checkbox">
                <input type="checkbox" id="aa" value="aa" v-model="dataForm.isCheckAA">
                <span>{{ t('固定角度：') }}</span>
                <div class="custom-select">
                    <select v-model="dataForm.alignAngle" :disabled="!dataForm.isCheckAA">
                        <option :value="item.id" v-for="item in alignAngleArr" :key="item.id">{{ item.name }}</option>
                    </select>
                    <div class="select-arrow"></div>
                </div>
            </div>
        </template>
    </AnnotationForm>
</template>

<script setup lang="ts">
import { isOpen } from "../../index";
import { drawExportAnnotation } from "./hooks"
import AnnotationForm from '../../components/AnnotationForm.vue';
import { t } from '../../i18n'
import { reactive, ref } from "vue";
const componentName = ref('YCBZ');
const alignAngleArr = reactive([
    { id: 30, name: '30°' },
    { id: 45, name: '45°' },
    { id: 60, name: '60°' },
    { id: 90, name: '90°' }])
// 确定绘制箭头引注
const sureDraw = async (dataForm) => {
    isOpen.value = false;
    if(!dataForm.isCheckAA) dataForm.alignAngle = 0;
    drawExportAnnotation(dataForm.textUp, dataForm.textDown, dataForm.arrowType, dataForm.alginType, dataForm.alignAngle);
}
</script>

<style lang="scss" scoped>
@import '../../assets/dialogContens.scss';
</style>