<template>
    <div class="content">
        <div style="display: flex;justify-content: space-between;align-items: center;">
            <div class="inputBox">
                <span>{{ `${t('剖切编号')}：` }}</span>
                <input type="text" v-model="dataForm.cutNum" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" style="width: 80px;"/>
            </div>
            <div style="display:flex;align-items: center;margin-bottom: 10px;margin-left: 20px;" class="custom-checkbox">
                <input type="checkbox" id="aa" value="aa" v-model="dataForm.isShowSec">
                <div class="inputBox" style="margin: 0;">
                    <span>{{ t('剖面图号：') }}</span>
                    <input type="text" v-model="dataForm.secNum" @focus="() => { setCommandFocus(false) }"
                        @blur="() => { setCommandFocus(true); }" :readonly="!dataForm.isShowSec"
                        :style="!dataForm.isShowSec ? 'opacity:0.5;width: 80px;' : 'width: 80px;'" />
                </div>
            </div>
        </div>
        <div style="display:flex;align-items: center;margin-bottom: 10px;">
            <span>{{ t('剖切样式：') }}</span>
            <div class="radio" v-for="item in typeArr" :key="item.id" style="margin:0 10px;">
                <input type="radio" :id="item.name" v-model="dataForm.cutStyle" :value="item.id"/>
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div style="display:flex;align-items: center;margin-bottom: 10px;">
            <span>{{ t('标注位置：') }}</span>
            <div class="radio" v-for="item in markArr" :key="item.id" :style="!dataForm.isShowSec ? 'margin:0 10px;opacity:0.5;':'margin:0 10px;'">
                <input type="radio" :id="item.name" v-model="dataForm.markPos" :value="item.id" :disabled="!dataForm.isShowSec"/>
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div style="display:flex;align-items: center;">
            <span>{{ t('标注方向：') }}</span>
            <div class="radio" v-for="item in directArr" :key="item.id" :style="!dataForm.isShowSec ? 'margin:0 10px;opacity:0.5;':'margin:0 10px;'">
                <input type="radio" :id="item.name" v-model="dataForm.markDirect" :value="item.id" :disabled="!dataForm.isShowSec"/>
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
            <button class="btn_normal" @click="sureDraw" style="margin: 15px 0px 5px;color: #fff!important;">{{
                t('确定') }}</button>
        </div>
    </div>
</template>

<script setup lang="ts">
import { reactive } from 'vue';
import { t } from '../../i18n';
import { drawSliceSymbol, cutStyle, markPos, markDirect } from "./hooks";
import { isOpen } from "../../index";

const dataForm: any = reactive({
    cutNum:'1',
    isShowSec:false,
    secNum:'1',
    cutStyle:cutStyle.crossSection,
    markPos:markPos.bothEnds,
    markDirect:markDirect.positionLine
});
const { store } = MxPluginContext;
const { setCommandFocus } = store.useFocus();
const typeArr = reactive([
    { name: t('正交剖面'), id: cutStyle.crossSection },
    { name: t('断面剖切'), id: cutStyle.sectionCutting }
]);
const markArr = reactive([
    { name: t('两端'), id: markPos.bothEnds },
    { name: t('始端'), id: markPos.start },
    { name: t('末端'), id: markPos.end }
]);
const directArr = reactive([
    { name: t('沿剖切位置线'), id: markDirect.positionLine },
    { name: t('沿剖视方向线'), id: markDirect.sectionLine }
]);
// 确定绘制箭头引注
const sureDraw = async () => {
    isOpen.value = false;
    drawSliceSymbol(dataForm.cutNum, dataForm.cutStyle, dataForm.isShowSec, dataForm.markPos, dataForm.markDirect, dataForm.secNum);
}

</script>

<style lang="scss" scoped>
@import '../../assets/dialogContens.scss';
</style>