<template>
    <div class="body" v-show="isShowPatternRec">
        <div class="dialog" @mousedown="dragModal" ref="dialog">
            <div class="header">
                <div style="display: flex;align-items: center;">
                    <img :src="utils.getLogoImgSrc()" alt="">
                    <span>{{t('图形识别')}}</span>
                </div>
                <a href="#!" @click="hideModal" role="button" class="close" aria-label="close this modal">
                    <svg aria-hidden="true" fill="rgb(var(--v-theme-on-dialog-card-text))">
                        <use xlink:href="#icon-cha"></use>
                    </svg>
                </a>
            </div>
            <div class="content">
                <p class="title">{{t('图形')}}</p>
                <div class="graphName">
                    <span>{{`${t('请输入图形名称')}：`}}</span>
                    <input type="text" v-model="graphName" @focus="() => { setCommandFocus(false); isFocused = true }"
                        @blur="() => { setCommandFocus(true); isShowtips = false; isFocused = false }"
                        :style="isShowtips ? 'border:1px solid red' : ''" />
                </div>
                <p style="color:red;padding-left: 100px;" v-if="isShowtips">{{`${t('图形名称不能为空')}!`}}</p>
                <div style="margin-top: 10px;">
                    <div style="width:100%;display:flex;justify-content:center">
                        <img :src="patternImgUrl" alt="graph" style="width: 70%;" />
                    </div>
                    <div style="width:100%;display:flex;justify-content:flex-end;margin-top:10px">
                        <button class="btn_normal" @click="reSelect">{{t('重新选择图形')}}</button>
                    </div>
                </div>
                <p class="title">{{t('查找范围')}}</p>
                <div style="display:flex;justify-content:space-between;align-items: center;">
                    <div class="radio">
                        <input type="radio" id="all" v-model="lookIn" value="all" />
                        <label for="all">{{ t('整张图纸') }}</label>
                    </div>
                    <div class="radio" >
                        <input type="radio" id="react" v-model="lookIn" value="react" />
                        <label for="react">{{ t('区域查找') }}</label>
                    </div>
                    <button :class="lookIn === 'react' ? 'btn_normal' : 'btn'" @click="regionSelection">{{t('区域选择')}}</button>
                </div>
                <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
                    <button class="btn_normal btn_begin" @click="identifying"
                        style="margin: 15px 0px 5px;color: #fff!important;">{{t('开始识别')}}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup lang="ts">
import { ref, watch } from "vue"
import { isShowPatternRec, patternImgUrl, patternList, searchPattern, getIdentificationPattern } from "../hooks/usePatternRec";
import { McGePoint3d, MxCADUiPrPoint } from "mxcad";
import { MxDbRect } from "mxdraw";
import { t } from "../../i18n";

const { callCommand, useLoading, utils } = MxPluginContext;
const { showLoading, hideLoading } = useLoading(); // loading图标
const graphName = ref('');// 图形名
const lookIn = ref('all');
const isFocused = ref(false);
watch(isShowPatternRec, (newVal, oldVal) => {
    if (newVal === true && !graphName.value) {
        graphName.value = 'Temp' + `${patternList.length + 1}`
    }
})
// 关闭弹窗
const hideModal = () => {
    isShowPatternRec.value = false;
    graphName.value = '';
    lookIn.value = 'all';
    isShowtips.value = false;
}
const dialog = ref(null);
// 是否显示提示
const isShowtips = ref(false);

// 移动设置窗口
const dragModal = (event) => {
    const dragBox = dialog.value;
    const disX = event.clientX - dragBox.offsetLeft;
    const disY = event.clientY - dragBox.offsetTop;

    const moveDrag = (event) => {
        if (!isFocused.value) {
            dragBox.style.left = event.clientX - disX + "px";
            dragBox.style.top = event.clientY - disY + "px";
        }
    };

    document.addEventListener("mousemove", moveDrag);
    document.addEventListener("mouseup", () => {
        document.removeEventListener("mousemove", moveDrag);
    });
};
// 输入框获取角点
const { store } = MxPluginContext;
const { setCommandFocus } = store.useFocus();
// 重新选择
const reSelect = () => {
    hideModal();
    getIdentificationPattern();
}
// 区域选择
let corner1: McGePoint3d | null;
let corner2: McGePoint3d | null;
const regionSelection = async () => {
    if (lookIn.value === 'all') {
        return;
    } else if (lookIn.value === 'react') {
        isShowPatternRec.value = false;
        const getPt1 = new MxCADUiPrPoint();
        getPt1.setMessage(`${t('请选择识图范围')}`);
        while (true) {
            corner1 = await getPt1.go();
            if (corner1) {
                break;
            }
        }
        let rect = new MxDbRect();
        rect.pt1 = corner1.toVector3();
        const getPt2 = new MxCADUiPrPoint();
        getPt2.setMessage(`${t('请选择识图范围')}`);
        while (true) {
            getPt2.setUserDraw((pt, pw) => {
                rect.pt2 = pt.toVector3();
                rect.setColor(new THREE.Color(0x70DB93))
                pw.drawCustomEntity(rect)
            })
            corner2 = await getPt2.go();
            if (corner2) {
                break;
            }
        }
        isShowPatternRec.value = true;
    }
}

// 开始识别
const identifying = () => {
    if (!graphName.value) {
        isShowtips.value = true;
        return;
    } else {
        showLoading();
        let searchList;
        if (lookIn.value === 'react' && corner1 && corner2) {
            searchList = searchPattern.identifyGraphics(corner1, corner2)
        } else {
            searchList = searchPattern.identifyGraphics()
        }
        if (searchList) {
            hideLoading();
        }
        patternList.push({ name: graphName.value, ...searchList });
    };
    hideModal();
    callCommand('Mx_PatternList');
}
</script>

<style scoped lang="scss">
.body {
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
}

.dialog {
    border: 8px;
    box-shadow: -20px 20px 35px 1px rgba(10, 49, 86, 0.18);
    position: absolute;
    top: calc(50% - 212px);
    left: calc(50% - 165px);
    color: rgb(var(--v-theme-on-dialog-card)) !important;
    background-color: rgb(var(--v-theme-dialog-card)) !important;
    --v-theme-overlay-multiplier: var(--v-theme-dialog-card-overlay-multiplier);
    overflow: auto;

    .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: .85rem;
        padding: 4px 0px;

        img {
            width: 24px;
            height: 24px;
            margin-left: 4px;
            margin-right: 8px;
        }

        .close {
            background: none;
            border: none;
            cursor: pointer;
            display: flex;
            width: 16px;
            text-decoration: none;
            height: 16px;
            margin-right: 8px;
        }
    }

    .content {
        --v-theme-overlay-multiplier: var(--v-theme-dialog-card-text-overlay-multiplier);
        background-color: rgb(var(--v-theme-dialog-card-text)) !important;
        color: rgb(var(--v-theme-on-dialog-card-text)) !important;
        font-size: var(--mx-font-size);
        padding: 2px 20px;
        margin: 4px;
        border-radius: 4px;

        .icon {
            width: 16px;
            height: 16px;
            font-weight: 700;
            vertical-align: -.15em;
            overflow: hidden;
        }

        .graphName {
            display: flex;
            align-items: center;

            input {
                width: 180px;
                padding: 2px;
                border-radius: 4px;
                outline: none;
                color: rgb(var(--v-theme-on-undertint-bg)) !important;
                background-color: rgb(var(--v-theme-undertint-bg)) !important;
            }
        }

        .title {
            color: rgb(var(--v-theme-on-dialog-card-text)) !important;
            border-bottom: 1px solid #ccc;
            margin: 10px 0px;
            opacity: 0.6;
        }

        .btn {
            padding: 0px 14px;
            font-size: var(--mx-font-size);
            border-radius: 4px;
            color: rgb(var(--v-theme-on-undertint-bg)) !important;
            background-color: rgb(var(--v-theme-undertint-bg));
            height: 30px;
            display: flex;
            align-items: center;
            pointer-events: none;
            opacity: 0.6;
            height: 28px;
            border: 1.5px solid transparent;
        }

        .radio {
            font-size: var(--mx-font-size);

            label {
                height: 20px;
                display: flex;
                align-items: center;
            }
        }
    }

}

input[type="radio"] {
    height: 15px;
    width: 15px;
    margin-right: 10px;
    display: none;
}

input[type="radio"]+label::before {
    content: "\a0";
    /*不换行空格*/
    display: inline-block;
    vertical-align: middle;
    width: 14px;
    height: 14px;
    margin-right: 5px;
    border-radius: 50%;
    border: 1px solid rgb(var(--v-theme-on-dialog-card)) !important;
    line-height: 22px;
    box-sizing: border-box;
}

input[type="radio"]:checked+label::before {
    background-color: rgb(var(--v-theme-on-dialog-card)) !important;
    background-clip: content-box;
    padding: 2px;
}

.btn_normal {
    padding: 0px 14px;
    font-size: var(--mx-font-size);
    border-radius: 4px;
    color: rgb(var(--v-theme-on-undertint-bg)) !important;
    background-color: rgb(var(--v-theme-undertint-bg));
    height: 30px;
    display: flex;
    align-items: center;
    border: 1.5px solid transparent;

    &:hover {
        background-color: rgb(var(--v-theme-btn-hover));
        border: 1.5px solid rgba(0, 0, 0, var(--v-border-opacity));
    }
}

;

.btn_begin {
    background-color: rgb(3, 155, 229);

    &:hover {
        background-color: rgb(3, 155, 229, 0.6);
        ;
        border: 1.5px solid rgba(0, 0, 0, var(--v-border-opacity));
    }
}
</style>