<template>
    <div class="content">
            <div class="inputBox">
                <span>{{ `${t('上标文字')}：` }}</span>
                <input type="text" v-model="dataForm.textUp" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" />
            </div>
            <div class="inputBox">
                <span>{{ `${t('下标文字')}：` }}</span>
                <input type="text" v-model="dataForm.textDown" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" :readonly="dataForm.alginType === alginType.start && props.componentName === 'JTYZ'"
                    :style="(dataForm.alginType === alginType.start && props.componentName === 'JTYZ') ? 'opacity:0.5' : ''" />
            </div>
            <div style="display:flex;justify-content:space-between;align-items: center;">
                <span>{{ t('对齐方式：') }}</span>
                <div class="radio" v-for="item in (props.componentName === 'JTYZ' ? typeArr1 : typeArr2)" :key="item.id">
                    <input type="radio" :id="item.name" v-model="dataForm.alginType" :value="item.id" />
                    <label :for="item.name">{{ item.name }}</label>
                </div>
            </div>
            <div style="display:flex;align-items: center;margin-top: 10px;">
                <span>{{ t('箭头样式：') }}</span>
                <div class="custom-select">
                    <select v-model="dataForm.arrowType">
                        <option :value="item.id" v-for="item in arrowTypeArr" :key="item.id">{{ item.name }}</option>
                    </select>
                    <div class="select-arrow"></div>
                </div>
            </div>
            <slot :dataForm="dataForm"></slot>
            <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
                <button class="btn_normal" @click="sureDraw" style="margin: 15px 0px 5px;color: #fff!important;">{{
                    t('确定') }}</button>
            </div>
    </div>
</template>

<script setup lang="ts">
import { reactive, defineEmits, defineProps } from 'vue';
import { t } from '../i18n'
import { arrowType,alginType  } from "../SymbolAnnotation"

const props = defineProps({
  componentName: String
});
const dataForm:any = reactive({
    textUp: '上',
    textDown: '下',
    alginType: alginType.start,
    arrowType: arrowType.arrow,
    isCheckAA:false,
    alignAngle:30
});
const { store } = MxPluginContext;
const { setCommandFocus } = store.useFocus();
const typeArr1 = reactive([
    { name: t('在线端'), id: alginType.start },
    { name: t('齐线中'), id: alginType.middle },
    { name: t('齐线端'), id: alginType.end },
]);
const typeArr2 = reactive([
    { name: t('始端'), id: alginType.start },
    { name: t('中间'), id: alginType.middle },
    { name: t('末端'), id: alginType.end },
]);
const arrowTypeArr = reactive([
    { name: t('箭头'), id: arrowType.arrow },
    { name: t('半箭头'), id: arrowType.halfArrow },
    { name: t('点'), id: arrowType.point },
    { name: t('十字'), id: arrowType.cross },
    { name: t('无'), id: arrowType.none }
])
const emit = defineEmits(['submitData']);
// 确定绘制箭头引注
const sureDraw = async () => {
    emit('submitData', dataForm);
}
</script>

<style lang="scss" scoped>
@import '../assets/dialogContens.scss';
</style>