<template>
  <div class="contents">
    <div class="static">
      <span class="static-title">{{ t("基本特性") }}</span>
      <div class="base-options" v-for="item in _baseProperties" :key="item.key">
        <div style="min-width:100px">
          <v-checkbox
            v-model="item.isChecked"
            :label="t(item.name)"
            :disabled="item.isReadOnly"
            hide-details
            density="compact"
          />
        </div>
        <div
          class="color_box"
          :style="{ backgroundColor: item.color }"
          v-show="item.value == 'trueColor'"
        ></div>
        <span
          :style="
            item.value == 'trueColor' ? 'margin-left: 5px' : 'margin-left: 45px'
          "
          >{{ item?.entProperty || "" }}</span
        >
      </div>
    </div>
    <div class="static" style="display: flex; justify-content: space-around">
      <span class="static-title">{{ t("特殊特性") }}</span>
      <v-row justify="space-around">
        <v-col v-for="item in _specialProperties" :key="item.key" cols="4">
          <v-checkbox
            v-model="item.isChecked"
            :label="t(item.name)"
            hide-details
            :disabled="item.isReadOnly"
            density="compact"
          />
        </v-col>
      </v-row>
    </div>
    <div class="footerBtn">
      <v-btn color="primary" @click="sureSet">{{ t("确定") }}</v-btn>
      <v-btn
        style="margin-left: 10px"
        @click="() => dialog.showDialog(false)"
        >{{ t("取消") }}</v-btn
      >
    </div>
  </div>
</template>

<script setup lang="ts">
import { baseProperties, specialProperties, PropertiesForm } from "../../index";
import { t } from "../../../i18n";
import { VBtn, VCheckbox, VRow, VCol } from "vuetify/components";
import { onMounted, onUnmounted, reactive } from "vue";
import { dialogWidth, dialog } from "../dialog";

const _baseProperties = reactive<PropertiesForm[]>([]);
const _specialProperties = reactive<PropertiesForm[]>([]);

const keysFun = (event: KeyboardEvent) => {
  [_baseProperties, _specialProperties].forEach((arr) => {
    arr.forEach((item) => {
      if (!item.isReadOnly && item.key === event.key)
        item.isChecked = !item.isChecked;
    });
  });
};
onMounted(() => {
  dialogWidth.value = 350;
  _baseProperties.push(...baseProperties);
  _specialProperties.push(...specialProperties);
  window.addEventListener("keydown", keysFun);
});

const sureSet = () => {
  baseProperties.length = 0;
  baseProperties.push(..._baseProperties);
  specialProperties.length = 0;
  specialProperties.push(..._specialProperties);
  dialog.showDialog(false);
};

onUnmounted(() => {
  window.removeEventListener("keydown", keysFun);
});
</script>

<style scoped lang='scss'>
.contents {
  padding: 5px;
  position: relative;
}
.static {
  border: 1px solid rgb(204, 204, 204, 0.5);
  padding: 10px;
  position: relative;
  margin-top: 20px;
  .static-title {
    position: absolute;
    top: -10px;
    left: 10px;
    background-color: rgb(var(--v-theme-dialog-card-text));
  }
}
.base-options {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 25px;
  margin-top: 5px;
}
.footerBtn {
  display: flex;
  position: absolute;
  bottom: -35px;
  right: 10px;
}
.color_box {
  height: 15px;
  width: 15px;
  margin-left: 45px;
}
</style>