<template>
  <div style="margin-top: 10px; padding: 5px; position: relative">
    <!-- 统计范围 -->
    <v-row class="static">
      <span class="static-title">{{ t("统计范围") }}</span>
      <v-col cols="12" md="4">
        <v-radio-group v-model="statRange" @change="modelChange">
          <v-radio :label="t('所有标注')" value="all" />
          <v-radio :label="t('手动选择标注')" value="manual" />
        </v-radio-group>
      </v-col>
      <v-col cols="12" md="8" class="d-flex align-end">
        <v-btn
          class="mr-2"
          :disabled="statRange !== 'manual'"
          @click="selectAnnotation"
          ><span class="custom-ellipsis">{{t('选择标注')}}</span></v-btn
        >
        <v-btn
          :disabled="statRange !== 'manual'"
          @click="
            () => {
              allMeasureEntitys.length = 0;
              manualSelectionEntitys.length = 0;
              getShowData();
            }
          "
          ><span class="custom-ellipsis">{{t('清空选择')}}</span></v-btn
        >
      </v-col>
    </v-row>
    <!-- 过滤选项 -->
    <div class="filter-options">
      <v-checkbox
        v-model="filterByColor"
        :label="t('按标注颜色进行过滤')"
        hide-details
        density="compact"
        @change="getShowData"
      />
      <v-btn class="ml-2" :disabled="!filterByColor" @click="openList('color')"
        ><span class="custom-ellipsis">{{t('打开颜色选择框')}}</span></v-btn
      >
    </div>
    <div class="filter-options">
      <v-checkbox
        v-model="filterByCategory"
        :label="t('按标注分类进行过滤')"
        hide-details
        density="compact"
        @change="getShowData"
      />
      <v-btn
        class="ml-2"
        :disabled="!filterByCategory"
        @click="openList('category')"
        ><span class="custom-ellipsis">{{t('打开分类选择框')}}</span></v-btn
      >
    </div>
    <!-- Tab -->
    <v-tabs
      v-model="tab"
      class="mt-2 mb-2"
      bg-color="rgb(var(--v-theme-prominent))"
    >
      <v-tab v-for="item in tabList" :value="item.value" :key="item.name" :class="item.value === tab ? 'activeTab':''">{{
        item.name
      }}</v-tab>
    </v-tabs>
    <v-data-table
      :headers="tab === DimType.Area ? areaHeaders : lengthHeader"
      :items="tableData()"
      :height="showDataList.length ? 200 : 100"
      :fixed-header="true"
      hide-default-footer
      class="text-caption"
      density="compact"
      item-value="name"
      hover
      :no-data-text="t('暂无数据')"
    >
      <template v-slot:item="{ item, index }">
        <tr
          :class="item.isChecked ? 'text-no-wrap checkedTr' : 'text-no-wrap'"
          @click="lookForEntity(item)"
        >
          <td>{{ index + 1 }}</td>
          <td>
            <div
              class="color-box"
              :style="{ backgroundColor: item.color }"
            ></div>
          </td>
          <td v-show="tab === DimType.Area">{{ item.areaVal }}</td>
          <td v-show="tab === DimType.Area">{{ item.perimeter }}</td>
          <td v-show="tab === DimType.Length">{{ item.lengthVal }}</td>
          <td>{{ item.category }}</td>
        </tr>
      </template>
    </v-data-table>
    <!-- 总计信息 -->
    <v-row class="mt-2">
      <v-col cols="12" md="9" class="d-flex align-center">
        <span class="font-weight-bold mr-2">{{t('总计：')}}</span>
      </v-col>
      <v-col cols="12" md="3" class="justify-start">
        <v-btn @click="copySummary"><span class="custom-ellipsis">{{t('复制统计结果')}}</span></v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <div class="summary-box">
          <p>
            {{t('总长度：')}} {{ summary.totalLength }};
            <br />
            {{t('总面积：')}} {{ summary.totalArea }};
            <br />
            {{t('总周长：')}} {{ summary.totalPerimeter }};
          </p>
        </div>
      </v-col>
    </v-row>
    <v-btn
      color="primary"
      @click="exportToExcel"
      class="exportBtn"
      :disabled="!showDataList.length"
      ><span class="custom-ellipsis">{{t('导出表格到Excel')}}</span></v-btn
    >

    <v-dialog v-model="alertDialog" max-width="200">
      <v-card>
        <v-card-title style="font-size: 1.2em">
          {{ t('提示') }}
        </v-card-title>
       <div style="padding:10px">
         <p>{{t('表格中没有带')}}{{alertText}}{{t('的标注')}},{{t('无法选择标注')}}{{alertText}}{{t('进行过滤')}}</p>
       </div>
        <template v-slot:actions>
          <v-spacer></v-spacer>
          <v-btn @click="alertDialog = false"><span class="custom-ellipsis"> {{ t("确定") }} </span></v-btn>
        </template>
      </v-card>
    </v-dialog>
    <v-dialog v-model="showChooseDialog" max-width="300">
      <v-card>
        <v-card-title style="font-size: 1.2em">
          {{ t(chooseListTitle) }}
        </v-card-title>
        <div class="filterBox">
          <div
            style="
              display: flex;
              justify-content: space-evenly;
              margin-bottom: 20px;
            "
          >
            <v-btn
              style="border: 1px solid #ccc; padding: 0px; height: 25px"
              @click="
                () => {
                  chooseListData.forEach((item) =>
                    item.forEach((i) => (i.isChecked = true))
                  );
                }
              "
              >{{t('全选')}}</v-btn
            >
            <v-btn
              style="border: 1px solid #ccc; padding: 0px; height: 25px"
              @click="
                () => {
                  chooseListData.forEach((item) =>
                    item.forEach((i) => (i.isChecked = false))
                  );
                }
              "
              >{{t('全不选')}}</v-btn
            >
          </div>
          <div
            v-for="item in chooseListData"
            :key="item"
            style="
              display: flex;
              justify-content: space-around;
              align-items: center;
            "
          >
            <v-checkbox v-for="i in item" :key="i" v-model="i.isChecked">
              <template v-slot:label>
                <div
                  v-if="filterFlag === 'color'"
                  class="color-box"
                  :style="{ backgroundColor: i.value }"
                ></div>
                <span v-else>{{ i.value }}</span>
              </template>
            </v-checkbox>
          </div>
        </div>
        <template v-slot:actions>
          <v-spacer></v-spacer>
          <v-btn @click="setFilter()" style="border: 1px solid #89cbfa">
            {{ t("确定") }}
          </v-btn>
          <v-btn
            @click="showChooseDialog = false"
            style="border: 1px solid #89cbfa"
          >
            {{ t("取消") }}
          </v-btn>
        </template>
      </v-card>
    </v-dialog>
  </div>
</template>

<script setup lang="ts">
import { MxCADResbuf, MxCADSelectionSet, MxCpp } from "mxcad";
import { ref, computed, onMounted, reactive } from "vue";
import {
  VDialog,
  VCard,
  VCardTitle,
  VCardText,
  VIcon,
  VBtn,
  VSpacer,
  VDivider,
  VRow,
  VCol,
  VRadioGroup,
  VRadio,
  VCheckbox,
  VTabs,
  VTab,
  VWindow,
  VWindowItem,
  VDataTable,
  VAlert,
} from "vuetify/components";
import { dialogWidth, isShowDialog } from "../dialog";
import { t } from "../../../i18n";
import {
  MxMeasureEntity,
  rgbToHex,
} from "../../../MeasurementAnnotation/hooks";
import { DimType } from "../../../MeasurementAnnotation/hooks";
import { MxFun } from "mxdraw";
// import * as XLSX from "xlsx";
import * as ExcelJS from "exceljs";
import { saveAs } from "file-saver";

const statRange = ref<"all" | "manual">("all");
const filterByColor = ref(false);
const filterByCategory = ref(false);
const tab = ref<DimType.Length | DimType.Area>(DimType.Length);
const showGray = ref(false);
const alertDialog = ref(false);
const alertText = ref("");
const showChooseDialog = ref(false);
const chooseListTitle = ref("");

// 切换lisr
const tabList = [
  { name: t("长度"), value: DimType.Length },
  { name: t("面积"), value: DimType.Area },
];

// 面积表头
const areaHeaders = [
  { title: t("序号"), value: "index" },
  { title: t("颜色"), value: "color" },
  { title: t("面积"), value: "areaVal" },
  { title: t("周长"), value: "perimeter" },
  { title: t("分类"), value: "category" },
];

// 长度表头
const lengthHeader = [
  { title: t("序号"), value: "index" },
  { title: t("颜色"), value: "color" },
  { title: t("长度"), value: "lengthVal" },
  { title: t("分类"), value: "category" },
];

interface dataList {
  id: string;
  isChecked: boolean;
  entity: MxMeasureEntity;
  color: string;
  category: string;
  dimType: DimType;
  perimeter?: number;
  areaVal?: number;
  lengthVal?: number;
}

// 所有标注实体集合
const allMeasureEntitys: dataList[] = reactive([]);
// 手动选择的标注集合
const manualSelectionEntitys: dataList[] = reactive([]);
// 获取图纸里所有的标注对象并进行分类
const getAllData = async (
  isSelectAll: Boolean = true,
  isResetFilter: Boolean = false
) => {
  allMeasureEntitys.length = 0;
  const ss = new MxCADSelectionSet();
  const filter = new MxCADResbuf();
  filter.AddString("CUSTOMENTITY", 5020);
  if (isSelectAll) {
    await ss.allSelect(filter);
  } else {
    await ss.userSelect(t("请点选或框选标注，右键结束选择"), filter);
  }
  ss.forEach((id) => {
    const ent = id.getMcDbEntity();
    if (ent instanceof MxMeasureEntity) {
      if (ent.dimType === DimType.Area || ent.dimType === DimType.Length) {
        const obj: dataList = {
          id: ent.getHandle(),
          color: rgbToHex(
            ent.trueColor.red,
            ent.trueColor.green,
            ent.trueColor.blue
          ),
          category: ent.categoryName,
          dimType: ent.dimType,
          entity: ent,
          isChecked: false,
        };
        if (ent.dimType === DimType.Area) {
          // 面积类
          obj.perimeter = (ent as any).weeklyValue;
          obj.areaVal = (ent as any).areaValue;
        } else {
          // 长度类
          obj.lengthVal = (ent as any).lengthValue;
        }
        allMeasureEntitys.push(obj);
        if (
          !manualSelectionEntitys.find((item) => item.id === ent.getHandle()) &&
          !isSelectAll
        )
          manualSelectionEntitys.push(obj);
      }
    }
  });
  if (isResetFilter) {
    filterColorArr.length = 0;
    filterColorArr.push(
      ...Array.from(new Set(allMeasureEntitys.map((item) => item.color)))
    );
    filterCategoryArr.length = 0;
    filterCategoryArr.push(
      ...Array.from(new Set(allMeasureEntitys.map((item) => item.category)))
    );
  }

  getShowData();
};

const showDataList: dataList[] = reactive([]);
const getShowData = () => {
  if (statRange.value === "manual") {
    allMeasureEntitys.length = 0;
    allMeasureEntitys.push(...manualSelectionEntitys);
  }
  const data = allMeasureEntitys.filter((cat) => {
    let result_1 = true;
    let result_2 = true;
    if (filterByColor.value) result_1 = filterColorArr.includes(cat.color);
    if (filterByCategory.value)
      result_2 = filterCategoryArr.includes(cat.category);
    return result_1 && result_2;
  });
  showDataList.length = 0;
  showDataList.push(...data);
};

const tableData = () => {
  return showDataList.filter((item: any) => item.dimType === tab.value);
};

onMounted(() => {
  dialogWidth.value = 500;
  getAllData(true, true);
});
const summary = computed(() => ({
  totalLength: allMeasureEntitys
    .map((item) => item.lengthVal || 0)
    .reduce((acc, cur) => acc + cur, 0)
    .toFixed(3),
  totalArea: allMeasureEntitys
    .map((item) => item.areaVal || 0)
    .reduce((acc, cur) => acc + cur, 0)
    .toFixed(3),
  totalPerimeter: allMeasureEntitys
    .map((item) => item.perimeter || 0)
    .reduce((acc, cur) => acc + cur, 0)
    .toFixed(3),
}));
function copySummary() {
  // 这里可用navigator.clipboard.writeText实现复制
  const text = `${t('总长度：')}${summary.value.totalLength};\n${t('总面积：')}${summary.value.totalArea}；\n${t('总周长：')}${summary.value.totalPerimeter};`;
  navigator.clipboard.writeText(text);
}

// 查看实体具体位置
const lookForEntity = (item: dataList) => {
  const { minPt, maxPt } = item.entity.getBoundingBox();
  const midPt = minPt.clone().addvec(maxPt.sub(minPt).mult(1 / 2));
  let mxdraw = MxFun.getCurrentDraw();
  mxdraw.getTempMarkDraw().clear();
  mxdraw
    .getTempMarkDraw()
    .drawCircleMark(midPt.toVector3(), mxdraw.viewCoordLong2Cad(50), 0xff0000);
  mxdraw.updateDisplay();

  MxCpp.getCurrentMxCAD().zoomCenter(midPt.x, midPt.y);

  allMeasureEntitys.forEach((i) => (i.isChecked = false));
  item.isChecked = true;
};

// 界面选择标注
const selectAnnotation = async () => {
  isShowDialog.value = false;
  await getAllData(false);
  isShowDialog.value = true;
};

const chooseListData: { value: string; isChecked: Boolean }[][] = reactive([]);
const chooseColorList: { value: string; isChecked: Boolean }[][] = reactive([]);
const chooseCategoryList: { value: string; isChecked: Boolean }[][] = reactive(
  []
);

function chunkArray(array: string[], size: number): string[][] {
  const result = [];
  for (let i = 0; i < array.length; i += size) {
    // 使用 slice 方法从原数组中提取元素并添加到结果数组中
    result.push(array.slice(i, i + size));
  }
  return result;
}

const filterFlag = ref("");
// 打开颜色/分类选择框
const openList = (flag: string) => {
  filterFlag.value = flag;
  const num = allMeasureEntitys.filter((item) => item.category !== "未分类");
  const text = flag === "color" ? t("颜色") : t("分类");
  if (!allMeasureEntitys.length || (flag === "category" && !num.length)) {
    alertDialog.value = true;
    alertText.value = text;
    return;
  } else {
    showChooseDialog.value = true;
    chooseListTitle.value =
      flag === "color" ? t("选择颜色分类") : t("选择标注分类");
    (filterFlag.value === "color"
      ? chooseColorList
      : chooseCategoryList
    ).length = 0;
    const data = Array.from(
      new Set(
        allMeasureEntitys.map((item) =>
          flag === "color" ? item.color : item.category
        )
      )
    );

    chunkArray(data, 4).forEach((item) => {
      const data = item.map((i) => {
        return {
          value: i,
          isChecked: (filterFlag.value === "color"
            ? filterColorArr
            : filterCategoryArr
          ).includes(i),
        };
      });
      (filterFlag.value === "color"
        ? chooseColorList
        : chooseCategoryList
      ).push(data);
    });
  }

  chooseListData.length = 0;
  chooseListData.push(
    ...(filterFlag.value === "color" ? chooseColorList : chooseCategoryList)
  );
};
// 筛选颜色
const filterColorArr: string[] = [];
// 筛选分类
const filterCategoryArr: string[] = [];
const setFilter = () => {
  showChooseDialog.value = false;
  filterFlag.value === "color"
    ? (filterColorArr.length = 0)
    : (filterCategoryArr.length = 0);
  (filterFlag.value === "color" ? chooseColorList : chooseCategoryList).forEach(
    (item) => {
      (filterFlag.value === "color" ? filterColorArr : filterCategoryArr).push(
        ...item.filter((i) => i.isChecked === true).map((j) => j.value)
      );
    }
  );
  getShowData();
};

// 选择模式修改
const modelChange = () => {
  if (statRange.value === "all") return getAllData(true);
  if (statRange.value === "manual") return getShowData();
};
// 导出excel文件
const exportToExcel = async () => {
  try {
    const workbook = new ExcelJS.Workbook();
    const worksheet = workbook.addWorksheet("测量数据统计");

    tabList.forEach((item, index) => {
      const header = (
        item.value === DimType.Length ? lengthHeader : areaHeaders
      ).filter((item) => item.value !== "color");
      const data = showDataList.filter((i: any) => i.dimType === item.value);
      if (index !== 0) worksheet.addRow([]);
      // 添加表格的标题
      const headerRow = worksheet.addRow(header.map((item) => item.title));
      applyHeaderStyle(headerRow, "FFD3D3D3"); // 灰色背景

      // 表格数据
      data
        .map((item: any, index) =>
          header.map((h) => {
            const val = h.value === "index" ? index + 1 : item[h.value];
            return val !== undefined ? val : "";
          })
        )
        .forEach((i) => {
          const dataRow = worksheet.addRow(i);
          applyCellStyle(dataRow);
        });
      const totalData = [];
      for (let i = 0; i < header.length; i++) {
        if (i === 0) {
          totalData.push("总计");
        } else {
          if (header[i].value === "category") {
            totalData.push("");
          } else {
            const total = data
              .map((item: any) => item[header[i].value])
              .reduce((acc, cur) => acc + cur, 0);
            totalData.push(total);
          }
        }
      }
      const dataRow = worksheet.addRow(totalData);
      applyCellStyle(dataRow);
    });

    // 设置所有有内容的单元格居中（不含空行）
    worksheet.eachRow((row) => {
      row.eachCell((cell) => {
        if (cell.value !== null && cell.value !== undefined) {
          cell.alignment = { horizontal: "center", vertical: "middle" };
        }
      });
    });

    // 导出文件
    const buffer = await workbook.xlsx.writeBuffer();
    const blob = new Blob([buffer], {
      type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
    });
    saveAs(blob, "测量结果统计.xlsx");
  } catch (error) {
    console.error("导出 Excel 出错:", error);
    alert("导出 Excel 失败，请重试。");
  }
};

// 设置表头样式
function applyHeaderStyle(row: any, bgColorHex: string) {
  row.eachCell((cell: any) => {
    cell.font = { bold: true }; // 加粗字体
    cell.fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: bgColorHex }, // 背景色
    };
  });
}
// 设置表格样式
function applyCellStyle(row: any) {
  row.eachCell((cell: any) => {
    cell.alignment = {
      horizontal: "center",
      vertical: "middle",
    };
    cell.border = {
      top: { style: "thin" },
      left: { style: "thin" },
      bottom: { style: "thin" },
      right: { style: "thin" },
    };
  });
}
</script>

<style scoped lang="scss">
.static {
  border: 1px solid rgb(204, 204, 204, 0.5);
  padding: 10px;
  position: relative;
  .static-title {
    position: absolute;
    top: -10px;
    left: 10px;
    background-color: rgb(var(--v-theme-dialog-card-text));
  }
}
.color-box {
  width: 20px;
  height: 20px;
  border-radius: 4px;
  border: 1px solid #ccc;
  display: inline-block;
}
.summary-box {
  background-color: rgb(var(--v-theme-prominent));
  border-radius: 4px;
  padding: 8px 12px;
  border: 1px solid rgb(204, 204, 204, 0.6);
}
.selected-row {
  background: #e3f2fd !important;
}
.filter-options {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 30px;
  margin-top: 10px;
}
.text-no-wrap {
  border-top: 1px solid rgb(204, 204, 204, 0.5);
}
.exportBtn {
  position: absolute;
  right: 0px;
  bottom: -35px;
}
:deep(.v-data-table-header__content) {
  justify-content: center;
}
:deep(
    .v-table.v-table--fixed-header > .v-table__wrapper > table > thead > tr > th
  ) {
  height: 30px;
}
:deep(.v-tab.v-tab.v-btn) {
  height: 30px;
}
:deep(.v-tabs--density-compact) {
  height: 30px;
}
.checkedTr {
  background-color: rgba(137, 203, 250, 0.5);
}
.filterBox {
  max-height: 200px;
  overflow: auto;
  width: 100%;
  padding: 10px;
}
.custom-ellipsis {
  display: inline-block;     /* 或 block，根据你的布局需求 */
  white-space: nowrap;       /* 禁止换行 */
  overflow: hidden;          /* 超出部分隐藏 */
  text-overflow: ellipsis;   /* 添加省略号 */
  max-width: 125px;          /* 设置最大宽度，或者由父容器控制 */
}
.activeTab{
  background-color: rgb(137, 203, 250, 0.5);
}
</style>