// 审图标注框
class McDbExtoolRectBoxLeadComment extends McDbCustomEntity {
    /** 云线角点1 */
    private pt1: McGePoint3d;
    /** 云线角点2 */
    private pt2: McGePoint3d;
    /** 审图标注点 */
    private pt3: McGePoint3d;
    /** 审图标注内容 */
    private _text: string = "";
    /** 字高 */
    private _textsize: number = 20;
    /** 云线半圆弧半径 */
    private _radius: number = 15;
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d;
    private maxPt: McGePoint3d;

    constructor(imp?: any) {
        super(imp);
    }
    public create(imp: any) {
        return new McDbExtoolRectBoxLeadComment(imp)
    }
    public getTypeName(): string {
        return "McDbExtoolRectBoxLeadComment";
    }

    //设置或获取文本值
    public set text(val: string) {
        this._text = val;
    }
    public get text(): string {
        return this._text;
    }
    //设置或获取文本大小
    public set textsize(val: number) {
        this._textsize = val;
    }
    public get textsize(): number {
        return this._textsize;
    }
    //设置或获取云线半圆弧半径
    public set radius(val: number) {
        this._radius = val;
    }
    public get radius(): number {
        return this._radius;
    }

    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.pt1 = filter.readPoint("pt1").val;
        this.pt2 = filter.readPoint("pt2").val;
        this.pt3 = filter.readPoint("pt3").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        this._text = filter.readString("text").val;
        this._textsize = filter.readDouble("textsize").val;
        return true;
    }
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("pt1", this.pt1);
        filter.writePoint("pt2", this.pt2);
        filter.writePoint("pt3", this.pt3);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        filter.writeString("text", this._text);
        filter.writeDouble("textsize", this._textsize);
        return true;
    }
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        if (iIndex === 0) {
            this.pt1.x += dXOffset;
            this.pt1.y += dYOffset;
            this.pt1.z += dZOffset;
        } else if (iIndex === 1) {
            this.pt2.x += dXOffset;
            this.pt2.y += dYOffset;
            this.pt2.z += dZOffset;
        } else if (iIndex === 2) {
            this.pt3.x += dXOffset;
            this.pt3.y += dYOffset;
            this.pt3.z += dZOffset;
        }
    };
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray();
        ret.append(this.pt1);
        ret.append(this.pt2);
        ret.append(this.pt3);
        return ret;
    };
    public worldDraw(draw: MxCADWorldDraw): void {

        const allEntityArr: McDbEntity[] = [];

        const radius = this._radius;

        // 根据弧线半径，重新计算云线角点
        if (this.pt1.x < this.pt2.x) {
            const dist = this.pt2.x - this.pt1.x;
            const num = parseInt((dist / (radius * 2)).toString());
            this.pt2.x = this.pt1.x + radius * 2 * num;
        } else {
            const dist = this.pt1.x - this.pt2.x;
            const num = parseInt((dist / (radius * 2)).toString());
            this.pt1.x = this.pt2.x + radius * 2 * num;
        }

        if (this.pt1.y < this.pt2.y) {
            const dist = this.pt2.y - this.pt1.y;
            const num = parseInt((dist / (radius * 2)).toString());
            this.pt2.y = this.pt1.y + radius * 2 * num;
        } else {
            const dist = this.pt1.y - this.pt2.y;
            const num = parseInt((dist / (radius * 2)).toString());
            this.pt1.y = this.pt2.y + radius * 2 * num;
        }
        // 审图框线
        const pl = new McDbPolyline();
        pl.isClosed = true;
        pl.addVertexAt(this.pt1);
        pl.addVertexAt(new McGePoint3d(this.pt2.x, this.pt1.y));
        pl.addVertexAt(this.pt2);
        pl.addVertexAt(new McGePoint3d(this.pt1.x, this.pt2.y));

        // 根据审图框线与半径获取出云线所有圆弧的圆心
        const length = pl.getLength().val;
        let num = length / radius;
        // 获取圆心
        const circleCenterArr: McGePoint3d[] = [];
        for (let i = 1; i <= num; i++) {
            const pt = pl.getPointAtDist(radius * i).val;
            circleCenterArr.push(pt)
        }

        // 根据圆心与圆弧半径绘制云线圆弧
        circleCenterArr.forEach((center, index) => {
            const circle = new McDbCircle(center.x, center.y, center.z, radius);
            if (index % 2 === 0) {
                const ptArr: McGePoint3d[] = []
                circle.IntersectWith(pl, McDb.Intersect.kOnBothOperands).forEach(pt => {
                    ptArr.push(pt)
                });
                circle.splitCurves(ptArr).forEach(e => {
                    const pt = (e as McDbCurve).getPointAtDist((e as McDbCurve).getLength().val / 2).val;
                    if (pt.x < Math.min(...[this.pt1.x, this.pt2.x]) || pt.x > Math.max(...[this.pt1.x, this.pt2.x])) {
                        draw.drawEntity(e as McDbCurve);
                        allEntityArr.push(e as McDbCurve);
                    }
                    if (pt.y < Math.min(...[this.pt1.y, this.pt2.y]) || pt.y > Math.max(...[this.pt1.y, this.pt2.y])) {
                        draw.drawEntity(e as McDbCurve);
                        allEntityArr.push(e as McDbCurve);
                    }
                })
            }
        });
        /**
         * 绘制标注
         */
        const distanceArr: number[] = []
        for (let i = 0; i < pl.numVerts(); i++) {
            const dist = pl.getPointAt(i).val.distanceTo(this.pt3);
            distanceArr.push(dist);
        };
        const index = distanceArr.indexOf(Math.min(...distanceArr));
        const pt = pl.getPointAt(index).val;

        const pl_text = new McDbPolyline();
        pl_text.addVertexAt(pt);
        pl_text.addVertexAt(this.pt3);
        if (this._text) {
            const text = new McDbText();
            text.textString = this._text;
            text.position = text.alignmentPoint = this.pt3;
            text.height = 20;
            draw.drawEntity(text);
            allEntityArr.push(text);
            const { minPt, maxPt, ret } = text.getBoundingBox();
            if (!ret) return;

            const textWidth = maxPt.x - minPt.x;
            pl_text.addVertexAt(new McGePoint3d(this.pt3.x + textWidth, this.pt3.y));
            draw.drawEntity(pl_text);
            allEntityArr.push(pl_text)
        } else {
            draw.drawEntity(pl_text);
            allEntityArr.push(pl_text);
        }

        this.getBox(allEntityArr);
    }

    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbText) {
                const ent = new McDbMText()
                ent.contents = entity.textString;
                ent.textHeight = entity.height;
                ent.location = entity.position;
                switch (entity.horizontalMode) {
                    case McDb.TextHorzMode.kTextLeft:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                    case McDb.TextHorzMode.kTextCenter:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    case McDb.TextHorzMode.kTextRight:
                        ent.attachment = McDb.AttachmentPoint.kBottomRight;
                        break;
                    case McDb.TextHorzMode.kTextMid:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    default:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                }
                // 将ent的文字样式设置为当前文字样式
                const textStyleId = mxcad.getDatabase().getCurrentlyTextStyleId();
                ent.textStyleId = textStyleId;
                ent.reCompute();
                result = MxCADUtility.getTextEntityBox(ent, false);
            } else if (entity instanceof McDbMText) {
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }

    public setPoint1(pt1: McGePoint3d) {
        this.assertWrite();
        this.pt1 = pt1.clone();
    }
    public setPoint2(pt2: McGePoint3d) {
        this.assertWrite();
        this.pt2 = pt2.clone();
    }
    public setPoint3(pt3: McGePoint3d) {
        this.assertWrite();
        this.pt3 = pt3.clone();
    }
    public getPoint1() {
        return this.pt1;
    }
    public getPoint2() {
        return this.pt2;
    }
    public getPoint3() {
        return this.pt3;
    }

    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
}

// 审图标注
async function Mx_Approval() {
    // 创建审图批注
    const rectBoxLeadComment = new McDbExtoolRectBoxLeadComment();
    // 设置审批意见
    const getContent = new MxCADUiPrString();
    getContent.setMessage(t("请输入审批意见"));
    let content = await getContent.go();
    if (!content) content = "审批意见XXXX"
    rectBoxLeadComment.text = content;

    // 设置云线框角点
    const getPt1 = new MxCADUiPrPoint();
    getPt1.setMessage(t("指定云线框起始点"));
    const pt1 = await getPt1.go();
    if (!pt1) return;
    const getPt2 = new MxCADUiPrPoint();
    getPt2.setMessage(t("指定云线框终止点"));
    getPt2.setUserDraw((pt, pw) => {
        rectBoxLeadComment.setPoint1(pt1);
        rectBoxLeadComment.setPoint2(pt);
        rectBoxLeadComment.setPoint3(pt);
        pw.drawMcDbEntity(rectBoxLeadComment);
    })
    const pt2 = await getPt2.go();
    if (!pt2) return;
    rectBoxLeadComment.setPoint2(pt2);

    // 设置审图标注点
    const getPt3 = new MxCADUiPrPoint();
    getPt3.setMessage(t("指定审图标注点"));
    getPt3.setUserDraw((pt, pw) => {
        rectBoxLeadComment.setPoint3(pt);
        pw.drawMcDbEntity(rectBoxLeadComment);
    })
    const pt3 = await getPt3.go();
    if (!pt3) return;
    rectBoxLeadComment.setPoint3(pt3);

    // 绘制审图标注对象
    MxCpp.getCurrentMxCAD().drawEntity(rectBoxLeadComment);
}

// 新创建 McDbExtoolAnnotatedRectangle 类继承 McDbCustomEntity
class McDbExtoolAnnotatedRectangle extends McDbCustomEntity {
    // 定义McDbTestLineCustomEntity内部的点对象 
    // 矩形角点pt1、pt2
    private pt1: McGePoint3d = new McGePoint3d();
    private pt2: McGePoint3d = new McGePoint3d();
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d;
    private maxPt: McGePoint3d;
    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new McDbExtoolAnnotatedRectangle(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "McDbExtoolAnnotatedRectangle";
    }
    // 读取自定义实体数据pt1、pt2
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.pt1 = filter.readPoint("pt1").val;
        this.pt2 = filter.readPoint("pt2").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        return true;
    }
    // 写入自定义实体数据pt1、pt2
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("pt1", this.pt1);
        filter.writePoint("pt2", this.pt2);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        if (iIndex == 0) {
            this.pt1.x += dXOffset;
            this.pt1.y += dYOffset;
            this.pt1.z += dZOffset;
        }
        else if (iIndex == 1) {
            this.pt2.x += dXOffset;
            this.pt2.y += dYOffset;
            this.pt2.z += dZOffset;
        }
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray()
        ret.append(this.pt1);
        ret.append(this.pt2);
        return ret;
    };
    // 绘制实体
    public worldDraw(draw: MxCADWorldDraw): void {

        const allEntityArr: McDbEntity[] = [];

        const pl = new McDbPolyline();
        pl.isClosed = true;
        pl.addVertexAt(this.pt1);
        pl.addVertexAt(new McGePoint3d(this.pt2.x, this.pt1.y));
        pl.addVertexAt(this.pt2);
        pl.addVertexAt(new McGePoint3d(this.pt1.x, this.pt2.y));
        draw.drawEntity(pl);
        allEntityArr.push(pl)

        let mxcad = MxCpp.getCurrentMxCAD();
        const pt3 = new McGePoint3d(this.pt2.x, this.pt1.y);
        mxcad.addDimStyle("MyDimStyle", "41,0.18,141,0.09,40,200", "77,1,271,3", "", "");
        mxcad.drawDimStyle = "MyDimStyle";
        const id = mxcad.drawDimAligned(this.pt1.x, this.pt1.y + 20, pt3.x, pt3.y + 20, (this.pt1.x + pt3.x) / 2, this.pt1.y + 20);
        draw.drawEntity(id.getMcDbEntity());
        allEntityArr.push(id.getMcDbEntity())

        id.erase();
        const _id = mxcad.drawDimAligned(this.pt2.x + 20, this.pt2.y, pt3.x + 20, pt3.y, pt3.x + 20, (this.pt2.y + pt3.y) / 2);
        draw.drawEntity(_id.getMcDbEntity());
        allEntityArr.push(_id.getMcDbEntity())
        _id.erase();

        this.getBox(allEntityArr);
    }

    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbText) {
                const ent = new McDbMText()
                ent.contents = entity.textString;
                ent.textHeight = entity.height;
                ent.location = entity.position;
                switch (entity.horizontalMode) {
                    case McDb.TextHorzMode.kTextLeft:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                    case McDb.TextHorzMode.kTextCenter:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    case McDb.TextHorzMode.kTextRight:
                        ent.attachment = McDb.AttachmentPoint.kBottomRight;
                        break;
                    case McDb.TextHorzMode.kTextMid:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    default:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                }
                // 将ent的文字样式设置为当前文字样式
                const textStyleId = mxcad.getDatabase().getCurrentlyTextStyleId();
                ent.textStyleId = textStyleId;
                ent.reCompute();
                result = MxCADUtility.getTextEntityBox(ent, false);
            } else if (entity instanceof McDbMText) {
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }

    // 设置pt1
    public setPoint1(pt1: McGePoint3d) {
        this.assertWrite();
        this.pt1 = pt1.clone();
    }
    // 设置pt2
    public setPoint2(pt2: McGePoint3d) {
        this.assertWrite();
        this.pt2 = pt2.clone();
    }
    // 获取pt1
    public getPoint1() {
        return this.pt1;
    }
    // 获取pt2
    public getPoint2() {
        return this.pt2;
    }

    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
}

// 矩形标注
async function Mx_AnnotatedRectangle() {
    let mxcad = MxCpp.getCurrentMxCAD();
    const getPoint = new MxCADUiPrPoint();
    getPoint.setMessage(t("指定第一个角点:"));
    let pt1 = await getPoint.go();
    if (!pt1) return;
    const myRect = new McDbExtoolAnnotatedRectangle();
    myRect.setPoint1(pt1);

    getPoint.setMessage(t("指定第二个角点:"));
    getPoint.setUserDraw((pt, pw) => {
        myRect.setPoint2(pt);
        pw.drawMcDbEntity(myRect);
    })
    let pt2 = await getPoint.go();
    if (!pt2) return;

    myRect.setPoint2(pt2);
    mxcad.drawEntity(myRect);
}

// 等分点标注
export class MxDbExtoolDivide extends McDbCustomEntity {
    // 定义MxDbExtoolDivide内部的点对象 
    // 等分点圆心，半径
    private center: McGePoint3d = new McGePoint3d();
    private _radius: number = 1;
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();;
    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new MxDbExtoolDivide(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "MxDbExtoolDivide";
    }
    //设置或获取云线半圆弧半径
    public set radius(val: number) {
        this._radius = val;
    }
    public get radius(): number {
        return this._radius;
    }
    // 读取自定义实体数据pt1、pt2
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.center = filter.readPoint("center").val;
        this._radius = filter.readDouble("radius").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        return true;
    }
    // 写入自定义实体数据pt1、pt2
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("center", this.center);
        filter.writeDouble("radius", this._radius);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        this.center.x += dXOffset;
        this.center.y += dYOffset;
        this.center.z += dZOffset;
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray()
        ret.append(this.center);
        return ret;
    };
    // 绘制实体
    public worldDraw(draw: MxCADWorldDraw): void {

        const allEntityArr: McDbEntity[] = [];

        const circle = new McDbCircle(this.center.x, this.center.y, this.center.z, this._radius);
        draw.drawEntity(circle);
        const { minPt, maxPt, ret } = circle.getBoundingBox();
        if (!ret) return;
        const line1 = new McDbLine(minPt, maxPt);
        const line2 = new McDbLine(new McGePoint3d(minPt.x, maxPt.y, 0), new McGePoint3d(maxPt.x, minPt.y, 0));
        draw.drawEntity(line1);
        draw.drawEntity(line2);

        allEntityArr.push(circle)
        allEntityArr.push(line1)
        allEntityArr.push(line2);

        this.getBox(allEntityArr);
    }

    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbText) {
                const ent = new McDbMText()
                ent.contents = entity.textString;
                ent.textHeight = entity.height;
                ent.location = entity.position;
                switch (entity.horizontalMode) {
                    case McDb.TextHorzMode.kTextLeft:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                    case McDb.TextHorzMode.kTextCenter:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    case McDb.TextHorzMode.kTextRight:
                        ent.attachment = McDb.AttachmentPoint.kBottomRight;
                        break;
                    case McDb.TextHorzMode.kTextMid:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    default:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                }
                // 将ent的文字样式设置为当前文字样式
                const textStyleId = mxcad.getDatabase().getCurrentlyTextStyleId();
                ent.textStyleId = textStyleId;
                ent.reCompute();
                result = MxCADUtility.getTextEntityBox(ent, false);
            } else if (entity instanceof McDbMText) {
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }

    // 设置圆心
    public setCenter(pt: McGePoint3d) {
        this.assertWrite();
        this.center = pt.clone();
    }
    // 获取圆心
    public getCenter() {
        return this.center;
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
}

// 对齐
async function Mx_Alignment() {
    // 选择要对齐水平标注或竖直标注[水平(H)/竖直(V)]<H>:
    const getKey = new MxCADUiPrKeyWord;
    getKey.setMessage(`选择要对齐水平标注或竖直标注：`)
    getKey.setKeyWords("[水平(H)/竖直(V)]");
    const keyVal = await getKey.go();
    if (!keyVal) return;

    // 选择对齐标注
    const ss = new MxCADSelectionSet();
    if (!await ss.userSelect("选择对齐标注:")) return;
    if (ss.count() == 0) return;
    const alignArr: McDbRotatedDimension[] = [];
    ss.forEach(id => {
        const ent = id.getMcDbEntity();
        if (ent.objectName === 'McDbRotatedDimension') {
            const rDim = ent.clone() as McDbRotatedDimension;
            const xDist = Math.abs(rDim.xLine1Point.x - rDim.xLine2Point.x).toFixed(0);
            const yDist = Math.abs(rDim.xLine1Point.y - rDim.xLine2Point.y).toFixed(0);
            const xNum = Math.abs(rDim.dimLinePoint.x - rDim.xLine1Point.x) + Math.abs(rDim.dimLinePoint.x - rDim.xLine2Point.x);
            const yNum = Math.abs(rDim.dimLinePoint.y - rDim.xLine1Point.y) + Math.abs(rDim.dimLinePoint.y - rDim.xLine2Point.y);

            if (keyVal === 'H' && xNum.toFixed(0) == xDist) {
                alignArr.push(ent as McDbRotatedDimension)
            };
            if (keyVal === 'V' && yNum.toFixed(0) == yDist) {
                alignArr.push(ent as McDbRotatedDimension)
            };
        }
    });
    if (alignArr.length === 0) return;
    const mxcad = MxCpp.getCurrentMxCAD();
    // 设置尺寸线位置
    const getPt1 = new MxCADUiPrPoint();
    getPt1.setMessage('设置尺寸线位置');
    const pt1 = await getPt1.go();
    if (!pt1) return;
    alignArr.forEach(ent => {
        ent.dimLinePoint = pt1;
    });
    mxcad.updateDisplay();

    // 设置尺寸线界限
    const getPt2 = new MxCADUiPrPoint();
    getPt2.setMessage('设置尺寸线界限');
    const pt2 = await getPt2.go();
    if (!pt2) return;
    alignArr.forEach(ent => {
        if (keyVal === 'H') {
            ent.xLine1Point = new McGePoint3d(ent.xLine1Point.x, pt2.y);
            ent.xLine2Point = new McGePoint3d(ent.xLine2Point.x, pt2.y);
        } else if (keyVal === 'V') {
            ent.xLine1Point = new McGePoint3d(pt2.x, ent.xLine1Point.y);
            ent.xLine2Point = new McGePoint3d(pt2.x, ent.xLine2Point.y);
        }
    });
    mxcad.updateDisplay();
}

// 序号标注
export class McDbExtoolIndex extends McDbCustomEntity {
    // 定义McDbExtoolIndex内部的点对象 
    // 序号点位置
    private numPos: McGePoint3d = new McGePoint3d();
    // 标注点位置
    private labelPos: McGePoint3d = new McGePoint3d();
    // 标注点大小
    private labelSize: number = MxFun.screenCoordLong2Doc(5);
    // 序号点大小
    private numSize: number = MxFun.screenCoordLong2Doc(10);
    private index: string = "1";
    /** 包围盒最大点 包围盒最小点 */
    private minPt: McGePoint3d = new McGePoint3d();
    private maxPt: McGePoint3d = new McGePoint3d();


    // 构造函数
    constructor(imp?: any) {
        super(imp);
    }
    // 创建函数
    public create(imp: any) {
        return new McDbExtoolIndex(imp)
    }
    // 获取类名
    public getTypeName(): string {
        return "McDbExtoolIndex";
    }
    public set symbolIndex(val: string) {
        this.index = val;
    }
    public get symbolIndex(): string {
        return this.index;
    }
    // 读取自定义实体数据pt1、pt2
    public dwgInFields(filter: IMcDbDwgFiler): boolean {
        this.numPos = filter.readPoint("numPos").val;
        this.labelPos = filter.readPoint("labelPos").val;
        this.index = filter.readString("index").val;
        this.labelSize = filter.readDouble("labelSize").val;
        this.numSize = filter.readDouble("numSize").val;
        this.minPt = filter.readPoint("minPt").val;
        this.maxPt = filter.readPoint("maxPt").val;
        return true;
    }
    // 写入自定义实体数据pt1、pt2
    public dwgOutFields(filter: IMcDbDwgFiler): boolean {
        filter.writePoint("labelPos", this.labelPos);
        filter.writePoint("numPos", this.numPos);
        filter.writeString("index", this.index);
        filter.writeDouble("labelSize", this.labelSize);
        filter.writeDouble("numSize", this.numSize);
        filter.writePoint("minPt", this.minPt);
        filter.writePoint("maxPt", this.maxPt);
        return true;
    }

    // 移动自定义对象的夹点
    public moveGripPointsAt(iIndex: number, dXOffset: number, dYOffset: number, dZOffset: number) {
        this.assertWrite();
        if (iIndex === 0) {
            this.labelPos.x += dXOffset;
            this.labelPos.y += dYOffset;
            this.labelPos.z += dZOffset;
        } else {
            this.numPos.x += dXOffset;
            this.numPos.y += dYOffset;
            this.numPos.z += dZOffset;
            this.labelPos.x += dXOffset;
            this.labelPos.y += dYOffset;
            this.labelPos.z += dZOffset;
        }
    };
    // 获取自定义对象的夹点
    public getGripPoints(): McGePoint3dArray {
        let ret = new McGePoint3dArray()
        ret.append(this.labelPos);
        ret.append(this.numPos);
        return ret;
    };
    // 绘制实体
    public worldDraw(draw: MxCADWorldDraw): void {

        const allEntityArr: McDbEntity[] = [];

        const solid = new McDbHatch();
        solid.appendCircleLoop(this.labelPos.x, this.labelPos.y, this.labelSize);
        draw.drawEntity(solid);
        allEntityArr.push(solid)

        const number = new McDbText();
        number.textString = this.index;
        number.height = this.numSize * (4 / 5);
        number.position = this.numPos.clone().subvec(McGeVector3d.kYAxis.clone().mult(this.numSize * (2 / 5)));
        number.alignmentPoint = number.position;
        number.horizontalMode = McDb.TextHorzMode.kTextMid;
        draw.drawEntity(number);
        allEntityArr.push(number)

        const circle = new McDbCircle(this.numPos.x, this.numPos.y, this.numPos.z, this.numSize);
        draw.drawEntity(circle);
        allEntityArr.push(circle)

        const v = this.labelPos.sub(this.numPos).normalize().mult(this.numSize);
        const endPt = this.numPos.addvec(v);
        const line = new McDbLine(this.labelPos, endPt);
        draw.drawEntity(line);
        allEntityArr.push(line);

        this.getBox(allEntityArr);
    }

    private getBox(entityArr: McDbEntity[]) {
        const mxcad = MxCpp.getCurrentMxCAD();
        let _minPt, _maxPt = null;
        let result;
        entityArr.forEach(entity => {
            if (entity instanceof McDbText) {
                const ent = new McDbMText()
                ent.contents = entity.textString;
                ent.textHeight = entity.height;
                ent.location = entity.position;
                switch (entity.horizontalMode) {
                    case McDb.TextHorzMode.kTextLeft:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                    case McDb.TextHorzMode.kTextCenter:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    case McDb.TextHorzMode.kTextRight:
                        ent.attachment = McDb.AttachmentPoint.kBottomRight;
                        break;
                    case McDb.TextHorzMode.kTextMid:
                        ent.attachment = McDb.AttachmentPoint.kBottomCenter;
                        break;
                    default:
                        ent.attachment = McDb.AttachmentPoint.kBottomLeft;
                        break;
                }
                // 将ent的文字样式设置为当前文字样式
                const textStyleId = mxcad.getDatabase().getCurrentlyTextStyleId();
                ent.textStyleId = textStyleId;
                ent.reCompute();
                result = MxCADUtility.getTextEntityBox(ent, false);
            } else if (entity instanceof McDbMText) {
                entity.reCompute();
                result = MxCADUtility.getTextEntityBox(entity, false);
            } else {
                result = entity.getBoundingBox();
            }
            const { minPt, maxPt, ret } = result;
            if (!_minPt) _minPt = minPt.clone();
            if (!_maxPt) _maxPt = maxPt.clone();
            if (minPt.x < _minPt.x) _minPt.x = minPt.x;
            if (minPt.y < _minPt.y) _minPt.y = minPt.y;
            if (maxPt.x > _maxPt.x) _maxPt.x = maxPt.x;
            if (maxPt.y > _maxPt.y) _maxPt.y = maxPt.y;
        });
        if (_minPt && _maxPt) {
            this.maxPt = _maxPt;
            this.minPt = _minPt
        }
    }

    // 设置序号点位置
    public setLabelPos(pt: McGePoint3d) {
        this.labelPos = pt.clone();
    }
    // 获取序号点位置
    public getLabelPos() {
        return this.labelPos;
    }
    // 设置圆心
    public setNumPos(pt: McGePoint3d) {
        this.numPos = pt.clone();
    }
    // 获取圆心
    public getNumPos() {
        return this.numPos;
    }
    // 获取包围盒
    public getBoundingBox(): { minPt: McGePoint3d; maxPt: McGePoint3d; ret: boolean; } {
        return { minPt: this.minPt, maxPt: this.maxPt, ret: true }
    }
}
// 绘制序号标注
async function Mx_indexSymbol() {
    let num = 1;
    while (true) {
        const getPoint = new MxCADUiPrPoint();
        getPoint.setMessage(t("指定标注点:"));
        let labelPos = await getPoint.go();
        if (!labelPos) break;
        const myIndex = new McDbExtoolIndex();
        myIndex.setLabelPos(labelPos);

        const getIndex = new MxCADUiPrPoint();
        getIndex.setMessage(t("指定序号位置:"));
        getIndex.setUserDraw((pt, pw) => {
            const _myIndex = myIndex.clone() as McDbExtoolIndex;
            _myIndex.setNumPos(pt);
            pw.drawMcDbEntity(_myIndex);
        });
        let indexPos = await getIndex.go();
        if (!indexPos) break;
        myIndex.setNumPos(indexPos);
        myIndex.symbolIndex = num.toString();

        const mxcad = MxCpp.getCurrentMxCAD();
        mxcad.drawEntity(myIndex);
        num += 1;
    }
}

export function init() {
    new McDbExtoolRectBoxLeadComment().rxInit();
    new McDbExtoolAnnotatedRectangle().rxInit();
    new MxDbExtoolDivide().rxInit();
    new McDbExtoolIndex().rxInit();

    MxFun.addCommand("Mx_Extool_Approval", Mx_Approval);
    MxFun.addCommand("Mx_Extool_AnnotatedRectangle", Mx_AnnotatedRectangle);
    MxFun.addCommand("Mx_Extool_Alignment", Mx_Alignment);
    MxFun.addCommand("Mx_Extool_indexSymbol", Mx_indexSymbol);
}