<template>
    <div class="content">
        <div style="display:flex;align-items: center;margin-bottom: 10px;">
            <span>{{ t('标注位置设置：') }}</span>
            <div class="radio" v-for="item in markPosArr" :key="item.id">
                <input type="radio" :id="item.name" v-model="dataForm.markPos" :value="item.id" />
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div style="display:flex;align-items: center;margin-bottom: 10px;">
            <span>{{ t('文字位置设置：') }}</span>
            <div class="radio" v-for="item in textPosArr" :key="item.id">
                <input type="radio" :id="item.name" v-model="dataForm.textPos" :value="item.id" />
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div style="display:flex;align-items: center;">
            <span>{{ t('精度单位设置：') }}</span>
            <div>
                <div class="radio" v-for="item in precisionArr" :key="item.id" style="display: flex;align-items: center;">
                    <input type="radio" :id="item.name" v-model="dataForm.precision" :value="item.id" />
                    <label :for="item.name">{{ item.name }}</label>
                    <div class="custom-select" v-show="item.id === 2" style="width: 80px;margin-left: 10px">
                        <select v-model="dataForm.precisionVal" :disabled="dataForm.precision != 2">
                            <option :value="index" v-for="(item, index) in 7" :key="index">{{ (item*0).toFixed(index) }}
                            </option>
                        </select>
                        <div class="select-arrow"></div>
                    </div>
                </div>
            </div>
        </div>
        <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
            <button class="btn_normal" @click="sureDraw" style="margin: 10px 0px 5px;color: #fff!important;">{{
                t('确定') }}</button>
        </div>
    </div>
</template>

<script setup lang="ts">
import { reactive } from 'vue';
import { t } from '../../i18n';
import { markPos, textPos, drawElevationMark } from "./hooks";
import { isOpen } from "../../index";

const dataForm: any = reactive({
    markPos: markPos.freeLabeling,
    textPos: textPos.start,
    precisionVal:0,
    precision:1,
});

const markPosArr = reactive([
    { name: t('自由标注'), id: markPos.freeLabeling },
    { name: t('对齐标注'), id: markPos.alignmentMark }
]);
const textPosArr = reactive([
    { name: t('文字齐始端'), id: textPos.start },
    { name: t('文字齐线端'), id: textPos.end }
]);
const precisionArr = reactive([
    { name: t('国家规范（0.000）'), id: 1 },
    { name: t('自定义设置'), id: 2 }
]);

// 确定绘制箭头引注
const sureDraw = async () => {
    isOpen.value = false;
    let precisionVal = 3;
    if(dataForm.precision === 2){
        precisionVal = dataForm.precisionVal;
    }
    drawElevationMark(precisionVal, dataForm.markPos, dataForm.textPos);
}

</script>

<style lang="scss" scoped>
@import '../../assets/dialogContens.scss';
</style>