<template>
    <div class="content">
        <div style="display:flex;justify-content:space-between;align-items: center;">
            <div class="inputBox">
                <span>{{ `${t('索引图号')}：` }}</span>
                <input type="text" v-model="dataForm.indexDrawNumber" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" style="width: 50px;" />
            </div>
            <div class="inputBox">
                <span>{{ `${t('索引编号')}：` }}</span>
                <input type="text" v-model="dataForm.number" @focus="() => { setCommandFocus(false) }"
                    @blur="() => { setCommandFocus(true); }" style="width: 50px;" />
            </div>
        </div>
        <div class="inputBox">
            <span>{{ `${t('上标文字')}：` }}</span>
            <input type="text" v-model="dataForm.textUp" @focus="() => { setCommandFocus(false) }"
                @blur="() => { setCommandFocus(true); }" />
        </div>
        <div class="inputBox">
            <span>{{ `${t('下标文字')}：` }}</span>
            <input type="text" v-model="dataForm.textDown" @focus="() => { setCommandFocus(false) }"
                @blur="() => { setCommandFocus(true); }" />
        </div>
        <div style="display:flex;justify-content:space-between;align-items: center;margin-bottom: 10px;">
            <span>{{ t('对齐方式：') }}</span>
            <div class="radio" v-for="item in typeArr" :key="item.id">
                <input type="radio" :id="item.name" v-model="dataForm.alginType" :value="item.id" />
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div style="display:flex;justify-content:space-between;align-items: center;">
            <span>{{ t('索引样式：') }}</span>
            <div class="radio" v-for="item in styleType" :key="item.id">
                <input type="radio" :id="item.name" v-model="dataForm.styleType" :value="item.id" />
                <label :for="item.name">{{ item.name }}</label>
            </div>
        </div>
        <div style="display:flex;align-items: center;margin-top: 10px;" class="custom-checkbox"
            v-if="dataForm.styleType === indexStyle.Directed">
            <input type="checkbox" id="aa" value="aa" v-model="dataForm.isCheckAA">
            <span>{{ t('固定角度：') }}</span>
            <div class="custom-select" style="width: 50px;">
                <select v-model="dataForm.alignAngle" :disabled="!dataForm.isCheckAA">
                    <option :value="item.id" v-for="item in alignAngleArr" :key="item.id">{{ item.name }}</option>
                </select>
                <div class="select-arrow"></div>
            </div>
            <div style="display:flex;align-items: center;margin-left: 20px;" class="custom-checkbox">
                <input type="checkbox" id="aa" value="aa" v-model="dataForm.addIndexArea">
                <span>{{ t('添加索引范围') }}</span>
            </div>
        </div>
        <div style="width:100%; display:flex;justify-content:flex-end;align-items:center">
            <button class="btn_normal" @click="sureDraw" style="margin: 15px 0px 5px;color: #fff!important;">{{
                t('确定') }}</button>
        </div>
    </div>
</template>

<script setup lang="ts">
import { reactive } from 'vue';
import { t } from '../../i18n'
import { alginType } from "../../SymbolAnnotation"
import { indexStyle, drawIndexSymbol } from './hooks'
import { isOpen } from "../../index";
const dataForm: any = reactive({
    textUp: '上',
    textDown: '下',
    alginType: alginType.start,
    indexDrawNumber: "-",
    number: "01",
    styleType: indexStyle.Directed,
    isCheckAA: false,
    alignAngle: 30,
    addIndexArea:false
});
const alignAngleArr = reactive([
    { id: 30, name: '30°' },
    { id: 45, name: '45°' },
    { id: 60, name: '60°' },
    { id: 90, name: '90°' }])
const { store } = MxPluginContext;
const { setCommandFocus } = store.useFocus();
const typeArr = reactive([
    { name: t('始端'), id: alginType.start },
    { name: t('居中'), id: alginType.middle },
    { name: t('末端'), id: alginType.end },
]);
const styleType = reactive([
    { name: t('指向索引'), id: indexStyle.Directed },
    { name: t('剖切索引'), id: indexStyle.Cutting },
]);

// 确定绘制箭头引注
const sureDraw = async () => {
    isOpen.value = false;
    let angle = 0;
    if(dataForm.isCheckAA){
       angle = dataForm.alignAngle;
    }
    drawIndexSymbol(dataForm.styleType, [dataForm.textUp, dataForm.textDown], dataForm.alginType, dataForm.indexDrawNumber, dataForm.number, angle, dataForm.addIndexArea)
}
</script>

<style lang="scss" scoped>
@import '../../assets/dialogContens.scss';
</style>